import React, { useState, useEffect } from 'react';
import WorkLog from './WorkLog';
import WorkLogHistory from './WorkLogHistory';
import ProfileCard from './ProfileCard';
import ProfileForm from './ProfileForm';
import FaceCapture from './FaceCapture';
import EmployeeSettings from './EmployeeSettings';
import '../styles/PunchInOut.css';
import '../styles/TablePagination.css';
import { formatDate, exportToCSV, printTable, showLoadingCursor, hideLoadingCursor, forceResetCursor } from '../utils/helpers';
import { formatTimeOnly } from '../utils/istTimeUtils';
import useSortableData, { SortableHeader } from '../hooks/useSortableData';
import { validateNetwork, getApiBaseUrl, getApiHeaders } from '../config/networkConfig';

function PunchInOut({ user }) {
  const [records, setRecords] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showWorkLog, setShowWorkLog] = useState(false);
  const [profile, setProfile] = useState(null);
  const [showProfileForm, setShowProfileForm] = useState(false);
  const [showFaceVerification, setShowFaceVerification] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [pendingPunchType, setPendingPunchType] = useState(null); // 'In' or 'Out'
  
  // Tab navigation state - default to 'attendance'
  const [activeTab, setActiveTab] = useState('attendance'); // attendance, workLog, workLogHistory
  
  // Pagination & Filter
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [searchTerm, setSearchTerm] = useState('');
  const [dateFilter, setDateFilter] = useState('SHOW'); // SHOW, TODAY, YESTERDAY, LATEST

  const employeeId = user?.email || 'default';
  const backendUrl = `${getApiBaseUrl()}/api/Attendance`;

  // Function to fetch profile data
  const fetchProfileData = async () => {
    try {
      const res = await fetch(`${getApiBaseUrl()}/api/Profile/${employeeId}`, {
        headers: getApiHeaders()
      });
      if (res.ok) {
        const data = await res.json();
        setProfile(data);
      } else if (res.status === 404) {
        setProfile(null);
      }
    } catch (err) {
      console.error('Profile fetch error:', err);
      setProfile(null);
    }
  };

  useEffect(() => {
    // Fetch attendance records
    console.log('Fetching attendance for employeeId:', employeeId);
    console.log('User object:', user);
    setLoading(true);
    fetch(`${backendUrl}/${employeeId}`, {
      headers: getApiHeaders()
    })
      .then(async res => {
        console.log('Attendance response status:', res.status);
        if (!res.ok) throw new Error(await res.text() || `HTTP ${res.status}`);
        const text = await res.text();
        console.log('Attendance response text:', text);
        return text ? JSON.parse(text) : [];
      })
      .then(data => {
        console.log('Attendance data received:', data);
        // Sort by timestamp, latest first
        const sorted = data.sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
        const formatted = sorted.map(rec => ({
          timestamp: rec.timestamp,
          type: rec.isClockIn ? 'In' : 'Out',
          wifiSsid: rec.wifiSsid || 'N/A',
          id: rec.id
        }));
        console.log('Formatted attendance records:', formatted);
        setRecords(formatted);
      })
      .catch(err => {
        console.error('Attendance fetch error:', err);
        alert(`Failed to load attendance: ${err.message}`);
      })
      .finally(() => setLoading(false));
    
    // Fetch user profile using the new function
    fetchProfileData();
  }, [employeeId]);

  const handlePunch = async (type) => {
    // Check if profile has face registered
    if (!profile?.faceRegistered) {
      alert(' Face not registered! Please complete your profile with face registration before punching in/out.');
      return;
    }

    // Network validation (Router MAC + Gateway IP)
    const networkCheck = await validateNetwork();
    if (!networkCheck.valid) {
      alert(` ${networkCheck.reason}`);
      return;
    }

    // Show face verification modal
    setPendingPunchType(type);
    setShowFaceVerification(true);
  };

  // Calculate euclidean distance between two face descriptors
  const calculateFaceDistance = (desc1, desc2) => {
    if (!desc1 || !desc2 || desc1.length !== desc2.length) return 1.0;
    return Math.sqrt(
      desc1.reduce((sum, val, i) => sum + Math.pow(val - desc2[i], 2), 0)
    );
  };

  const handleFaceVerification = async (capturedFace) => {
    try {
      // Get stored face descriptor from profile
      const storedFaceStr = profile?.faceDescriptor;
      if (!storedFaceStr) {
        alert(' No face registered in your profile. Please update your profile.');
        setShowFaceVerification(false);
        return;
      }

      const storedFace = JSON.parse(storedFaceStr);
      const capturedDescriptor = capturedFace.faceDescriptor;
      
      // VALIDATION 1: Check facial state (eyes, mouth, expression)
      console.log('Captured face state - EAR:', capturedFace.eyeAspectRatio, 'MAR:', capturedFace.mouthAspectRatio);
      
      // These checks are already done in FaceCapture, but double-check here
      if (capturedFace.eyeAspectRatio < 0.15) {
        alert(' Verification failed: Eyes not properly visible!\n\nEnsure:\n Eyes are fully open\n Looking at camera\n Good lighting on face');
        setShowFaceVerification(false);
        return;
      }
      
      if (capturedFace.mouthAspectRatio > 0.5) {
        alert(' Verification failed: Unusual facial expression!\n\nEnsure:\n Neutral expression (no smiling/talking)\n Mouth closed\n Face relaxed');
        setShowFaceVerification(false);
        return;
      }

      // VALIDATION 2: Calculate face descriptor similarity (lower distance = more similar)
      const distance = calculateFaceDistance(capturedDescriptor, storedFace);
      
      // ULTRA-STRICT threshold for security - only accepts the exact same person
      // 0.3 = Ultra-strict (may reject same person in different lighting)
      // 0.35 = Very very strict (recommended for high security)
      // 0.4 = Very strict
      const threshold = 0.35;

      console.log('Face verification distance:', distance, 'Threshold:', threshold);

      if (distance < threshold) {
        // Face verified! Proceed with punch
        console.log(' Face match verified! Distance:', distance, 'EAR:', capturedFace.eyeAspectRatio, 'MAR:', capturedFace.mouthAspectRatio);
        setShowFaceVerification(false);
        await performPunch(pendingPunchType);
      } else {
        console.log(' Face verification FAILED! Distance:', distance, 'Required: <', threshold);
        alert(` Face verification failed!\n\n Match Score: ${distance.toFixed(3)} (Required: < ${threshold})\n\n This appears to be a DIFFERENT person.\n\nIf you are the registered employee:\n Ensure identical lighting conditions\n Face camera at same angle as registration\n Match glasses/accessories from registration\n Try re-registering your face in better lighting\n\nContact admin if this persists.`);
        setShowFaceVerification(false);
      }
    } catch (err) {
      console.error('Face verification error:', err);
      alert(' Face verification error: ' + err.message);
      setShowFaceVerification(false);
    }
  };

  const handleCancelFaceVerification = () => {
    setShowFaceVerification(false);
    setPendingPunchType(null);
  };

  const performPunch = async (type) => {
    // Get network information for recording
    let networkInfo = null;
    if (window.electronAPI && window.electronAPI.getNetworkInfo) {
      networkInfo = await window.electronAPI.getNetworkInfo();
    }

    const isClockIn = type === 'In';
    setLoading(true);
    showLoadingCursor();

    try {
      const response = await fetch(backendUrl, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({
          employeeId,
          isClockIn,
          gatewayIp: networkInfo?.gatewayIp,
          routerMac: networkInfo?.routerMac,
          deviceIp: networkInfo?.localIp,
          deviceMac: networkInfo?.deviceMac
        }),
      });

      if (!response.ok) {
        const text = await response.text();
        let msg;
        try { msg = JSON.parse(text).message; } catch { msg = `HTTP ${response.status}`; }
        throw new Error(msg);
      }

      const text = await response.text();
      const savedRecord = text ? JSON.parse(text) : null;
      setRecords(prevRecords => [
        { timestamp: savedRecord.timestamp, type: isClockIn ? 'In' : 'Out', wifiSsid: savedRecord.wifiSsid || 'N/A', id: savedRecord.id },
        ...prevRecords
      ]);
      alert(` Successfully punch ${type}! Face verified.`);
    } catch (err) {
      alert(`Error: ${err.message}`);
      forceResetCursor();
    } finally {
      setLoading(false);
      hideLoadingCursor();
    }
  };

  // Filter records
  const filteredRecords = records.filter(rec => {
    // Date filtering
    if (dateFilter !== 'SHOW') {
      const now = new Date();
      const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
      const yesterday = new Date(today);
      yesterday.setDate(yesterday.getDate() - 1);
      const dayBeforeYesterday = new Date(today);
      dayBeforeYesterday.setDate(dayBeforeYesterday.getDate() - 2);
      const recordDate = new Date(rec.timestamp);
      
      if (dateFilter === 'TODAY' && recordDate < today) return false;
      if (dateFilter === 'YESTERDAY' && (recordDate < yesterday || recordDate >= today)) return false;
      if (dateFilter === 'LATEST' && recordDate < dayBeforeYesterday) return false;
    }
    
    // Search filtering
    const searchLower = searchTerm.toLowerCase();
    return (
      formatDate(rec.timestamp).toLowerCase().includes(searchLower) ||
      formatTimeOnly(rec.timestamp).toLowerCase().includes(searchLower) ||
      rec.type.toLowerCase().includes(searchLower)
    );
  });

  // Sorting
  const { items: sortedRecords, requestSort, sortConfig } = useSortableData(filteredRecords);

  // Paginate filtered records
  const paginatedRecords = sortedRecords.slice(
    (currentPage - 1) * rowsPerPage,
    currentPage * rowsPerPage
  );

  const totalPages = Math.ceil(sortedRecords.length / rowsPerPage);

  // Check latest record type to disable duplicate punches
  const latestRecord = records.length > 0 ? records[0] : null;
  const isPunchInDisabled = latestRecord && latestRecord.type === 'In';
  const isPunchOutDisabled = latestRecord && latestRecord.type === 'Out';

  const handleExport = () => {
    const exportData = filteredRecords.map(rec => ({
      Date: formatDate(rec.timestamp),
      Time: formatTimeOnly(rec.timestamp),
      Status: rec.type,
      'WiFi SSID': rec.wifiSsid || 'N/A'
    }));
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      searchText: searchTerm.trim() || null,
      totalRecords: records.length,
      filteredRecords: filteredRecords.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    exportToCSV(exportData, 'attendance', fullName, employeeId, filterInfo);
  };

  const handlePrint = () => {
    const columns = [
      { key: 'date', label: '<th>Date</th>', getValue: (rec) => formatDate(rec.timestamp) },
      { key: 'time', label: '<th>Time</th>', getValue: (rec) => formatTimeOnly(rec.timestamp) },
      { key: 'status', label: '<th>Status</th>', getValue: (rec) => rec.type },
      { key: 'wifiSsid', label: '<th>WiFi SSID</th>', getValue: (rec) => rec.wifiSsid || 'N/A' }
    ];
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      searchText: searchTerm.trim() || null,
      totalRecords: records.length,
      filteredRecords: filteredRecords.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    printTable(filteredRecords, columns, 'Attendance History', fullName, employeeId, filterInfo);
  };

  // Show face verification modal
  if (showFaceVerification) {
    return (
      <FaceCapture
        onCapture={handleFaceVerification}
        onCancel={handleCancelFaceVerification}
        mode="verify"
        userName={profile ? `${profile.firstName} ${profile.lastName}` : user.email}
      />
    );
  }

  return (
    <>
      {/* Title outside container, centered at top */}
      <h2 className="employee-dashboard-title">Employee Dashboard</h2>
    
    <div className="employee-layout-container">
      {/* Left Sidebar - Profile */}
      <div className="employee-sidebar">
    
        {/* Profile Card - Always show in sidebar */}
        {profile && !showProfileForm && (
          <>
            <ProfileCard 
              profile={profile} 
              onEdit={() => setShowProfileForm(true)} 
            />
            {/* Settings Button */}
            <button 
              className="employee-settings-button"
              onClick={() => setShowSettings(true)}
              style={{
                width: '100%',
                marginTop: '1rem',
                padding: '0.75rem',
                backgroundColor: '#667eea',
                color: 'white',
                border: 'none',
                borderRadius: '8px',
                fontSize: '1rem',
                fontWeight: '500',
                cursor: 'pointer',
                transition: 'all 0.2s'
              }}
              onMouseEnter={(e) => e.target.style.transform = 'translateY(-2px)'}
              onMouseLeave={(e) => e.target.style.transform = 'translateY(0)'}
            >
              Settings
            </button>
          </>
        )}

        {/* Profile Form in sidebar */}
        {showProfileForm && (
          <div className="employee-sidebar-form">
            <ProfileForm 
              userId={employeeId}
              initialProfile={profile}
              onSave={async (savedProfile) => {
                setProfile(savedProfile);
                setShowProfileForm(false);
                // Immediately refetch profile to ensure latest data
                await fetchProfileData();
                alert(' Profile saved successfully!');
              }}
              onCancel={() => setShowProfileForm(false)}
            />
          </div>
        )}
      </div>

      {/* Main Content Area */}
      <div className="punch-container punch-container-flex">
        {/* Tab Navigation */}
        <div className="employee-tabs">
          <button
            className={`employee-tab ${activeTab === 'attendance' ? 'active' : ''}`}
            onClick={() => setActiveTab('attendance')}
          >
            Attendance
          </button>
          <button
            className={`employee-tab ${activeTab === 'workLog' ? 'active' : ''}`}
            onClick={() => setActiveTab('workLog')}
          >
            Work Log
          </button>
          <button
            className={`employee-tab ${activeTab === 'workLogHistory' ? 'active' : ''}`}
            onClick={() => setActiveTab('workLogHistory')}
          >
            Work Log History
          </button>
        </div>

        {/* Tab Content */}
        <div className="employee-tab-content">
          
          {/* ATTENDANCE TAB */}
          {activeTab === 'attendance' && (
            <div>
              {loading && <div className="loading-spinner">Loading...</div>}

              <div className="punch-buttons">
                <button 
                  onClick={() => handlePunch('In')} 
                  className={`punch-in-button ${isPunchInDisabled ? 'punch-button-disabled' : ''}`}
                  disabled={loading || isPunchInDisabled}
                  title={isPunchInDisabled ? "You have already punched in. Please punch out first." : "Punch In"}
                >
                  Punch In
                </button>
        <button 
          onClick={() => handlePunch('Out')} 
          className={`punch-out-button ${isPunchOutDisabled ? 'punch-button-disabled' : ''}`}
          disabled={loading || isPunchOutDisabled}
          title={isPunchOutDisabled ? "You have already punched out. Please punch in first." : "Punch Out"}
        >
          Punch Out
        </button>
      </div>

      <div className="attendance-history">
        {/* TOP: Title + Search only */}
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '1rem', flexWrap: 'wrap', gap: '12px' }}>
          <h3 className="history-title">Attendance History ({filteredRecords.length} records)</h3>
          <input
            type="text"
            placeholder="Search attendance..."
            value={searchTerm}
            onChange={(e) => {
              setSearchTerm(e.target.value);
              setCurrentPage(1);
            }}
            style={{
              padding: '8px 12px',
              border: '1px solid #ddd',
              borderRadius: '6px',
              fontSize: '14px',
              minWidth: '250px'
            }}
          />
        </div>
        
        {/* TABLE - Always show structure */}
        <>
          <table id="attendanceHistoryTable" className="history-table">
            <thead>
              <tr>
                <SortableHeader columnKey="timestamp" label="Date" requestSort={requestSort} sortConfig={sortConfig} />
                <th className="time-column">Time</th>
                <SortableHeader columnKey="type" label="Status" requestSort={requestSort} sortConfig={sortConfig} />
                <SortableHeader columnKey="wifiSsid" label="WiFi SSID" requestSort={requestSort} sortConfig={sortConfig} />
              </tr>
            </thead>
            <tbody>
              {paginatedRecords.length > 0 ? (
                paginatedRecords.map((rec) => (
                  <tr key={rec.id}>
                    <td>{formatDate(rec.timestamp)}</td>
                    <td>{formatTimeOnly(rec.timestamp)}</td>
                    <td className={`status-${rec.type.toLowerCase()}`}>{rec.type}</td>
                    <td>{rec.wifiSsid || 'N/A'}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4" className="table-empty-message">
                    No attendance records found
                  </td>
                </tr>
              )}
            </tbody>
          </table>
          
          {/* BOTTOM: Pagination + Date Filter + Print + Export - Always show */}
            <div className="table-pagination-container">
              {/* Left: Pagination */}
              <div className="table-pagination-controls">
                <button
                  onClick={() => setCurrentPage(1)}
                  disabled={currentPage === 1}
                  className="table-pagination-button"
                >
                  First
                </button>
                <button
                  onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                  disabled={currentPage === 1}
                  className="table-pagination-button"
                >
                  Previous
                </button>
                <span className="table-pagination-info">
                  Page {currentPage} of {totalPages} ({filteredRecords.length} total records)
                </span>
                <button
                  onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                  disabled={currentPage === totalPages}
                  className="table-pagination-button"
                >
                  Next
                </button>
                <button
                  onClick={() => setCurrentPage(totalPages)}
                  disabled={currentPage === totalPages}
                  className="table-pagination-button"
                >
                  Last
                </button>
                <select
                  value={rowsPerPage}
                  onChange={(e) => {
                    setRowsPerPage(Number(e.target.value));
                    setCurrentPage(1);
                  }}
                  className="table-pagination-select"
                >
                  <option value={10}>10 rows</option>
                  <option value={50}>50 rows</option>
                  <option value={100}>100 rows</option>
                </select>
                <select
                  value={dateFilter}
                  onChange={(e) => {
                    setDateFilter(e.target.value);
                    setCurrentPage(1);
                  }}
                  className="table-pagination-select"
                >
                  <option value="SHOW">SHOW ALL</option>
                  <option value="TODAY">TODAY</option>
                  <option value="YESTERDAY">YESTERDAY</option>
                  <option value="LATEST">LATEST (3 days)</option>
                </select>
              </div>
              
              {/* Right: Export + Print */}
              <div className="table-pagination-actions">
                <button onClick={handleExport} className="table-export-button">
                   Export CSV
                </button>
                <button onClick={handlePrint} className="table-print-button">
                   Print
                </button>
              </div>
            </div>
        </>
              </div>
            </div>
          )}

          {/* WORK LOG TAB */}
          {activeTab === 'workLog' && (
            <WorkLog user={user} showHistory={false} />
          )}

          {/* WORK LOG HISTORY TAB */}
          {activeTab === 'workLogHistory' && (
            <WorkLogHistory user={user} />
          )}

        </div>
      </div>
    </div>

    {/* Employee Settings Modal */}
    {showSettings && (
      <EmployeeSettings
        employeeId={profile?.employeeId}
        currentEmail={employeeId}
        onClose={() => {
          setShowSettings(false);
          // Refetch profile to show updated data
          fetchProfileData();
        }}
      />
    )}
    </>
  );
}

export default PunchInOut;
