export const formatDate = (dateString) => {
  if (!dateString) return 'N/A';
  try {
    // Ensure date is treated as UTC if no timezone info provided
    const dateStr = dateString.endsWith('Z') || dateString.includes('+') ? dateString : `${dateString}Z`;
    const date = new Date(dateStr);
    
    if (isNaN(date.getTime())) return 'N/A';
    
    // Use Intl.DateTimeFormat for reliable IST conversion
    const options = {
      timeZone: 'Asia/Kolkata',
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    };
    
    const formatter = new Intl.DateTimeFormat('en-IN', options);
    const parts = formatter.formatToParts(date);
    
    const day = parts.find(p => p.type === 'day').value;
    const month = parts.find(p => p.type === 'month').value;
    const year = parts.find(p => p.type === 'year').value;
    
    return `${day}-${month}-${year}`;
  } catch (e) {
    console.error('Error formatting date:', e);
    return 'N/A';
  }
};

// Loading cursor utilities
let loadingCursorCount = 0;
let cursorResetTimeout = null;

export const showLoadingCursor = () => {
  loadingCursorCount++;
  document.body.style.cursor = 'wait';
  // Never block input fields
  document.body.style.pointerEvents = 'auto';
  
  // Clear any pending auto-reset
  if (cursorResetTimeout) {
    clearTimeout(cursorResetTimeout);
    cursorResetTimeout = null;
  }
};

export const hideLoadingCursor = () => {
  loadingCursorCount--;
  // Only reset cursor if no more loading operations
  if (loadingCursorCount <= 0) {
    loadingCursorCount = 0;
    document.body.style.cursor = 'default';
    document.body.style.pointerEvents = 'auto';
  }
  
  // Safety mechanism: Auto-reset after 5 seconds if still stuck
  if (cursorResetTimeout) {
    clearTimeout(cursorResetTimeout);
  }
  cursorResetTimeout = setTimeout(() => {
    if (loadingCursorCount > 0) {
      console.warn('Loading cursor stuck detected! Force resetting...');
      forceResetCursor();
    }
  }, 5000);
};

// Force reset cursor - use in error handlers
export const forceResetCursor = () => {
  loadingCursorCount = 0;
  document.body.style.cursor = 'default';
  document.body.style.pointerEvents = 'auto';
  if (cursorResetTimeout) {
    clearTimeout(cursorResetTimeout);
    cursorResetTimeout = null;
  }
};

export const withLoadingCursor = async (asyncFunction) => {
  showLoadingCursor();
  try {
    return await asyncFunction();
  } finally {
    hideLoadingCursor();
  }
};

// Export table data to CSV with timestamp and S.No
export const exportToCSV = (data, filename = 'export', employeeName = null, employeeEmail = null, filterInfo = null) => {
  if (!data || data.length === 0) {
    alert('No data to export');
    return;
  }

  // Confirmation dialog
  const confirmExport = window.confirm(`Export all ${data.length} records to CSV?`);
  if (!confirmExport) return;

  showLoadingCursor();

  try {
    // Add timestamp to filename
    const timestamp = new Date().toISOString().replace(/:/g, '-').split('.')[0].replace('T', '_');
    const fullFilename = `${employeeEmail ? `${employeeEmail}_` : ''}${filename}_${timestamp}.csv`;

    // Add header rows if employee info provided
    let csv = '';
    if (employeeName || employeeEmail) {
      csv += 'EMPLOYEES TIMEPULSE\n';
      if (employeeName) csv += `Employee Name:,${employeeName}\n`;
      if (employeeEmail) csv += `Employee Email:,${employeeEmail}\n`;
      csv += '\n';
    }

    // Add filter information if provided
    if (filterInfo && Object.keys(filterInfo).some(key => filterInfo[key] !== null && filterInfo[key] !== undefined && key !== 'totalRecords' && key !== 'filteredRecords')) {
      csv += 'APPLIED FILTERS:\n';
      if (filterInfo.dateFilter) csv += `Date Filter:,${filterInfo.dateFilter}\n`;
      if (filterInfo.statusFilter) csv += `Status:,${filterInfo.statusFilter}\n`;
      if (filterInfo.priorityFilter) csv += `Priority:,${filterInfo.priorityFilter}\n`;
      if (filterInfo.approvedByFilter) csv += `Approved/Rejected By:,${filterInfo.approvedByFilter}\n`;
      if (filterInfo.overdueStatusFilter) csv += `Overdue Status:,${filterInfo.overdueStatusFilter}\n`;
      if (filterInfo.rejectionCountFilter) csv += `Rejection Count:,${filterInfo.rejectionCountFilter}\n`;
      if (filterInfo.employeeFilter) csv += `Employee:,${filterInfo.employeeFilter}\n`;
      if (filterInfo.roleFilter) csv += `Role:,${filterInfo.roleFilter}\n`;
      if (filterInfo.approvalFilter) csv += `Approval Status:,${filterInfo.approvalFilter}\n`;
      if (filterInfo.searchText) csv += `Search Text:,"${filterInfo.searchText}"\n`;
      if (filterInfo.filteredRecords !== undefined && filterInfo.totalRecords !== undefined) {
        csv += `Records Shown:,${filterInfo.filteredRecords} of ${filterInfo.totalRecords}\n`;
      }
      csv += '\n';
    }

    // Add S.No to data
    const dataWithSNo = data.map((row, index) => ({
      'S.No': index + 1,
      ...row
    }));

    // Get headers from first object
    const headers = Object.keys(dataWithSNo[0]);
    
    // Create CSV content
    csv += headers.join(',') + '\n';
    
    dataWithSNo.forEach(row => {
      const values = headers.map(header => {
        const value = row[header];
        // Handle values with commas or quotes
        if (value && (value.toString().includes(',') || value.toString().includes('"'))) {
          return `"${value.toString().replace(/"/g, '""')}"`;
        }
        return value !== null && value !== undefined ? value : '';
      });
      csv += values.join(',') + '\n';
    });

    // Create download link
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', fullFilename);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    alert(`Successfully exported ${data.length} records to ${fullFilename}`);
  } catch (error) {
    alert('Export failed: ' + error.message);
  } finally {
    hideLoadingCursor();
  }
};

// Print table - accepts data array and columns to print ALL filtered records with S.No
export const printTable = (data, columns, title = 'Print Table', employeeName = null, employeeEmail = null, filterInfo = null) => {
  if (!data || data.length === 0) {
    alert('No data to print');
    return;
  }

  // Confirmation dialog
  const confirmPrint = window.confirm(`Print all ${data.length} records?`);
  if (!confirmPrint) return;

  showLoadingCursor();
  
  try {
    // Add S.No column at the beginning
    const allColumns = [{ label: '<th>S.No</th>', key: 'sno', getValue: (row, index) => index + 1 }, ...columns];
    
    // Generate table HTML from data with S.No
    const headers = allColumns.map(col => col.label).join('');
    const rows = data.map((row, index) => {
      const cells = allColumns.map(col => {
        if (col.key === 'sno') {
          return `<td>${index + 1}</td>`;
        }
        return `<td>${col.getValue ? col.getValue(row) : row[col.key] || 'N/A'}</td>`;
      }).join('');
      return `<tr>${cells}</tr>`;
    }).join('');

    // Add timestamp
    const timestamp = new Date().toLocaleString();

    // Employee info section
    const employeeInfo = (employeeName || employeeEmail) ? `
      <div style="text-align: left; margin-bottom: 15px; padding: 10px; background-color: #f5f5f5; border-radius: 5px;">
        ${employeeName ? `<p style="margin: 5px 0;"><strong>Employee Name:</strong> ${employeeName}</p>` : ''}
        ${employeeEmail ? `<p style="margin: 5px 0;"><strong>Employee Email:</strong> ${employeeEmail}</p>` : ''}
      </div>
    ` : '';

    // Filter info section
    const filterSection = filterInfo && Object.keys(filterInfo).some(key => filterInfo[key] !== null && filterInfo[key] !== undefined && key !== 'totalRecords' && key !== 'filteredRecords') ? `
      <div style="text-align: left; margin-bottom: 15px; padding: 10px; background-color: #e3f2fd; border-radius: 5px; border-left: 4px solid #2196F3;">
        <h3 style="margin: 5px 0 10px 0; color: #1976D2; font-size: 14px;">Applied Filters:</h3>
        ${filterInfo.dateFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Date Filter:</strong> ${filterInfo.dateFilter}</p>` : ''}
        ${filterInfo.statusFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Status:</strong> ${filterInfo.statusFilter}</p>` : ''}
        ${filterInfo.priorityFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Priority:</strong> ${filterInfo.priorityFilter}</p>` : ''}
        ${filterInfo.approvedByFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Approved/Rejected By:</strong> ${filterInfo.approvedByFilter}</p>` : ''}
        ${filterInfo.overdueStatusFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Overdue Status:</strong> ${filterInfo.overdueStatusFilter}</p>` : ''}
        ${filterInfo.rejectionCountFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Rejection Count:</strong> ${filterInfo.rejectionCountFilter}</p>` : ''}
        ${filterInfo.employeeFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Employee:</strong> ${filterInfo.employeeFilter}</p>` : ''}
        ${filterInfo.roleFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Role:</strong> ${filterInfo.roleFilter}</p>` : ''}
        ${filterInfo.approvalFilter ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Approval Status:</strong> ${filterInfo.approvalFilter}</p>` : ''}
        ${filterInfo.searchText ? `<p style="margin: 3px 0; font-size: 12px;"><strong>Search:</strong> "${filterInfo.searchText}"</p>` : ''}
        ${filterInfo.filteredRecords !== undefined && filterInfo.totalRecords !== undefined ? 
          `<p style="margin: 8px 0 0 0; color: #666; font-size: 11px; font-style: italic;">Showing ${filterInfo.filteredRecords} of ${filterInfo.totalRecords} total records</p>` : ''}
      </div>
    ` : '';

    const htmlContent = `
      <!DOCTYPE html>
      <html>
        <head>
          <title>${title}</title>
          <style>
            body { 
              font-family: Arial, sans-serif; 
              margin: 20px; 
            }
            h1 {
              text-align: center;
              color: #2c3e50;
              font-size: 24px;
              margin-bottom: 5px;
              text-transform: uppercase;
              letter-spacing: 1px;
            }
            h2 { 
              text-align: center; 
              color: #333;
              margin-bottom: 10px;
            }
            p {
              text-align: center;
              color: #666;
              font-size: 14px;
              margin: 5px 0;
            }
            table { 
              width: 100%; 
              border-collapse: collapse; 
              margin-top: 15px;
            }
            th, td { 
              border: 1px solid #333; 
              padding: 8px; 
              text-align: left; 
              font-size: 12px;
            }
            th { 
              background-color: #4CAF50; 
              color: white; 
              font-weight: bold;
            }
            tbody tr:nth-child(even) { 
              background-color: #f9f9f9; 
            }
            .footer {
              margin-top: 30px;
              text-align: center;
              font-size: 12px;
              color: #666;
            }
            @media print {
              body { margin: 10px; }
              thead { display: table-header-group; }
              tr { page-break-inside: avoid; }
            }
          </style>
        </head>
        <body>
          <h1>EMPLOYEES TIMEPULSE</h1>
          <h2>${title}</h2>
          <p>Generated on: ${timestamp}</p>
          ${employeeInfo}
          ${filterSection}
          <table>
            <thead>
              <tr>${headers}</tr>
            </thead>
            <tbody>
              ${rows}
            </tbody>
          </table>
          <div class="footer">
            <p>Printed on: ${timestamp}</p>
          </div>
        </body>
      </html>
    `;
    
    // Use hidden iframe method for better print preview support
    const iframe = document.createElement('iframe');
    iframe.style.position = 'fixed';
    iframe.style.right = '0';
    iframe.style.bottom = '0';
    iframe.style.width = '0';
    iframe.style.height = '0';
    iframe.style.border = 'none';
    document.body.appendChild(iframe);
    
    const iframeWindow = iframe.contentWindow;
    const iframeDoc = iframeWindow.document;
    
    iframeDoc.open();
    iframeDoc.write(htmlContent);
    iframeDoc.close();
    
    // Wait for content to load then trigger print
    iframe.onload = function() {
      setTimeout(() => {
        iframeWindow.focus();
        iframeWindow.print();
        
        // Clean up after printing
        setTimeout(() => {
          document.body.removeChild(iframe);
          hideLoadingCursor();
        }, 1000);
      }, 250);
    };
  } catch (error) {
    console.error('Print error:', error);
    alert('Failed to print: ' + error.message);
    hideLoadingCursor();
  }
};

// Pagination helper
export const paginateData = (data, currentPage, itemsPerPage = 10) => {
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  return data.slice(startIndex, endIndex);
};

export const getTotalPages = (dataLength, itemsPerPage = 10) => {
  return Math.ceil(dataLength / itemsPerPage);
};
