import React, { useState, useEffect } from 'react';
import '../styles/ProfileForm.css';
import { showLoadingCursor, hideLoadingCursor, forceResetCursor } from '../utils/helpers';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import FaceCapture from './FaceCapture';

function ProfileForm({ userId, initialProfile, onSave, onCancel, userEmail, onProfileComplete }) {
  const email = userId || userEmail;
  const isEditMode = !!initialProfile;
  
  // Check if this is face-only mode (profile exists but needs face)
  const isFaceOnlyMode = initialProfile && !initialProfile.faceRegistered;

  const [formData, setFormData] = useState({
    email: email,
    firstName: '',
    middleName: '',
    lastName: '',
    dateOfBirth: '',
    phoneNumber: '',
    address: '',
    profilePicture: ''
  });
  const [imagePreview, setImagePreview] = useState(null);
  const [showFaceCapture, setShowFaceCapture] = useState(false);
  const [faceData, setFaceData] = useState(null);

  // Pre-populate form if editing
  useEffect(() => {
    if (initialProfile) {
      setFormData({
        email: initialProfile.email,
        firstName: initialProfile.firstName || '',
        middleName: initialProfile.middleName || '',
        lastName: initialProfile.lastName || '',
        dateOfBirth: initialProfile.dateOfBirth?.split('T')[0] || '',
        phoneNumber: initialProfile.phoneNumber || '',
        address: initialProfile.address || '',
        profilePicture: initialProfile.profilePicture || ''
      });
      if (initialProfile.profilePicture) {
        setImagePreview(initialProfile.profilePicture);
      }
      // Check if face is already registered
      if (initialProfile.faceRegistered) {
        setFaceData({ registered: true });
      }
    }
  }, [initialProfile]);

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (file.size > 2 * 1024 * 1024) { // 2MB limit
        alert('Image size should be less than 2MB');
        return;
      }
      
      const reader = new FileReader();
      reader.onloadend = () => {
        const imageData = reader.result;
        setImagePreview(imageData);
        setFormData(prevData => ({ ...prevData, profilePicture: imageData }));
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // In face-only mode, all fields are already filled, just need face
    if (!isFaceOnlyMode) {
      if (!formData.firstName || !formData.lastName || !formData.dateOfBirth || 
          !formData.phoneNumber || !formData.address) {
        alert('Please fill all required fields');
        return;
      }
      
      // Validate mobile number: exactly 10 digits
      if (formData.phoneNumber.length !== 10) {
        alert('Mobile number must be exactly 10 digits!');
        return;
      }
    }

    // Face registration is MANDATORY for new profiles AND face-only mode
    if (!faceData?.faceDescriptor) {
      alert(' Face registration is mandatory! Please register your face before completing your profile.');
      return;
    }

    showLoadingCursor();
    try {
      const method = (isEditMode || isFaceOnlyMode) ? 'PUT' : 'POST';
      const url = (isEditMode || isFaceOnlyMode)
        ? `${getApiBaseUrl()}/api/Profile/${encodeURIComponent(formData.email)}`
        : `${getApiBaseUrl()}/api/Profile`;
      
      // Include face data in the profile
      const profileData = {
        ...formData,
        faceDescriptor: faceData?.faceDescriptor ? JSON.stringify(faceData.faceDescriptor) : initialProfile?.faceDescriptor,
        faceRegistered: !!faceData?.faceDescriptor || initialProfile?.faceRegistered || false,
      };
      
      const response = await fetch(url, {
        method,
        headers: getApiHeaders(),
        body: JSON.stringify(profileData),
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText || `Failed to ${isEditMode ? 'update' : 'create'} profile`);
      }

      const profile = await response.json();
      
      if (isFaceOnlyMode) {
        alert(' Face registered successfully! You can now use the attendance system.');
        if (onProfileComplete) {
          onProfileComplete(profile);
        }
      } else if (isEditMode && onSave) {
        onSave(profile);
      } else if (onProfileComplete) {
        alert('Profile created successfully!');
        onProfileComplete(profile);
      }
    } catch (err) {
      alert(`Error: ${err.message}`);
      forceResetCursor(); // Force reset on error
    } finally {
      hideLoadingCursor();
      // Double-check cursor is reset
      setTimeout(() => forceResetCursor(), 100);
    }
  };

  const handleFaceCapture = (captureData) => {
    setFaceData(captureData);
    setShowFaceCapture(false);
    alert(' Face registered successfully! You can now complete your profile.');
  };

  const handleCancelFaceCapture = () => {
    setShowFaceCapture(false);
  };

  // Show face capture modal if active
  if (showFaceCapture) {
    return (
      <FaceCapture
        onCapture={handleFaceCapture}
        onCancel={handleCancelFaceCapture}
        mode="register"
        userName={formData.firstName || email}
      />
    );
  }

  return (
    <div className="profile-form-overlay">
      <div className="profile-form-modal">
        {/* Cancel Button */}
        {onCancel && (
          <button 
            type="button"
            className="profile-form-cancel"
            onClick={onCancel}
            title={isEditMode ? "Cancel editing" : "Cancel and return to login"}
          >
            
          </button>
        )}
        
        <h2>
          {isFaceOnlyMode ? ' Face Registration Required' : 
           isEditMode ? 'Edit Your Profile' : 
           'Complete Your Profile'}
        </h2>
        <p className="profile-form-subtitle">
          {isFaceOnlyMode ? 'Your profile is complete, but face registration is mandatory for attendance' :
           isEditMode ? 'You can only edit phone number and address' : 
           'Please provide your details to continue'}
        </p>

        <form onSubmit={handleSubmit} className="profile-form">
          {/* Show profile fields only if NOT face-only mode */}
          {!isFaceOnlyMode && (
            <>
              {/* Profile Picture */}
              <div className="profile-form-section">
                <label className="profile-pic-label">
                  Profile Picture *
                  <div className="profile-pic-upload">
                    {imagePreview ? (
                      <img src={imagePreview} alt="Preview" className="profile-pic-preview" />
                    ) : (
                      <div className="profile-pic-placeholder">
                        <span></span>
                        <span>Click to upload</span>
                      </div>
                    )}
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleImageUpload}
                      className="profile-pic-input"
                      required={!formData.profilePicture && !imagePreview}
                    />
                  </div>
                </label>
              </div>

              {/* Name Fields */}
              <div className="profile-form-row">
            <div className="profile-form-field">
              <label>First Name *</label>
              <input
                type="text"
                value={formData.firstName}
                onChange={(e) => setFormData({ ...formData, firstName: e.target.value.toUpperCase() })}
                placeholder="First Name"
                required
                readOnly={isEditMode}
                style={isEditMode ? { backgroundColor: '#f3f4f6', cursor: 'not-allowed' } : {}}
                title={isEditMode ? "Name cannot be edited" : ""}
              />
            </div>
            <div className="profile-form-field">
              <label>Middle Name</label>
              <input
                type="text"
                value={formData.middleName}
                onChange={(e) => setFormData({ ...formData, middleName: e.target.value.toUpperCase() })}
                placeholder="Middle Name (Optional)"
                readOnly={isEditMode}
                style={isEditMode ? { backgroundColor: '#f3f4f6', cursor: 'not-allowed' } : {}}
                title={isEditMode ? "Name cannot be edited" : ""}
              />
            </div>
            <div className="profile-form-field">
              <label>Last Name *</label>
              <input
                type="text"
                value={formData.lastName}
                onChange={(e) => setFormData({ ...formData, lastName: e.target.value.toUpperCase() })}
                placeholder="Last Name"
                required
                readOnly={isEditMode}
                style={isEditMode ? { backgroundColor: '#f3f4f6', cursor: 'not-allowed' } : {}}
                title={isEditMode ? "Name cannot be edited" : ""}
              />
            </div>
          </div>

          {/* Date of Birth */}
          <div className="profile-form-field">
            <label>Date of Birth *</label>
            <input
              type="date"
              value={formData.dateOfBirth}
              onChange={(e) => setFormData({ ...formData, dateOfBirth: e.target.value })}
              max={new Date().toISOString().split('T')[0]}
              required
              readOnly={isEditMode}
              style={isEditMode ? { backgroundColor: '#f3f4f6', cursor: 'not-allowed' } : {}}
              title={isEditMode ? "Date of Birth cannot be edited" : ""}
            />
          </div>

          {/* Phone Number */}
          <div className="profile-form-field">
            <label>Phone Number * (Exactly 10 digits)</label>
            <input
              type="tel"
              value={formData.phoneNumber}
              onChange={(e) => {
                // Only allow digits, max 10
                const value = e.target.value.replace(/\D/g, '').slice(0, 10);
                setFormData({ ...formData, phoneNumber: value });
              }}
              placeholder="1234567890"
              maxLength={10}
              pattern="[0-9]{10}"
              required
            />
            {formData.phoneNumber && formData.phoneNumber.length !== 10 && (
              <span style={{ color: 'red', fontSize: '12px' }}>
                Mobile number must be exactly 10 digits (currently {formData.phoneNumber.length})
              </span>
            )}
          </div>

          {/* Address */}
          <div className="profile-form-field">
            <label>Address *</label>
            <textarea
              value={formData.address}
              onChange={(e) => setFormData({ ...formData, address: e.target.value })}
              placeholder="Street Address, City, State, ZIP"
              rows={3}
              required
            />
          </div>
            </>
          )}

          {/* Face Registration - MANDATORY for new profiles AND face-only mode */}
          {(!isEditMode || isFaceOnlyMode) && (
            <div className="profile-form-field" style={{ marginTop: '24px' }}>
              <label style={{ fontSize: '16px', fontWeight: '600', color: '#2c3e50' }}>
                Face Registration * (Mandatory)
              </label>
              <div style={{ 
                padding: '16px', 
                background: faceData?.faceDescriptor ? '#d4edda' : '#fff3cd',
                border: `2px solid ${faceData?.faceDescriptor ? '#28a745' : '#ffc107'}`,
                borderRadius: '8px',
                marginTop: '8px'
              }}>
                {faceData?.faceDescriptor ? (
                  <div>
                    <p style={{ color: '#155724', fontWeight: '600', margin: '0 0 8px 0' }}>
                       Face Registered Successfully!
                    </p>
                    <p style={{ color: '#155724', fontSize: '14px', margin: 0 }}>
                      Your face has been captured and will be used for attendance verification.
                    </p>
                    <button
                      type="button"
                      onClick={() => setShowFaceCapture(true)}
                      style={{
                        marginTop: '12px',
                        padding: '8px 16px',
                        background: '#6c757d',
                        color: '#fff',
                        border: 'none',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontSize: '14px'
                      }}
                    >
                       Re-capture Face
                    </button>
                  </div>
                ) : (
                  <div>
                    <p style={{ color: '#856404', fontWeight: '600', margin: '0 0 8px 0' }}>
                       Face Registration Required
                    </p>
                    <p style={{ color: '#856404', fontSize: '14px', margin: '0 0 12px 0' }}>
                      You must register your face to complete your profile. This will be used for secure attendance verification.
                    </p>
                    <button
                      type="button"
                      onClick={() => setShowFaceCapture(true)}
                      style={{
                        padding: '12px 24px',
                        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                        color: '#fff',
                        border: 'none',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontSize: '16px',
                        fontWeight: '600',
                        boxShadow: '0 4px 12px rgba(102, 126, 234, 0.4)'
                      }}
                    >
                       Register Face Now
                    </button>
                  </div>
                )}
              </div>
            </div>
          )}

          {/* Edit Mode - Show face status */}
          {isEditMode && (
            <div className="profile-form-field" style={{ marginTop: '24px' }}>
              <label style={{ fontSize: '16px', fontWeight: '600', color: '#2c3e50' }}>
                Face Recognition Status
              </label>
              <div style={{ 
                padding: '16px', 
                background: initialProfile?.faceRegistered ? '#d4edda' : '#f8d7da',
                border: `2px solid ${initialProfile?.faceRegistered ? '#28a745' : '#dc3545'}`,
                borderRadius: '8px',
                marginTop: '8px'
              }}>
                <p style={{ 
                  color: initialProfile?.faceRegistered ? '#155724' : '#721c24', 
                  fontWeight: '600', 
                  margin: 0 
                }}>
                  {initialProfile?.faceRegistered ? ' Face Registered' : ' Face Not Registered'}
                </p>
                {!initialProfile?.faceRegistered && (
                  <p style={{ color: '#721c24', fontSize: '14px', marginTop: '8px' }}>
                    Contact admin to update face registration.
                  </p>
                )}
              </div>
            </div>
          )}

          <button type="submit" className="profile-form-submit">
            {isEditMode ? 'Save Changes' : 'Complete Profile'}
          </button>
        </form>
      </div>
    </div>
  );
}

export default ProfileForm;
