import React, { useRef, useEffect, useState } from 'react';
import * as faceapi from 'face-api.js';
import '../styles/FaceCapture.css';

function FaceCapture({ onCapture, onCancel, mode = 'register', userName = '' }) {
  const videoRef = useRef();
  const canvasRef = useRef();
  const [modelsLoaded, setModelsLoaded] = useState(false);
  const [faceDetected, setFaceDetected] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Load face-api.js models
  useEffect(() => {
    const loadModels = async () => {
      // Try multiple paths for model loading
      const MODEL_URL = process.env.PUBLIC_URL ? `${process.env.PUBLIC_URL}/models` : '/models';
      
      try {
        setLoading(true);
        console.log('Loading models from:', MODEL_URL);
        
        await Promise.all([
          faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
          faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL),
          faceapi.nets.faceRecognitionNet.loadFromUri(MODEL_URL),
        ]);
        
        console.log('Models loaded successfully');
        setModelsLoaded(true);
        setLoading(false);
      } catch (err) {
        console.error('Error loading models:', err);
        console.error('Tried loading from:', MODEL_URL);
        setError('Failed to load face recognition models. Please ensure models are in /models/ folder. Error: ' + err.message);
        setLoading(false);
      }
    };
    loadModels();
  }, []);

  // Start video stream
  useEffect(() => {
    if (modelsLoaded) {
      startVideo();
    }
  }, [modelsLoaded]);

  const startVideo = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { width: 640, height: 480 } 
      });
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
      }
    } catch (err) {
      console.error('Error accessing camera:', err);
      setError('Camera access denied. Please allow camera permissions.');
    }
  };

  // Detect face in real-time
  const handleVideoPlay = () => {
    if (!videoRef.current || !canvasRef.current) return;

    const canvas = canvasRef.current;
    const displaySize = { 
      width: videoRef.current.videoWidth || 640, 
      height: videoRef.current.videoHeight || 480 
    };
    
    faceapi.matchDimensions(canvas, displaySize);

    const interval = setInterval(async () => {
      if (!videoRef.current || !canvasRef.current) {
        clearInterval(interval);
        return;
      }

      try {
        const detections = await faceapi
          .detectAllFaces(videoRef.current, new faceapi.TinyFaceDetectorOptions())
          .withFaceLandmarks()
          .withFaceDescriptors();

        const resizedDetections = faceapi.resizeResults(detections, displaySize);
        
        // Clear and redraw
        const ctx = canvas.getContext('2d');
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        
        // Draw detection boxes
        faceapi.draw.drawDetections(canvas, resizedDetections);
        faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);

        setFaceDetected(detections.length === 1); // Exactly one face
      } catch (err) {
        console.error('Detection error:', err);
      }
    }, 100);

    // Cleanup
    return () => clearInterval(interval);
  };

  // Capture face descriptor
  const captureFrame = async () => {
    try {
      const detections = await faceapi
        .detectSingleFace(videoRef.current, new faceapi.TinyFaceDetectorOptions())
        .withFaceLandmarks()
        .withFaceDescriptor();

      if (detections) {
        // Check detection quality
        const score = detections.detection.score;
        console.log('Face detection score:', score);
        
        // Get facial landmarks for validation
        const landmarks = detections.landmarks;
        
        // STRICT VALIDATION: Check if eyes are open and face is normal
        const leftEye = landmarks.getLeftEye();
        const rightEye = landmarks.getRightEye();
        const nose = landmarks.getNose();
        const mouth = landmarks.getMouth();
        
        // Calculate eye aspect ratio (EAR) to detect closed eyes
        const leftEyeHeight = Math.abs(leftEye[1].y - leftEye[5].y) + Math.abs(leftEye[2].y - leftEye[4].y);
        const leftEyeWidth = Math.abs(leftEye[0].x - leftEye[3].x);
        const leftEAR = leftEyeHeight / (2 * leftEyeWidth);
        
        const rightEyeHeight = Math.abs(rightEye[1].y - rightEye[5].y) + Math.abs(rightEye[2].y - rightEye[4].y);
        const rightEyeWidth = Math.abs(rightEye[0].x - rightEye[3].x);
        const rightEAR = rightEyeHeight / (2 * rightEyeWidth);
        
        const avgEAR = (leftEAR + rightEAR) / 2;
        
        // Calculate mouth aspect ratio (MAR) to detect open mouth/weird expressions
        const mouthHeight = Math.abs(mouth[3].y - mouth[9].y);
        const mouthWidth = Math.abs(mouth[0].x - mouth[6].x);
        const mouthMAR = mouthHeight / mouthWidth;
        
        console.log('Eye Aspect Ratio (EAR):', avgEAR.toFixed(3), '(should be > 0.15)');
        console.log('Mouth Aspect Ratio (MAR):', mouthMAR.toFixed(3), '(should be < 0.5)');
        
        // VALIDATION RULES
        if (avgEAR < 0.15) {
          alert(' Eyes appear closed or partially closed!\n\nPlease:\n Open your eyes fully\n Look directly at the camera\n Ensure good lighting on your face');
          return;
        }
        
        if (mouthMAR > 0.5) {
          alert(' Unusual mouth expression detected!\n\nPlease:\n Keep mouth closed or neutral\n Avoid smiling widely or opening mouth\n Use a neutral facial expression');
          return;
        }
        
        // Require minimum quality for registration (not for verification)
        if (mode === 'register' && score < 0.6) {
          alert(` Face detection quality too low (${(score * 100).toFixed(0)}%)!\n\nPlease:\n Ensure good lighting\n Face camera directly\n Remove obstructions (glasses, mask)\n Move closer to camera`);
          return;
        }
        
        // Get 128-dimensional face descriptor
        const faceDescriptor = Array.from(detections.descriptor);
        
        // Store landmarks for later comparison
        const landmarksArray = landmarks.positions.map(p => ({ x: p.x, y: p.y }));
        
        console.log('Face captured successfully! Quality:', (score * 100).toFixed(0) + '%', 'EAR:', avgEAR.toFixed(3), 'MAR:', mouthMAR.toFixed(3));
        
        // Stop video stream
        const stream = videoRef.current.srcObject;
        if (stream) {
          stream.getTracks().forEach(track => track.stop());
        }
        
        onCapture({ 
          faceDescriptor, 
          detected: true, 
          quality: score,
          landmarks: landmarksArray,
          eyeAspectRatio: avgEAR,
          mouthAspectRatio: mouthMAR
        });
      } else {
        alert(' No face detected! Please position your face clearly in the frame.');
      }
    } catch (err) {
      console.error('Capture error:', err);
      alert(' Error capturing face. Please try again.');
    }
  };

  // Cancel and stop camera
  const handleCancel = () => {
    const stream = videoRef.current?.srcObject;
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
    }
    onCancel();
  };

  if (loading) {
    return (
      <div className="face-capture-overlay">
        <div className="face-capture-modal">
          <div className="loading-models">
            <h3> Loading Face Recognition...</h3>
            <p>Please wait while we load the AI models...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="face-capture-overlay">
        <div className="face-capture-modal">
          <div className="error-message">
            <h3> Error</h3>
            <p>{error}</p>
            <button onClick={handleCancel} className="btn-cancel">Close</button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="face-capture-overlay">
      <div className="face-capture-modal">
        <button className="close-button" onClick={handleCancel}></button>
        
        <h2>
          {mode === 'register' ? ' Register Your Face' : ' Verify Your Face'}
        </h2>
        {userName && <p className="user-name">User: {userName}</p>}
        
        <div className="instructions">
          {mode === 'register' ? (
            <>
              <p><strong> Important:</strong> High-quality registration required!</p>
              <p> Position your face in the center, look directly at camera</p>
              <p> Good lighting is essential - face must be well-lit</p>
              <p> Remove glasses, masks, hats if possible</p>
              <p> Keep face steady and neutral expression</p>
              <p> This photo will be used for strict security verification</p>
            </>
          ) : (
            <>
              <p><strong> Security Verification Active</strong></p>
              <p> Match the conditions from your registration photo</p>
              <p> Same lighting, angle, and facial features required</p>
              <p> Glasses/masks must match registration state</p>
            </>
          )}
        </div>

        <div className="video-container">
          <video
            ref={videoRef}
            width="640"
            height="480"
            autoPlay
            muted
            onPlay={handleVideoPlay}
          />
          <canvas
            ref={canvasRef}
            className="detection-canvas"
          />
          
          <div className="face-status">
            {faceDetected ? (
              <span className="status-detected"> Face Detected - Ready to Capture</span>
            ) : (
              <span className="status-not-detected"> Position Your Face in Frame</span>
            )}
          </div>
        </div>

        <div className="action-buttons">
          <button
            onClick={captureFrame}
            disabled={!faceDetected}
            className={`btn-capture ${faceDetected ? 'active' : 'disabled'}`}
          >
            {mode === 'register' ? ' Capture & Register Face' : ' Verify Face'}
          </button>
          <button onClick={handleCancel} className="btn-cancel-alt">
            Cancel
          </button>
        </div>
      </div>
    </div>
  );
}

export default FaceCapture;
