import React from 'react';
import { formatDate } from '../utils/helpers';
import '../styles/ProfileCard.css';

function ProfileCard({ profile }) {
  if (!profile) return null;

  const fullName = [profile.firstName, profile.middleName, profile.lastName]
    .filter(Boolean)
    .join(' ');

  return (
    <div className="profile-card">
      <div className="profile-card-header">
        <div className="profile-card-avatar">
          {profile.profilePicture ? (
            <img src={profile.profilePicture} alt={fullName} />
          ) : (
            <div className="profile-card-avatar-placeholder">
              {profile.firstName?.charAt(0)}{profile.lastName?.charAt(0)}
            </div>
          )}
        </div>
        <h3 className="profile-card-name">{fullName}</h3>
      </div>

      <div className="profile-card-info">
        <div className="profile-card-info-item">
          <span className="profile-card-label"> Email</span>
          <span className="profile-card-value">{profile.email}</span>
        </div>
        <div className="profile-card-info-item">
          <span className="profile-card-label"> Phone</span>
          <span className="profile-card-value">{profile.phoneNumber}</span>
        </div>
        <div className="profile-card-info-item">
          <span className="profile-card-label"> Birthday</span>
          <span className="profile-card-value">
            {formatDate(profile.dateOfBirth)}
          </span>
        </div>
        <div className="profile-card-info-item">
          <span className="profile-card-label"> Address</span>
          <span className="profile-card-value profile-card-address">
            {profile.address}
          </span>
        </div>
      </div>
    </div>
  );
}

export default ProfileCard;
