import React, { useState, useEffect } from 'react';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import { exportToCSV, printTable, paginateData, getTotalPages } from '../utils/helpers';
import { formatUTCtoIST } from '../utils/istTimeUtils';
import useSortableData from '../hooks/useSortableData';
import '../styles/WorkLogHistory.css';
import '../styles/TablePagination.css';

function WorkLogHistory({ user, showActions = false, onEdit, onDelete }) {
  const [submissionHistory, setSubmissionHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [dateFilter, setDateFilter] = useState('SHOW');
  const [searchText, setSearchText] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  
  // Column-specific filters
  const [columnFilters, setColumnFilters] = useState({
    status: '',
    priority: '',
    approvedBy: '',
    overdueStatus: '',
    rejectionCount: ''
  });

  const employeeEmail = user?.email || 'default';

  useEffect(() => {
    fetchSubmissionHistory();
    
    // Set up polling every 10 seconds for real-time updates
    const pollingInterval = setInterval(() => {
      fetchSubmissionHistorySilent(); // Use silent version for polling
    }, 10000); // 10 seconds
    
    // Cleanup interval on unmount
    return () => clearInterval(pollingInterval);
  }, [employeeEmail]);

  // Silent fetch for polling - no loading state, only updates if changed
  const fetchSubmissionHistorySilent = async () => {
    try {
      const endpoint = employeeEmail === 'all' 
        ? `${getApiBaseUrl()}/api/Task/all`
        : `${getApiBaseUrl()}/api/Task/employee/${employeeEmail}`;
      
      const response = await fetch(endpoint, {
        headers: getApiHeaders()
      });
      
      if (response.ok) {
        const data = await response.json();
        // Map task fields to expected format (same as fetchSubmissionHistory)
        const formattedData = data.map(task => ({
          id: task.id,
          createdAt: task.createdAt,
          taskSummary: task.title,
          description: task.description,
          assignedToEmail: task.assignedToEmail,
          dueDate: task.dueDate,
          priority: task.priority,
          status: task.status,
          employeeRemarks: task.employeeRemarks,
          approvedBy: task.approvedBy || task.rejectedBy,
          approvalTime: task.approvedDate || task.rejectedDate,
          adminRemarks: task.adminRemarks,
          completedDate: task.completedDate,
          rejectionCount: task.rejectionCount || 0
        }));
        const sorted = formattedData.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        // Only update if data actually changed
        if (JSON.stringify(sorted) !== JSON.stringify(submissionHistory)) {
          setSubmissionHistory(sorted);
        }
      }
    } catch (error) {
      console.debug('Polling error (ignored):', error.message);
    }
  };

  const fetchSubmissionHistory = () => {
    setLoading(true);
    // Fetch tasks - if user email is 'all', fetch all tasks (admin view), otherwise fetch employee tasks
    const endpoint = employeeEmail === 'all' 
      ? `${getApiBaseUrl()}/api/Task/all`
      : `${getApiBaseUrl()}/api/Task/employee/${employeeEmail}`;
    
    fetch(endpoint, {
      headers: getApiHeaders()
    })
      .then(res => res.json())
      .then(data => {
        // Map task fields to expected format
        const formattedData = data.map(task => ({
          id: task.id,
          createdAt: task.createdAt,
          taskSummary: task.title,
          description: task.description,
          assignedToEmail: task.assignedToEmail,
          dueDate: task.dueDate,
          priority: task.priority,
          status: task.status,
          employeeRemarks: task.employeeRemarks,
          approvedBy: task.approvedBy || task.rejectedBy,
          approvalTime: task.approvedDate || task.rejectedDate,
          adminRemarks: task.adminRemarks,
          completedDate: task.completedDate,
          rejectionCount: task.rejectionCount || 0
        }));
        const sorted = formattedData.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        setSubmissionHistory(sorted);
      })
      .catch(err => console.error('Failed to fetch task history:', err))
      .finally(() => setLoading(false));
  };

  // Filter by date
  const getFilteredByDate = (records) => {
    if (dateFilter === 'SHOW') return records;

    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    const dayBeforeYesterday = new Date(today);
    dayBeforeYesterday.setDate(dayBeforeYesterday.getDate() - 2);

    return records.filter(record => {
      const recordDate = new Date(record.createdAt);
      if (dateFilter === 'TODAY') return recordDate >= today;
      if (dateFilter === 'YESTERDAY') return recordDate >= yesterday && recordDate < today;
      if (dateFilter === 'LATEST') return recordDate >= dayBeforeYesterday;
      return true;
    });
  };

  // Filter by search text and column filters
  const getFilteredRecords = () => {
    let filtered = getFilteredByDate(submissionHistory);

    // Text search
    if (searchText.trim()) {
      const search = searchText.toLowerCase();
      filtered = filtered.filter(record =>
        (record.taskSummary || '').toLowerCase().includes(search) ||
        (record.status || '').toLowerCase().includes(search) ||
        (record.employeeRemarks || '').toLowerCase().includes(search) ||
        (record.adminRemarks || '').toLowerCase().includes(search) ||
        (record.approvedBy || '').toLowerCase().includes(search) ||
        formatUTCtoIST(record.createdAt).toLowerCase().includes(search)
      );
    }

    // Column filters
    if (columnFilters.status) {
      filtered = filtered.filter(record => 
        (record.status || '').toLowerCase() === columnFilters.status.toLowerCase()
      );
    }
    if (columnFilters.priority) {
      filtered = filtered.filter(record => 
        (record.priority || '').toLowerCase() === columnFilters.priority.toLowerCase()
      );
    }
    if (columnFilters.approvedBy) {
      filtered = filtered.filter(record =>
        (record.approvedBy || '').toLowerCase().includes(columnFilters.approvedBy.toLowerCase())
      );
    }
    if (columnFilters.overdueStatus) {
      filtered = filtered.filter(record => {
        const isOverdue = calculateOverdueStatus(record);
        return isOverdue.toLowerCase() === columnFilters.overdueStatus.toLowerCase();
      });
    }
    if (columnFilters.rejectionCount) {
      const filterValue = columnFilters.rejectionCount;
      filtered = filtered.filter(record => {
        if (filterValue === 'rejected') return (record.rejectionCount || 0) > 0;
        if (filterValue === 'never') return (record.rejectionCount || 0) === 0;
        if (filterValue === '1') return (record.rejectionCount || 0) === 1;
        if (filterValue === '2+') return (record.rejectionCount || 0) >= 2;
        return true;
      });
    }

    return filtered;
  };

  // Calculate if task is overdue or completed in time
  const calculateOverdueStatus = (record) => {
    if (!record.dueDate) return 'N/A';
    const dueDate = new Date(record.dueDate);
    
    // If task is completed (status Completed or Approved), we compare completedDate vs dueDate.
    if (record.completedDate) {
        const completedAt = new Date(record.completedDate);
        return completedAt > dueDate ? 'Overdue' : 'On Time';
    }
    
    // If Pending/Rejected (not completed yet), compare with NOW.
    if (new Date() > dueDate) {
        return 'Overdue';
    }
    
    return 'Pending';
  };

  const handleColumnFilterChange = (column, value) => {
    setColumnFilters(prev => ({
      ...prev,
      [column]: value
    }));
    setCurrentPage(1);
  };

  const filteredRecords = getFilteredRecords();
  const { items: sortedData, requestSort, sortConfig } = useSortableData(filteredRecords);
  const paginatedData = paginateData(sortedData, currentPage, itemsPerPage);
  const totalPages = getTotalPages(sortedData.length, itemsPerPage);

  const handlePrint = () => {
    const columns = [
      { label: '<th>Assigned On</th>', key: 'createdAt', getValue: (row) => formatUTCtoIST(row.createdAt) },
      { label: '<th>Submitted On</th>', key: 'completedDate', getValue: (row) => row.completedDate ? formatUTCtoIST(row.completedDate) : 'Not Submitted' },
      { label: '<th>Task Summary</th>', key: 'taskSummary', getValue: (row) => row.taskSummary || 'N/A' },
      { label: '<th>Due Date</th>', key: 'dueDate', getValue: (row) => row.dueDate ? formatUTCtoIST(row.dueDate) : 'N/A' },
      { label: '<th>Priority</th>', key: 'priority', getValue: (row) => row.priority || 'N/A' },
      { label: '<th>Status</th>', key: 'status', getValue: (row) => row.status || 'N/A' },
      { label: '<th>Employee Remarks</th>', key: 'employeeRemarks', getValue: (row) => row.employeeRemarks || 'N/A' },
      { label: '<th>Approved/Rejected By</th>', key: 'approvedBy', getValue: (row) => row.approvedBy || 'N/A' },
      { label: '<th>Approval Time</th>', key: 'approvalTime', getValue: (row) => row.approvalTime ? formatUTCtoIST(row.approvalTime) : 'N/A' },
      { label: '<th>Admin Remarks</th>', key: 'adminRemarks', getValue: (row) => row.adminRemarks || 'N/A' },
      { label: '<th>Times Rejected</th>', key: 'rejectionCount', getValue: (row) => row.rejectionCount || 0 },
      { label: '<th>Overdue Status</th>', key: 'overdueStatus', getValue: (row) => calculateOverdueStatus(row) }
    ];
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      statusFilter: columnFilters.status || null,
      priorityFilter: columnFilters.priority || null,
      approvedByFilter: columnFilters.approvedBy || null,
      overdueStatusFilter: columnFilters.overdueStatus || null,
      rejectionCountFilter: columnFilters.rejectionCount || null,
      searchText: searchText.trim() || null,
      totalRecords: submissionHistory.length,
      filteredRecords: filteredRecords.length
    };
    
    printTable(filteredRecords, columns, 'Work Log History', user?.name, user?.email, filterInfo);
  };

  const handleExport = () => {
    const exportData = filteredRecords.map(record => ({
      'Assigned On': formatUTCtoIST(record.createdAt),
      'Submitted On': record.completedDate ? formatUTCtoIST(record.completedDate) : 'Not Submitted',
      'Task Summary': record.taskSummary || 'N/A',
      'Due Date': record.dueDate ? formatUTCtoIST(record.dueDate) : 'N/A',
      'Priority': record.priority || 'N/A',
      'Status': record.status || 'N/A',
      'Employee Remarks': record.employeeRemarks || 'N/A',
      'Approved/Rejected By': record.approvedBy || 'N/A',
      'Approval Time': record.approvalTime ? formatUTCtoIST(record.approvalTime) : 'N/A',
      'Admin Remarks': record.adminRemarks || 'N/A',
      'Times Rejected': record.rejectionCount || 0,
      'Overdue Status': calculateOverdueStatus(record)
    }));
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      statusFilter: columnFilters.status || null,
      priorityFilter: columnFilters.priority || null,
      approvedByFilter: columnFilters.approvedBy || null,
      overdueStatusFilter: columnFilters.overdueStatus || null,
      rejectionCountFilter: columnFilters.rejectionCount || null,
      searchText: searchText.trim() || null,
      totalRecords: submissionHistory.length,
      filteredRecords: filteredRecords.length
    };
    
    exportToCSV(exportData, 'work_log_history', user?.name, user?.email, filterInfo);
  };

  const getSortIndicator = (columnName) => {
    if (!sortConfig || sortConfig.key !== columnName) return ' ';
    return sortConfig.direction === 'ascending' ? ' ' : ' ';
  };

  return (
    <div className="worklog-history-container">
      {/* TOP: Search Only */}
      <div className="worklog-history-header">
        <h3 className="worklog-history-title">
          Work Log History ({filteredRecords.length} records)
        </h3>
        <input
          type="text"
          placeholder="Search work logs..."
          value={searchText}
          onChange={(e) => {
            setSearchText(e.target.value);
            setCurrentPage(1);
          }}
          className="worklog-history-search"
        />
      </div>

      {/* MIDDLE: Table - Always show structure */}
      {loading ? (
        <div className="worklog-history-loading">
          Loading work log history...
        </div>
      ) : (
        <>
          <div className="worklog-history-table-wrapper">
            <table className="worklog-history-table">
              <thead>
                {/* Header Row with Column Names and Sort */}
                <tr className="worklog-history-table-header-row">
                  {showActions && (
                    <th onClick={() => requestSort('assignedToEmail')} className="worklog-history-th-sortable worklog-history-th-employee">
                      Employee{getSortIndicator('assignedToEmail')}
                    </th>
                  )}
                  <th onClick={() => requestSort('createdAt')} className="worklog-history-th-sortable worklog-history-th-date">
                    Assigned On{getSortIndicator('createdAt')}
                  </th>
                  <th onClick={() => requestSort('completedDate')} className="worklog-history-th-sortable worklog-history-th-submitted">
                    Submitted On{getSortIndicator('completedDate')}
                  </th>
                  <th onClick={() => requestSort('taskSummary')} className="worklog-history-th-sortable worklog-history-th-task">
                    Task Summary{getSortIndicator('taskSummary')}
                  </th>
                  <th onClick={() => requestSort('dueDate')} className="worklog-history-th-sortable worklog-history-th-due">
                    Due Date{getSortIndicator('dueDate')}
                  </th>
                  <th onClick={() => requestSort('priority')} className="worklog-history-th-sortable worklog-history-th-priority">
                    Priority{getSortIndicator('priority')}
                  </th>
                  <th onClick={() => requestSort('status')} className="worklog-history-th-sortable worklog-history-th-status">
                    Status{getSortIndicator('status')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-emp-remarks">
                    Employee Remarks
                  </th>
                  <th onClick={() => requestSort('approvedBy')} className="worklog-history-th-sortable worklog-history-th-approver">
                    Approved/Rejected By{getSortIndicator('approvedBy')}
                  </th>
                  <th onClick={() => requestSort('approvalTime')} className="worklog-history-th-sortable worklog-history-th-approval-time">
                    Approval Time{getSortIndicator('approvalTime')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-admin-remarks">
                    Admin Remarks
                  </th>
                  <th onClick={() => requestSort('rejectionCount')} className="worklog-history-th-sortable worklog-history-th-rejection-count">
                    Times Rejected{getSortIndicator('rejectionCount')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-overdue">
                    Overdue Status
                  </th>
                  {showActions && (
                    <th className="worklog-history-th worklog-history-th-actions">
                      Actions
                    </th>
                  )}
                </tr>
                {/* Filter Row */}
                <tr className="worklog-history-filter-row">
                  {showActions && (
                    <th className="worklog-history-filter-cell"></th>
                  )}
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell">
                    <select
                      value={columnFilters.priority}
                      onChange={(e) => handleColumnFilterChange('priority', e.target.value)}
                      className="worklog-history-filter-select"
                    >
                      <option value="">All</option>
                      <option value="High">High</option>
                      <option value="Medium">Medium</option>
                      <option value="Low">Low</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell">
                    <select
                      value={columnFilters.status}
                      onChange={(e) => handleColumnFilterChange('status', e.target.value)}
                      className="worklog-history-filter-select"
                    >
                      <option value="">All</option>
                      <option value="Pending">Pending</option>
                      <option value="Approved">Approved</option>
                      <option value="Rejected">Rejected</option>
                      <option value="Completed">Completed</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell">
                    <input
                      type="text"
                      placeholder="Filter..."
                      value={columnFilters.approvedBy}
                      onChange={(e) => handleColumnFilterChange('approvedBy', e.target.value)}
                      className="worklog-history-filter-input"
                    />
                  </th>
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell"></th>
                  <th className="worklog-history-filter-cell">
                    <select
                      value={columnFilters.rejectionCount}
                      onChange={(e) => handleColumnFilterChange('rejectionCount', e.target.value)}
                      className="worklog-history-filter-select"
                    >
                      <option value="">All</option>
                      <option value="never">Never Rejected</option>
                      <option value="rejected">Rejected (Any)</option>
                      <option value="1">Rejected 1 time</option>
                      <option value="2+">Rejected 2+ times</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell">
                    <select
                      value={columnFilters.overdueStatus}
                      onChange={(e) => handleColumnFilterChange('overdueStatus', e.target.value)}
                      className="worklog-history-filter-select"
                    >
                      <option value="">All</option>
                      <option value="On Time">On Time</option>
                      <option value="Overdue">Overdue</option>
                      <option value="Pending">Pending</option>
                    </select>
                  </th>
                  {showActions && (
                    <th className="worklog-history-filter-cell">
                      <button
                        onClick={() => {
                          setColumnFilters({ priority: '', status: '', approvedBy: '', rejectionCount: '', overdueStatus: '' });
                          setSearchText('');
                          setDateFilter('SHOW');
                          setCurrentPage(1);
                        }}
                        className="worklog-history-reset-button"
                        title="Reset all filters"
                      >
                        Reset
                      </button>
                    </th>
                  )}
                </tr>
              </thead>
              <tbody>
                {filteredRecords.length > 0 ? (
                  paginatedData.map((record, idx) => (
                    <tr key={idx} className="worklog-history-table-row">
                      {showActions && (
                        <td className="worklog-history-td">{record.assignedToEmail || 'N/A'}</td>
                      )}
                      <td className="worklog-history-td">{formatUTCtoIST(record.createdAt)}</td>
                      <td className="worklog-history-td">
                        {record.completedDate ? formatUTCtoIST(record.completedDate) : 'Not Submitted'}
                      </td>
                      <td className="worklog-history-td">{record.taskSummary || 'N/A'}</td>
                      <td className="worklog-history-td">
                        {record.dueDate ? formatUTCtoIST(record.dueDate) : 'N/A'}
                      </td>
                      <td className="worklog-history-td">
                        <span className={`worklog-history-badge-priority worklog-history-badge-priority-${record.priority?.toLowerCase() || 'na'}`}>
                          {record.priority || 'N/A'}
                        </span>
                      </td>
                      <td className="worklog-history-td">
                        <span className={`worklog-history-badge-status worklog-history-badge-status-${record.status?.toLowerCase() || 'na'}`}>
                          {record.status || 'N/A'}
                        </span>
                      </td>
                      <td className="worklog-history-td worklog-history-td-ellipsis">
                        {record.employeeRemarks || 'N/A'}
                      </td>
                      <td className="worklog-history-td">{record.approvedBy || 'N/A'}</td>
                      <td className="worklog-history-td">
                        {record.approvalTime ? formatUTCtoIST(record.approvalTime) : 'N/A'}
                      </td>
                      <td className="worklog-history-td worklog-history-td-ellipsis">
                        {record.adminRemarks || 'N/A'}
                      </td>
                      <td className="worklog-history-td">
                        {record.rejectionCount > 0 ? (
                          <span className="worklog-history-badge-rejection" style={{
                            backgroundColor: record.rejectionCount === 1 ? '#fff3cd' : '#f8d7da',
                            color: record.rejectionCount === 1 ? '#856404' : '#721c24',
                            padding: '0.25rem 0.5rem',
                            borderRadius: '0.25rem',
                            fontWeight: 'bold',
                            display: 'inline-flex',
                            alignItems: 'center',
                            gap: '0.25rem'
                          }}>
                             {record.rejectionCount}
                          </span>
                        ) : (
                          <span style={{ color: '#28a745' }}> Never</span>
                        )}
                      </td>
                      <td className="worklog-history-td">
                        <span className={`worklog-history-badge-overdue worklog-history-badge-overdue-${calculateOverdueStatus(record).toLowerCase().replace(' ', '-')}`}>
                          {calculateOverdueStatus(record)}
                        </span>
                      </td>
                      {showActions && (
                        <td className="worklog-history-td" style={{ whiteSpace: 'nowrap', textAlign: 'center', color: '#666' }}>
                          Read-Only
                        </td>
                      )}
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={showActions ? "13" : "11"} className="worklog-history-no-data">
                      No work log records found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* BOTTOM: Controls */}
          <div className="table-pagination-container">
            {/* LEFT: Pagination */}
            <div className="table-pagination-controls">
              <button
                onClick={() => setCurrentPage(1)}
                disabled={currentPage === 1}
                className="table-pagination-button"
              >
                First
              </button>
              <button
                onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                disabled={currentPage === 1}
                className="table-pagination-button"
              >
                Previous
              </button>
              <span className="table-pagination-info">
                Page {currentPage} of {totalPages} ({filteredRecords.length} total records)
              </span>
              <button
                onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                disabled={currentPage === totalPages}
                className="table-pagination-button"
              >
                Next
              </button>
              <button
                onClick={() => setCurrentPage(totalPages)}
                disabled={currentPage === totalPages}
                className="table-pagination-button"
              >
                Last
              </button>
              <select
                value={itemsPerPage}
                onChange={(e) => {
                  setItemsPerPage(Number(e.target.value));
                  setCurrentPage(1);
                }}
                className="table-pagination-select"
              >
                <option value={10}>10 rows</option>
                <option value={50}>50 rows</option>
                <option value={100}>100 rows</option>
              </select>
              <select
                value={dateFilter}
                onChange={(e) => {
                  setDateFilter(e.target.value);
                  setCurrentPage(1);
                }}
                className="table-pagination-select"
              >
                <option value="SHOW">SHOW ALL</option>
                <option value="TODAY">TODAY</option>
                <option value="YESTERDAY">YESTERDAY</option>
                <option value="LATEST">LATEST (3 days)</option>
              </select>
            </div>

            {/* RIGHT: Actions */}
            <div className="table-pagination-actions">
              <button
                onClick={handleExport}
                className="table-export-button"
              >
                 Export CSV
              </button>
              <button
                onClick={handlePrint}
                className="table-print-button"
              >
                 Print
              </button>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default WorkLogHistory;
