import React, { useState, useEffect } from 'react';
import EmployeeManagement from './EmployeeManagement';
import WorkLogManagement from './WorkLogManagement';
import WorkLogHistory from './WorkLogHistory';
import NetworkManagement from './NetworkManagement';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import '../styles/AdminDashboard.css';
import '../styles/TablePagination.css';
import { formatDate, exportToCSV, printTable, paginateData, getTotalPages, showLoadingCursor, hideLoadingCursor } from '../utils/helpers';
// IST timezone utilities
import { formatUTCtoIST, convertISTtoUTC, getDateTimeLocalValue } from '../utils/istTimeUtils';
import useSortableData, { SortableHeader } from '../hooks/useSortableData';

function AdminDashboard({ token, user }) {
  const [records, setRecords] = useState([]);
  const [filters, setFilters] = useState({ start: '', end: '', employeeId: '' });
  const [appliedFilters, setAppliedFilters] = useState({ start: '', end: '', employeeId: '' });
  const [error, setError] = useState('');
  const [editingId, setEditingId] = useState(null);
  const [newTimestamp, setNewTimestamp] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [showAddAttendance, setShowAddAttendance] = useState(false);
  const [manualAttendance, setManualAttendance] = useState({
    employeeId: '',
    timestamp: '',
    isClockIn: true
  });
  const [dateFilter, setDateFilter] = useState('SHOW');
  
  // Tab navigation state
  const [activeTab, setActiveTab] = useState('attendance'); // attendance, taskAssignment, workLogs, employeeMgmt, tasksOverview, networkManagement
  
  // Task assignment states
  const [allTasks, setAllTasks] = useState([]);
  const [employees, setEmployees] = useState([]);
  const [taskForm, setTaskForm] = useState({
    title: '',
    description: '',
    assignedToEmail: '',
    dueDate: '',
    priority: 'Medium',
    assignedByEmail: user.email
  });
  const [editingTaskId, setEditingTaskId] = useState(null);
  
  // Multi-task assignment states
  const [multiTaskMode, setMultiTaskMode] = useState(false);
  const [taskRows, setTaskRows] = useState([{
    id: Date.now(),
    title: '',
    description: '',
    dueDate: '',
    priority: 'Medium'
  }]);
  const [selectedEmployee, setSelectedEmployee] = useState('');
  const [submissionResults, setSubmissionResults] = useState([]);
  const [manualAttendanceSearch, setManualAttendanceSearch] = useState('');
  const [filterEmployeeSearch, setFilterEmployeeSearch] = useState('');
  const [manualAttendanceDropdownOpen, setManualAttendanceDropdownOpen] = useState(false);
  const [filterEmployeeDropdownOpen, setFilterEmployeeDropdownOpen] = useState(false);

  // State for Tasks Overview
  const [taskSearchTerm, setTaskSearchTerm] = useState('');
  const [taskColumnFilters, setTaskColumnFilters] = useState({
    priority: '',
    status: '',
    employee: ''
  });
  const [taskCurrentPage, setTaskCurrentPage] = useState(1);
  const [taskRowsPerPage, setTaskRowsPerPage] = useState(10);
  const [taskSortConfig, setTaskSortConfig] = useState({ key: null, direction: 'asc' });

  // Close dropdowns when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      // Close manual attendance dropdown if clicking outside
      if (manualAttendanceDropdownOpen && !event.target.closest('.manual-attendance-form')) {
        setManualAttendanceDropdownOpen(false);
      }
      // Close employee filter dropdown if clicking outside
      if (filterEmployeeDropdownOpen && !event.target.closest('.employee-filter-dropdown-wrapper')) {
        setFilterEmployeeDropdownOpen(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [manualAttendanceDropdownOpen, filterEmployeeDropdownOpen]);

  // Load attendance records on appliedFilters or token change
  useEffect(() => {
    fetchAttendanceRecords();
    fetchEmployees();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [appliedFilters, token]);

  // Load tasks when tasks overview tab is active
  useEffect(() => {
    if (activeTab === 'tasksOverview') {
      fetchAllTasks();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab]);

  const fetchEmployees = () => {
    fetch(`${getApiBaseUrl()}/api/employees`, {
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then((res) => res.json())
      .then((data) => {
        const employeeList = data.filter(u => u.role !== 'Admin');
        setEmployees(employeeList);
      })
      .catch((err) => console.error('Failed to fetch employees:', err));
  };

  const fetchAllTasks = () => {
    fetch(`${getApiBaseUrl()}/api/Task/all`, {
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then((res) => res.json())
      .then((data) => setAllTasks(data))
      .catch((err) => console.error('Failed to fetch tasks:', err));
  };

  const fetchAttendanceRecords = () => {
    const params = new URLSearchParams(appliedFilters).toString();
    const queryString = params ? `?${params}` : '';
    fetch(`${getApiBaseUrl()}/api/Admin/AttendanceRecords${queryString}`, {
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
      cache: 'no-store',
    })
      .then(async (res) => {
        if (!res.ok) {
          const errorText = await res.text();
          throw new Error(`Error ${res.status}: ${errorText}`);
        }
        return res.json();
      })
      .then(data => {
        // Sort by timestamp, latest first
        const sorted = data.sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
        setRecords(sorted);
        setCurrentPage(1);
      })
      .catch((err) => setError(err.message));
  };

  const handleTaskAssign = () => {
    if (!taskForm.title || !taskForm.assignedToEmail || !taskForm.dueDate) {
      alert(' Please fill in all required fields (Title, Employee, Due Date)');
      return;
    }

    const method = editingTaskId ? 'PUT' : 'POST';
    const url = editingTaskId 
      ? `${getApiBaseUrl()}/api/Task/edit/${editingTaskId}`
      : `${getApiBaseUrl()}/api/Task/assign`;

    showLoadingCursor();
    
    // Prepare task data with IST timezone handling for dueDate
    const taskData = { ...taskForm };
    if (taskData.dueDate) {
      const localDateTime = new Date(taskData.dueDate);
      const year = localDateTime.getFullYear();
      const month = String(localDateTime.getMonth() + 1).padStart(2, '0');
      const day = String(localDateTime.getDate()).padStart(2, '0');
      const hours = String(localDateTime.getHours()).padStart(2, '0');
      const minutes = String(localDateTime.getMinutes()).padStart(2, '0');
      const seconds = '00';
      taskData.dueDate = `${year}-${month}-${day}T${hours}:${minutes}:${seconds}+05:30`;
    }
    
    fetch(url, {
      method,
      headers: {
        ...getApiHeaders(),
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(taskData),
    })
      .then((res) => {
        if (!res.ok) throw new Error('Task assignment failed');
        return res.json();
      })
      .then(() => {
        alert(` Task ${editingTaskId ? 'updated' : 'assigned'} successfully!`);
        setTaskForm({
          title: '',
          description: '',
          assignedToEmail: '',
          dueDate: '',
          priority: 'Medium',
          assignedByEmail: user.email
        });
        setEditingTaskId(null);
        if (activeTab === 'tasksOverview') fetchAllTasks();
      })
      .catch((err) => alert(` Failed to ${editingTaskId ? 'update' : 'assign'} task: ${err.message}`))
      .finally(() => hideLoadingCursor());
  };

  // Multi-task assignment handlers
  const addTaskRow = () => {
    setTaskRows([...taskRows, {
      id: Date.now(),
      title: '',
      description: '',
      dueDate: '',
      priority: 'Medium'
    }]);
  };

  const removeTaskRow = (id) => {
    if (taskRows.length === 1) {
      alert(' At least one task row is required');
      return;
    }
    setTaskRows(taskRows.filter(row => row.id !== id));
  };

  const updateTaskRow = (id, field, value) => {
    setTaskRows(taskRows.map(row => 
      row.id === id ? { ...row, [field]: value } : row
    ));
  };

  const handleMultiTaskSubmit = async () => {
    if (!selectedEmployee) {
      alert(' Please select an employee');
      return;
    }

    // Validate all rows
    const invalidRows = taskRows.filter(row => !row.title || !row.dueDate);
    if (invalidRows.length > 0) {
      alert(' All tasks must have a Title and Due Date');
      return;
    }

    showLoadingCursor();
    setSubmissionResults([]);

    // Submit all tasks
    const results = [];
    for (let i = 0; i < taskRows.length; i++) {
      const row = taskRows[i];
      
      // Prepare task data with IST timezone handling for dueDate
      const taskData = {
        title: row.title,
        description: row.description,
        assignedToEmail: selectedEmployee,
        dueDate: row.dueDate,
        priority: row.priority,
        assignedByEmail: user.email
      };
      
      if (taskData.dueDate) {
        const localDateTime = new Date(taskData.dueDate);
        const year = localDateTime.getFullYear();
        const month = String(localDateTime.getMonth() + 1).padStart(2, '0');
        const day = String(localDateTime.getDate()).padStart(2, '0');
        const hours = String(localDateTime.getHours()).padStart(2, '0');
        const minutes = String(localDateTime.getMinutes()).padStart(2, '0');
        const seconds = '00';
        taskData.dueDate = `${year}-${month}-${day}T${hours}:${minutes}:${seconds}+05:30`;
      }

      try {
        const response = await fetch(`${getApiBaseUrl()}/api/Task/assign`, {
          method: 'POST',
          headers: getApiHeaders(),
          body: JSON.stringify(taskData),
        });

        if (response.ok) {
          results.push({ 
            index: i + 1, 
            title: row.title, 
            status: 'success', 
            message: 'Assigned successfully' 
          });
        } else {
          const error = await response.text();
          results.push({ 
            index: i + 1, 
            title: row.title, 
            status: 'failed', 
            message: error || 'Assignment failed' 
          });
        }
      } catch (err) {
        results.push({ 
          index: i + 1, 
          title: row.title, 
          status: 'failed', 
          message: err.message 
        });
      }
    }

    setSubmissionResults(results);
    hideLoadingCursor();

    // Check if all succeeded
    const allSuccess = results.every(r => r.status === 'success');
    if (allSuccess) {
      alert(` All ${results.length} tasks assigned successfully!`);
      // Reset form
      setTaskRows([{
        id: Date.now(),
        title: '',
        description: '',
        dueDate: '',
        priority: 'Medium'
      }]);
      setSelectedEmployee('');
    } else {
      const successCount = results.filter(r => r.status === 'success').length;
      alert(` ${successCount} of ${results.length} tasks assigned successfully. Check results below.`);
    }

    if (activeTab === 'tasksOverview') fetchAllTasks();
  };

  const handleEditTask = (task) => {
    // Convert from IST to local datetime-local format
    let dueDateLocal = '';
    if (task.dueDate) {
      const dueDate = new Date(task.dueDate);
      // If the date string contains +05:30, it's already IST
      // datetime-local expects local time, so we keep it as is
      const year = dueDate.getFullYear();
      const month = String(dueDate.getMonth() + 1).padStart(2, '0');
      const day = String(dueDate.getDate()).padStart(2, '0');
      const hours = String(dueDate.getHours()).padStart(2, '0');
      const minutes = String(dueDate.getMinutes()).padStart(2, '0');
      dueDateLocal = `${year}-${month}-${day}T${hours}:${minutes}`;
    }
    
    setTaskForm({
      title: task.title,
      description: task.description,
      assignedToEmail: task.assignedToEmail,
      dueDate: dueDateLocal,
      priority: task.priority,
      assignedByEmail: user.email
    });
    setEditingTaskId(task.id);
    setActiveTab('taskAssignment');
  };

  const handleDeleteTask = (taskId) => {
    if (!window.confirm('Delete this task? This action cannot be undone.')) return;

    showLoadingCursor();
    fetch(`${getApiBaseUrl()}/api/Task/delete/${taskId}`, {
      method: 'DELETE',
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        if (!res.ok) throw new Error('Delete failed');
        alert(' Task deleted successfully!');
        fetchAllTasks();
      })
      .catch((err) => alert(` Failed to delete task: ${err.message}`))
      .finally(() => hideLoadingCursor());
  };

  // Tasks Overview helper functions
  const handleTaskSort = (key) => {
    let direction = 'asc';
    if (taskSortConfig.key === key && taskSortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setTaskSortConfig({ key, direction });
  };

  const handleTaskColumnFilterChange = (e) => {
    const { name, value } = e.target;
    setTaskColumnFilters((prev) => ({ ...prev, [name]: value }));
    setTaskCurrentPage(1);
  };

  const exportTasksToCSV = () => {
    const headers = ['Employee', 'Task', 'Description', 'Due Date', 'Priority', 'Status', 'Assigned By'];
    const rows = getFilteredAndSortedTasks().map(task => [
      task.assignedToEmail || '',
      task.title || '',
      task.description || '',
      formatUTCtoIST(task.dueDate),
      task.priority || '',
      task.status || 'Pending',
      task.assignedByEmail || ''
    ]);

    const csvContent = [headers, ...rows].map(row => 
      row.map(cell => `"${String(cell).replace(/"/g, '""')}"`).join(',')
    ).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `tasks_overview_${new Date().toISOString().split('T')[0]}.csv`;
    link.click();
  };

  const printTasksTable = () => {
    const printContent = document.getElementById('tasks-overview-table').outerHTML;
    const printWindow = window.open('', '', 'height=600,width=800');
    printWindow.document.write('<html><head><title>Tasks Overview</title>');
    printWindow.document.write('<style>table{border-collapse:collapse;width:100%;}th,td{border:1px solid #ddd;padding:8px;text-align:left;}th{background-color:#f2f2f2;}</style>');
    printWindow.document.write('</head><body><h2>Tasks Overview</h2>');
    printWindow.document.write(printContent);
    printWindow.document.write('</body></html>');
    printWindow.document.close();
    printWindow.print();
  };

  const getFilteredAndSortedTasks = () => {
    let filtered = allTasks.filter(task => {
      const matchesSearch = 
        !taskSearchTerm ||
        (task.title && task.title.toLowerCase().includes(taskSearchTerm.toLowerCase())) ||
        (task.description && task.description.toLowerCase().includes(taskSearchTerm.toLowerCase())) ||
        (task.assignedToEmail && task.assignedToEmail.toLowerCase().includes(taskSearchTerm.toLowerCase()));

      const matchesPriority = !taskColumnFilters.priority || task.priority === taskColumnFilters.priority;
      const matchesStatus = !taskColumnFilters.status || (task.status || 'Pending') === taskColumnFilters.status;
      const matchesEmployee = !taskColumnFilters.employee || 
        (task.assignedToEmail && task.assignedToEmail.toLowerCase().includes(taskColumnFilters.employee.toLowerCase()));

      return matchesSearch && matchesPriority && matchesStatus && matchesEmployee;
    });

    if (taskSortConfig.key) {
      filtered.sort((a, b) => {
        let aVal = a[taskSortConfig.key];
        let bVal = b[taskSortConfig.key];

        if (taskSortConfig.key === 'dueDate') {
          aVal = new Date(aVal);
          bVal = new Date(bVal);
        }

        if (aVal < bVal) return taskSortConfig.direction === 'asc' ? -1 : 1;
        if (aVal > bVal) return taskSortConfig.direction === 'asc' ? 1 : -1;
        return 0;
      });
    }

    return filtered;
  };

  const handleDelete = (id) => {
    const record = records.find(r => r.id === id);
    const employeeName = record ? record.employeeName : 'this attendance';
    const timestamp = record ? formatUTCtoIST(record.timestamp) : 'N/A';
    
    if (!window.confirm(`Delete attendance record?\n\nEmployee: ${employeeName}\nTime: ${timestamp}\n\nThis action cannot be undone.`)) return;
    
    showLoadingCursor();
    fetch(`${getApiBaseUrl()}/api/Admin/AttendanceRecord/${id}`, {
      method: 'DELETE',
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        if (!res.ok) throw new Error('Delete failed');
        setRecords(records.filter((r) => r.id !== id));
        alert(` Attendance record for ${employeeName} deleted successfully!`);
      })
      .catch((err) => {
        alert(` Failed to delete attendance record: ${err.message}`);
        setError(err.message);
      })
      .finally(() => hideLoadingCursor());
  };

  const startEdit = (id, timestamp) => {
    setEditingId(id);
    // Convert IST timestamp to local datetime-local format
    const date = new Date(timestamp);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(date.getHours()).padStart(2, '0');
    const minutes = String(date.getMinutes()).padStart(2, '0');
    const localTimestamp = `${year}-${month}-${day}T${hours}:${minutes}`;
    setNewTimestamp(localTimestamp);
  };

  const cancelEdit = () => {
    setEditingId(null);
    setNewTimestamp('');
  };

  const saveEdit = (id) => {
    showLoadingCursor();
    
    // Convert datetime-local to IST timestamp
    // datetime-local gives us local time, treat it as IST
    const localDateTime = new Date(newTimestamp);
    const year = localDateTime.getFullYear();
    const month = String(localDateTime.getMonth() + 1).padStart(2, '0');
    const day = String(localDateTime.getDate()).padStart(2, '0');
    const hours = String(localDateTime.getHours()).padStart(2, '0');
    const minutes = String(localDateTime.getMinutes()).padStart(2, '0');
    const seconds = '00';
    const istTimestamp = `${year}-${month}-${day}T${hours}:${minutes}:${seconds}+05:30`;
    
    fetch(`${getApiBaseUrl()}/api/admin/AttendanceRecord/${id}`, {
      method: 'PUT',
      headers: getApiHeaders(),
      body: JSON.stringify(istTimestamp),
    })
      .then((res) => {
        if (!res.ok) throw new Error('Update failed');
        setRecords((prev) =>
          prev.map((r) =>
            r.id === id ? { ...r, timestamp: istTimestamp } : r
          )
        );
        cancelEdit();
      })
      .catch((err) => setError(err.message))
      .finally(() => hideLoadingCursor());
  };

  const handleManualAttendance = async () => {
    if (!manualAttendance.employeeId || !manualAttendance.timestamp) {
      alert('Please fill in all fields');
      return;
    }

    // Parse the datetime-local input (already in IST/local timezone)
    // Don't convert to UTC - send it as-is to backend which expects IST
    const selectedTimestamp = new Date(manualAttendance.timestamp);
    const selectedDate = selectedTimestamp.toDateString();
    
    // Find existing records for this employee on the same day
    const employeeRecordsToday = records.filter(record => {
      const recordDate = new Date(record.timestamp).toDateString();
      return record.employeeId === manualAttendance.employeeId && recordDate === selectedDate;
    });

    // If adding Clock OUT, verify it's after the latest Clock IN
    if (!manualAttendance.isClockIn) {
      const latestClockIn = employeeRecordsToday
        .filter(r => r.isClockIn)
        .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp))[0];
      
      if (latestClockIn) {
        const clockInTime = new Date(latestClockIn.timestamp);
        if (selectedTimestamp <= clockInTime) {
          alert(`Clock OUT time (${selectedTimestamp.toLocaleTimeString()}) must be after the Clock IN time (${clockInTime.toLocaleTimeString()}). Please select a later time.`);
          return;
        }
      }
    }

    // If adding Clock IN, verify it's before any Clock OUT on the same day
    if (manualAttendance.isClockIn) {
      const earliestClockOut = employeeRecordsToday
        .filter(r => !r.isClockIn)
        .sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp))[0];
      
      if (earliestClockOut) {
        const clockOutTime = new Date(earliestClockOut.timestamp);
        if (selectedTimestamp >= clockOutTime) {
          alert(`Clock IN time (${selectedTimestamp.toLocaleTimeString()}) must be before the Clock OUT time (${clockOutTime.toLocaleTimeString()}). Please select an earlier time.`);
          return;
        }
      }
    }

    showLoadingCursor();
    try {
      // Convert IST input to UTC for database storage
      const utcTimestamp = convertISTtoUTC(manualAttendance.timestamp);
      
      console.log('IST Input:', manualAttendance.timestamp);
      console.log('UTC for DB:', utcTimestamp);
      
      const response = await fetch(`${getApiBaseUrl()}/api/Attendance`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({
          employeeId: manualAttendance.employeeId,
          name: manualAttendance.employeeId.split('@')[0],
          timestamp: utcTimestamp,
          isClockIn: manualAttendance.isClockIn,
          wifiSsid: 'manual-entry'
        }),
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText || 'Failed to add attendance');
      }

      alert('Attendance added successfully!');
      setManualAttendance({ employeeId: '', timestamp: '', isClockIn: true });
      setShowAddAttendance(false);
      fetchAttendanceRecords();
    } catch (err) {
      alert('Error: ' + err.message);
    } finally {
      hideLoadingCursor();
    }
  };

  // Sorting
  const { items: sortedRecords, requestSort, sortConfig } = useSortableData(records);

  // Date filtering
  const getFilteredRecords = () => {
    if (dateFilter === 'SHOW') return sortedRecords;
    
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    const dayBeforeYesterday = new Date(today);
    dayBeforeYesterday.setDate(dayBeforeYesterday.getDate() - 2);
    
    if (dateFilter === 'TODAY') {
      return sortedRecords.filter(record => {
        const recordDate = new Date(record.timestamp);
        return recordDate >= today;
      });
    }
    
    if (dateFilter === 'YESTERDAY') {
      const tomorrowStart = new Date(today);
      tomorrowStart.setDate(tomorrowStart.getDate() + 1);
      return sortedRecords.filter(record => {
        const recordDate = new Date(record.timestamp);
        return recordDate >= yesterday && recordDate < today;
      });
    }
    
    if (dateFilter === 'LATEST') {
      return sortedRecords.filter(record => {
        const recordDate = new Date(record.timestamp);
        return recordDate >= dayBeforeYesterday;
      });
    }
    
    return sortedRecords;
  };

  const filteredRecords = getFilteredRecords();

  const handleExport = () => {
    // Export filtered records based on date filter
    const exportData = filteredRecords.map(record => ({
      'Employee ID': record.employeeId,
      'Name': record.name,
      'Timestamp': formatUTCtoIST(record.timestamp),
      'Type': record.isClockIn ? 'In' : 'Out',
      'Wi-Fi SSID': record.wifiSsid || 'N/A'
    }));
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      totalRecords: records.length,
      filteredRecords: filteredRecords.length
    };
    
    exportToCSV(exportData, `attendance_records_${new Date().toISOString().split('T')[0]}.csv`, null, null, filterInfo);
  };

  const handlePrint = () => {
    // Print filtered records based on date filter
    const columns = [
      { label: '<th>Employee ID</th>', key: 'employeeId' },
      { label: '<th>Name</th>', key: 'name' },
      { label: '<th>Timestamp</th>', key: 'timestamp', getValue: (record) => formatUTCtoIST(record.timestamp) },
      { label: '<th>Type</th>', key: 'isClockIn', getValue: (record) => record.isClockIn ? 'In' : 'Out' },
      { label: '<th>Wi-Fi SSID</th>', key: 'wifiSsid', getValue: (record) => record.wifiSsid || 'N/A' }
    ];
    
    // Build filter info object
    const filterInfo = {
      dateFilter: dateFilter !== 'SHOW' ? dateFilter : null,
      totalRecords: records.length,
      filteredRecords: filteredRecords.length
    };
    
    printTable(filteredRecords, columns, 'Attendance Records', null, null, filterInfo);
  };

  // Pagination
  const paginatedRecords = paginateData(filteredRecords, currentPage, itemsPerPage);
  const totalPages = getTotalPages(filteredRecords.length, itemsPerPage);

  const handlePageChange = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  return (
    <>
      {/* Title outside container, centered at top */}
      <h2 className="admin-dashboard-title">Admin Dashboard</h2>
      
      <div className="admin-dashboard">
        {/* Tab Navigation */}
        <div className="admin-tabs">
          <button
            className={`admin-tab ${activeTab === 'attendance' ? 'active' : ''}`}
            onClick={() => setActiveTab('attendance')}
          >
            Attendance
          </button>
          <button
            className={`admin-tab ${activeTab === 'taskAssignment' ? 'active' : ''}`}
            onClick={() => {
              setActiveTab('taskAssignment');
              setEditingTaskId(null);
              setTaskForm({
                title: '',
                description: '',
                assignedToEmail: '',
                dueDate: '',
                priority: 'Medium',
                assignedByEmail: user.email
              });
            }}
          >
            Task Assignment
          </button>
          <button
            className={`admin-tab ${activeTab === 'workLogs' ? 'active' : ''}`}
            onClick={() => setActiveTab('workLogs')}
          >
            Work Log Management
          </button>
          <button
            className={`admin-tab ${activeTab === 'tasksOverview' ? 'active' : ''}`}
            onClick={() => {
              setActiveTab('tasksOverview');
              fetchAllTasks();
            }}
          >
            Work Log History
          </button>
          <button
            className={`admin-tab ${activeTab === 'employeeMgmt' ? 'active' : ''}`}
            onClick={() => setActiveTab('employeeMgmt')}
          >
            Employee Management
          </button>
          
          <button
            className={`admin-tab ${activeTab === 'networkManagement' ? 'active' : ''}`}
            onClick={() => setActiveTab('networkManagement')}
          >
            Network Management
          </button>
        </div>

        {/* Tab Content - Only render active tab */}
        <div className="admin-tab-content">
          
          {/* ATTENDANCE TAB */}
          {activeTab === 'attendance' && (
            <div>
              <h2>Employees Attendance Log</h2>
      
      {/* Add Manual Attendance Button and Date Filter */}
      <div className="attendance-header-controls">
        <button
          className="action-button btn-add-manual-attendance"
          onClick={() => setShowAddAttendance(!showAddAttendance)}
        >
          {showAddAttendance ? 'Cancel' : '+ Add Attendance Manually'}
        </button>
      </div>

      {/* Manual Attendance Form */}
      {showAddAttendance && (
        <div className="manual-attendance-form">
          <h3>Add Attendance Manually</h3>
          <div className="manual-attendance-grid">
            <div>
              <label className="form-group-label">Employee ID (Email)</label>
              <div style={{ position: 'relative' }}>
                <input
                  type="text"
                  value={manualAttendance.employeeId ? employees.find(e => e.email === manualAttendance.employeeId)?.name || manualAttendance.employeeId : ''}
                  placeholder="Select Employee "
                  readOnly
                  onClick={() => setManualAttendanceDropdownOpen(!manualAttendanceDropdownOpen)}
                  className="form-input-styled"
                  style={{ cursor: 'pointer' }}
                />
                <div style={{ display: manualAttendanceDropdownOpen ? 'block' : 'none', position: 'absolute', zIndex: 1000, backgroundColor: 'white', border: '2px solid #e2e8f0', borderRadius: '8px', marginTop: '0.25rem', width: '100%', boxShadow: '0 4px 6px rgba(0,0,0,0.1)' }}>
                  <input
                    type="text"
                    placeholder=" Search employee..."
                    value={manualAttendanceSearch}
                    onChange={(e) => setManualAttendanceSearch(e.target.value)}
                    className="form-input-styled"
                    style={{ borderRadius: '8px 8px 0 0', borderBottom: '1px solid #e2e8f0' }}
                  />
                  <div style={{ maxHeight: '200px', overflowY: 'auto' }}>
                    {employees
                      .filter(emp => {
                        const searchLower = manualAttendanceSearch.toLowerCase();
                        const nameMatch = emp.name?.toLowerCase().includes(searchLower);
                        const emailMatch = emp.email?.toLowerCase().includes(searchLower);
                        return nameMatch || emailMatch;
                      })
                      .map(emp => (
                        <div
                          key={emp.email}
                          onClick={() => {
                            setManualAttendance({ ...manualAttendance, employeeId: emp.email });
                            setManualAttendanceSearch('');
                            setManualAttendanceDropdownOpen(false);
                          }}
                          style={{
                            padding: '0.75rem 1rem',
                            cursor: 'pointer',
                            borderBottom: '1px solid #f1f5f9',
                            transition: 'background 0.2s'
                          }}
                          onMouseEnter={(e) => e.target.style.backgroundColor = '#f8fafc'}
                          onMouseLeave={(e) => e.target.style.backgroundColor = 'white'}
                        >
                          <div style={{ fontWeight: '600' }}>{emp.name}</div>
                          <div style={{ fontSize: '0.85rem', color: '#64748b' }}>{emp.email}</div>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            </div>
            <div>
              <label className="form-group-label">Timestamp</label>
              <input
                type="datetime-local"
                value={manualAttendance.timestamp}
                onChange={(e) => {
                  setManualAttendance({ ...manualAttendance, timestamp: e.target.value });
                  e.target.blur();
                }}
                className="form-input-styled"
              />
            </div>
            <div>
              <label className="form-group-label">Type</label>
              <select
                value={manualAttendance.isClockIn}
                onChange={(e) => setManualAttendance({ ...manualAttendance, isClockIn: e.target.value === 'true' })}
                className="form-select-styled"
              >
                <option value="true">Clock In</option>
                <option value="false">Clock Out</option>
              </select>
            </div>
          </div>
          <div style={{ display: 'flex', gap: '0.5rem' }}>
            <button
              className="action-button btn-submit-manual"
              onClick={handleManualAttendance}
            >
              Submit Attendance
            </button>
            <button
              className="action-button btn-reset"
              onClick={() => setManualAttendance({ employeeId: '', timestamp: '', isClockIn: true })}
            >
              Clear
            </button>
          </div>
        </div>
      )}

      <div className="admin-actions">
        <label>
          Start Date:{' '}
          <input
            type="date"
            value={filters.start}
            onChange={(e) => {
              setFilters((f) => ({ ...f, start: e.target.value }));
              e.target.blur();
            }}
          />
        </label>
        <label>
          End Date:{' '}
          <input
            type="date"
            value={filters.end}
            min={filters.start}
            onChange={(e) => {
              setFilters((f) => ({ ...f, end: e.target.value }));
              e.target.blur();
            }}
          />
        </label>
        <label>
          Employee ID:{' '}
          <div className="employee-filter-dropdown-wrapper" style={{ position: 'relative', marginTop: '0.25rem' }}>
            <input
              type="text"
              value={filters.employeeId ? employees.find(e => e.email === filters.employeeId)?.name || filters.employeeId : ''}
              placeholder="All Employees "
              readOnly
              onClick={() => setFilterEmployeeDropdownOpen(!filterEmployeeDropdownOpen)}
              className="filter-select"
              style={{ cursor: 'pointer' }}
            />
            <div style={{ display: filterEmployeeDropdownOpen ? 'block' : 'none', position: 'absolute', zIndex: 1000, backgroundColor: 'white', border: '2px solid #e2e8f0', borderRadius: '8px', marginTop: '0.25rem', width: '100%', boxShadow: '0 4px 6px rgba(0,0,0,0.1)' }}>
              <input
                type="text"
                placeholder=" Search employee..."
                value={filterEmployeeSearch}
                onChange={(e) => setFilterEmployeeSearch(e.target.value)}
                className="filter-select"
                style={{ borderRadius: '8px 8px 0 0', borderBottom: '1px solid #e2e8f0' }}
              />
              <div style={{ maxHeight: '200px', overflowY: 'auto' }}>
                <div
                  onClick={() => {
                    setFilters((f) => ({ ...f, employeeId: '' }));
                    setFilterEmployeeSearch('');
                    setFilterEmployeeDropdownOpen(false);
                  }}
                  style={{
                    padding: '0.75rem 1rem',
                    cursor: 'pointer',
                    borderBottom: '1px solid #f1f5f9',
                    fontWeight: '600',
                    transition: 'background 0.2s'
                  }}
                  onMouseEnter={(e) => e.target.style.backgroundColor = '#f8fafc'}
                  onMouseLeave={(e) => e.target.style.backgroundColor = 'white'}
                >
                  All Employees
                </div>
                {employees
                  .filter(emp => {
                    const searchLower = filterEmployeeSearch.toLowerCase();
                    const nameMatch = emp.name?.toLowerCase().includes(searchLower);
                    const emailMatch = emp.email?.toLowerCase().includes(searchLower);
                    return nameMatch || emailMatch;
                  })
                  .map(emp => (
                    <div
                      key={emp.email}
                      onClick={() => {
                        setFilters((f) => ({ ...f, employeeId: emp.email }));
                        setFilterEmployeeSearch('');
                        setFilterEmployeeDropdownOpen(false);
                      }}
                      style={{
                        padding: '0.75rem 1rem',
                        cursor: 'pointer',
                        borderBottom: '1px solid #f1f5f9',
                        transition: 'background 0.2s'
                      }}
                      onMouseEnter={(e) => e.target.style.backgroundColor = '#f8fafc'}
                      onMouseLeave={(e) => e.target.style.backgroundColor = 'white'}
                    >
                      <div style={{ fontWeight: '600' }}>{emp.name}</div>
                      <div style={{ fontSize: '0.85rem', color: '#64748b' }}>{emp.email}</div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </label>
        <button className="action-button" onClick={() => {
          // Adjust dates to include full day range
          const adjustedFilters = { ...filters };
          if (filters.start) {
            const startDate = new Date(filters.start);
            startDate.setHours(0, 0, 0, 0);
            adjustedFilters.start = startDate.toISOString();
          }
          if (filters.end) {
            const endDate = new Date(filters.end);
            endDate.setHours(23, 59, 59, 999);
            adjustedFilters.end = endDate.toISOString();
          }
          setAppliedFilters(adjustedFilters);
        }}>Apply Filters</button>
        <button 
          className="action-button btn-reset" 
          onClick={() => {
            setFilters({ start: '', end: '', employeeId: '' });
            setAppliedFilters({ start: '', end: '', employeeId: '' });
            setFilterEmployeeSearch('');
          }}
        >
          Reset
        </button>
      </div>

      {error && <p className="error-text">{error}</p>}

      <div className="data-table-container" id="attendance-table-container">
        <table className="data-table" id="attendance-table">
          <thead>
            <tr>
              <SortableHeader columnKey="employeeId" label="Employee ID" requestSort={requestSort} sortConfig={sortConfig} />
              <SortableHeader columnKey="name" label="Name" requestSort={requestSort} sortConfig={sortConfig} />
              <SortableHeader columnKey="timestamp" label="Timestamp" requestSort={requestSort} sortConfig={sortConfig} />
              <SortableHeader columnKey="type" label="Type" requestSort={requestSort} sortConfig={sortConfig} />
              <SortableHeader columnKey="wifiSsid" label="Wi-Fi SSID" requestSort={requestSort} sortConfig={sortConfig} />
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {paginatedRecords.length === 0 ? (
              <tr>
                <td colSpan="6" className="text-center-cell">
                  No records found.
                </td>
              </tr>
            ) : (
              (() => {
                if (dateFilter !== 'LATEST') {
                  // Normal rendering for SHOW, TODAY, YESTERDAY
                  return paginatedRecords.map((record) => (
                    <tr key={record.id}>
                      <td>{record.employeeId}</td>
                      <td>{record.name}</td>
                      <td>
                        {editingId === record.id ? (
                          <input
                            type="datetime-local"
                            value={newTimestamp}
                            onChange={(e) => {
                              setNewTimestamp(e.target.value);
                              e.target.blur();
                            }}
                          />
                        ) : (
                          formatUTCtoIST(record.timestamp)
                        )}
                      </td>
                      <td className={record.isClockIn ? 'status-in-cell' : 'status-out-cell'}>
                        {record.isClockIn ? 'In' : 'Out'}
                      </td>
                      <td>{record.wifiSsid || 'N/A'}</td>
                      <td>
                        {editingId === record.id ? (
                          <>
                            <button className="edit-button" onClick={() => saveEdit(record.id)}>
                              Save
                            </button>
                            <button className="delete-button" onClick={cancelEdit}>
                              Cancel
                            </button>
                          </>
                        ) : (
                          <>
                            <button
                              className="edit-button"
                              onClick={() => startEdit(record.id, record.timestamp)}
                            >
                              Edit
                            </button>
                            <button className="delete-button" onClick={() => handleDelete(record.id)}>
                              Delete
                            </button>
                          </>
                        )}
                      </td>
                    </tr>
                  ));
                } else {
                  // LATEST mode - group by date with separators
                  const now = new Date();
                  const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
                  const yesterday = new Date(today);
                  yesterday.setDate(yesterday.getDate() - 1);
                  const dayBeforeYesterday = new Date(today);
                  dayBeforeYesterday.setDate(dayBeforeYesterday.getDate() - 2);
                  
                  const todayRecords = paginatedRecords.filter(r => new Date(r.timestamp) >= today);
                  const yesterdayRecords = paginatedRecords.filter(r => {
                    const d = new Date(r.timestamp);
                    return d >= yesterday && d < today;
                  });
                  const dayBeforeRecords = paginatedRecords.filter(r => {
                    const d = new Date(r.timestamp);
                    return d >= dayBeforeYesterday && d < yesterday;
                  });
                  
                  const renderRecords = (records) => records.map((record) => (
                    <tr key={record.id}>
                      <td>{record.employeeId}</td>
                      <td>{record.name}</td>
                      <td>
                        {editingId === record.id ? (
                          <input
                            type="datetime-local"
                            value={newTimestamp}
                            onChange={(e) => {
                              setNewTimestamp(e.target.value);
                              e.target.blur();
                            }}
                          />
                        ) : (
                          formatUTCtoIST(record.timestamp)
                        )}
                      </td>
                      <td className={record.isClockIn ? 'status-in-cell' : 'status-out-cell'}>
                        {record.isClockIn ? 'In' : 'Out'}
                      </td>
                      <td>{record.wifiSsid || 'N/A'}</td>
                      <td>
                        {editingId === record.id ? (
                          <>
                            <button className="edit-button" onClick={() => saveEdit(record.id)}>
                              Save
                            </button>
                            <button className="delete-button" onClick={cancelEdit}>
                              Cancel
                            </button>
                          </>
                        ) : (
                          <>
                            <button
                              className="edit-button"
                              onClick={() => startEdit(record.id, record.timestamp)}
                            >
                              Edit
                            </button>
                            <button className="delete-button" onClick={() => handleDelete(record.id)}>
                              Delete
                            </button>
                          </>
                        )}
                      </td>
                    </tr>
                  ));
                  
                  return (
                    <>
                      {todayRecords.length > 0 && (
                        <>
                          <tr className="summary-row-today">
                            <td colSpan="6" className="summary-cell-bold">
                               Today ({formatDate(today)})
                            </td>
                          </tr>
                          {renderRecords(todayRecords)}
                        </>
                      )}
                      {yesterdayRecords.length > 0 && (
                        <>
                          <tr className="summary-row-yesterday">
                            <td colSpan="6" className="summary-cell-bold">
                               Yesterday ({formatDate(yesterday)})
                            </td>
                          </tr>
                          {renderRecords(yesterdayRecords)}
                        </>
                      )}
                      {dayBeforeRecords.length > 0 && (
                        <>
                          <tr className="summary-row-latest">
                            <td colSpan="6" className="summary-cell-bold">
                               {formatDate(dayBeforeYesterday)}
                            </td>
                          </tr>
                          {renderRecords(dayBeforeRecords)}
                        </>
                      )}
                    </>
                  );
                }
              })()
            )}
          </tbody>
        </table>
      </div>

      {/* Pagination Controls */}
      {records.length > 0 && (
        <div className="table-pagination-container">
          <div className="table-pagination-controls">
            <button
              onClick={() => handlePageChange(1)}
              disabled={currentPage === 1}
              className="table-pagination-button"
            >
              First
            </button>
            <button
              onClick={() => handlePageChange(currentPage - 1)}
              disabled={currentPage === 1}
              className="table-pagination-button"
            >
              Previous
            </button>
            <span className="table-pagination-info">
              Page {currentPage} of {totalPages} ({filteredRecords.length} total records)
            </span>
            <button
              onClick={() => handlePageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
              className="table-pagination-button"
            >
              Next
            </button>
            <button
              onClick={() => handlePageChange(totalPages)}
              disabled={currentPage === totalPages}
              className="table-pagination-button"
            >
              Last
            </button>
            <select
              value={itemsPerPage}
              onChange={(e) => {
                setItemsPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="table-pagination-select"
            >
              <option value={10}>10 rows</option>
              <option value={50}>50 rows</option>
              <option value={100}>100 rows</option>
            </select>

            {/* Date Filter - Moved from top */}
            <select
              value={dateFilter}
              onChange={(e) => {
                setDateFilter(e.target.value);
                setCurrentPage(1);
              }}
              className="table-pagination-select"
            >
              <option value="SHOW">SHOW ALL</option>
              <option value="TODAY">TODAY</option>
              <option value="YESTERDAY">YESTERDAY</option>
              <option value="LATEST">LATEST (3 days)</option>
            </select>

            {/* Reset Filters Button */}
            <button
              onClick={() => {
                setDateFilter('SHOW');
                setCurrentPage(1);
              }}
              className="table-reset-button"
              title="Reset all filters"
            >
              Reset
            </button>
          </div>

          {/* Export and Print Buttons */}
          <div className="table-pagination-actions">
            <button
              onClick={handleExport}
              className="table-export-button"
            >
              Export CSV
            </button>
            <button
              onClick={handlePrint}
              className="table-print-button"
            >
              Print
            </button>
          </div>
        </div>
      )}
            </div>
          )}

          {/* TASK ASSIGNMENT TAB */}
          {activeTab === 'taskAssignment' && (
            <div style={{
              backgroundColor: '#fff',
              padding: '1.5rem',
              borderRadius: '12px',
              boxShadow: '0 4px 12px rgba(0,0,0,0.1)',
              marginBottom: '2rem',
              border: '2px solid #9C27B0'
            }}>
              {/* Mode Toggle */}
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '1.5rem' }}>
                <h3 style={{ margin: 0, color: '#9C27B0' }}>
                  {editingTaskId ? 'Edit Task' : multiTaskMode ? 'Assign Multiple Tasks' : 'Assign New Task'}
                </h3>
                {!editingTaskId && (
                  <button
                    onClick={() => {
                      setMultiTaskMode(!multiTaskMode);
                      setSubmissionResults([]);
                    }}
                    style={{
                      padding: '0.5rem 1rem',
                      borderRadius: '6px',
                      border: '2px solid #9C27B0',
                      backgroundColor: multiTaskMode ? '#9C27B0' : 'white',
                      color: multiTaskMode ? 'white' : '#9C27B0',
                      fontWeight: '600',
                      cursor: 'pointer'
                    }}
                  >
                    {multiTaskMode ? ' Single Task Mode' : '+ Multi-Task Mode'}
                  </button>
                )}
              </div>

              {/* SINGLE TASK MODE */}
              {!multiTaskMode ? (
                <div style={{ display: 'grid', gap: '1rem' }}>
                  <div>
                    <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem' }}>
                      Employee *
                    </label>
                    <select
                      value={taskForm.assignedToEmail}
                      onChange={(e) => setTaskForm({ ...taskForm, assignedToEmail: e.target.value })}
                      style={{
                        width: '100%',
                        padding: '0.6rem',
                        borderRadius: '8px',
                        border: '2px solid #e2e8f0',
                        fontSize: '0.95rem'
                      }}
                    >
                      <option value="">Select Employee</option>
                      {employees.map(emp => (
                        <option key={emp.email} value={emp.email}>
                          {emp.name ? `${emp.name} (${emp.email})` : emp.email}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem' }}>
                      Task Title *
                    </label>
                    <input
                      type="text"
                      value={taskForm.title}
                      onChange={(e) => setTaskForm({ ...taskForm, title: e.target.value })}
                      placeholder="Enter task title"
                      style={{
                        width: '100%',
                        padding: '0.6rem',
                        borderRadius: '8px',
                        border: '2px solid #e2e8f0',
                        fontSize: '0.95rem'
                      }}
                    />
                  </div>

                  <div>
                    <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem' }}>
                      Description
                    </label>
                    <textarea
                      value={taskForm.description}
                      onChange={(e) => setTaskForm({ ...taskForm, description: e.target.value })}
                      placeholder="Enter task description"
                      rows={3}
                      style={{
                        width: '100%',
                        padding: '0.6rem',
                        borderRadius: '8px',
                        border: '2px solid #e2e8f0',
                        fontSize: '0.95rem',
                        resize: 'vertical'
                      }}
                    />
                  </div>

                  <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '1rem' }}>
                    <div>
                      <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem' }}>
                        Due Date *
                      </label>
                      <input
                        type="datetime-local"
                        value={taskForm.dueDate}
                        onChange={(e) => {
                          setTaskForm({ ...taskForm, dueDate: e.target.value });
                          e.target.blur();
                        }}
                        style={{
                          width: '100%',
                          padding: '0.6rem',
                          borderRadius: '8px',
                          border: '2px solid #e2e8f0',
                          fontSize: '0.95rem'
                        }}
                      />
                    </div>

                    <div>
                      <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem' }}>
                        Priority
                      </label>
                      <select
                        value={taskForm.priority}
                        onChange={(e) => setTaskForm({ ...taskForm, priority: e.target.value })}
                        style={{
                          width: '100%',
                          padding: '0.6rem',
                          borderRadius: '8px',
                          border: '2px solid #e2e8f0',
                          fontSize: '0.95rem'
                        }}
                      >
                        <option value="Low">Low</option>
                        <option value="Medium">Medium</option>
                        <option value="High">High</option>
                      </select>
                    </div>
                  </div>

                  <div style={{ display: 'flex', gap: '0.5rem', marginTop: '0.5rem' }}>
                    <button
                      onClick={handleTaskAssign}
                      style={{
                        flex: 1,
                        padding: '0.75rem',
                        borderRadius: '8px',
                        border: 'none',
                        backgroundColor: '#9C27B0',
                        color: 'white',
                        fontSize: '1rem',
                        fontWeight: '600',
                        cursor: 'pointer'
                      }}
                    >
                      {editingTaskId ? 'Update Task' : 'Assign Task'}
                    </button>
                    {!editingTaskId && (
                      <button
                        onClick={() => {
                          setTaskForm({ employeeId: '', title: '', description: '', dueDate: '', priority: 'Medium' });
                        }}
                        style={{
                          flex: 1,
                          padding: '0.75rem',
                          borderRadius: '8px',
                          border: '2px solid #ef4444',
                          backgroundColor: 'white',
                          color: '#ef4444',
                          fontSize: '1rem',
                          fontWeight: '600',
                          cursor: 'pointer'
                        }}
                      >
                        Clear
                      </button>
                    )}
                  </div>
                </div>
              ) : (
                /* MULTI-TASK MODE */
                <div>
                  {/* Select Employee Once */}
                  <div style={{ marginBottom: '1.5rem', padding: '1rem', backgroundColor: '#f3e5f5', borderRadius: '8px' }}>
                    <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.5rem', color: '#9C27B0' }}>
                      Assign All Tasks To: *
                    </label>
                    <select
                      value={selectedEmployee}
                      onChange={(e) => setSelectedEmployee(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '0.75rem',
                        borderRadius: '8px',
                        border: '2px solid #9C27B0',
                        fontSize: '1rem',
                        fontWeight: '600'
                      }}
                    >
                      <option value="">Select Employee</option>
                      {employees.map(emp => (
                        <option key={emp.email} value={emp.email}>
                          {emp.name ? `${emp.name} (${emp.email})` : emp.email}
                        </option>
                      ))}
                    </select>
                  </div>

                  {/* Task Rows */}
                  {taskRows.map((row, index) => (
                    <div key={row.id} style={{ 
                      border: '2px solid #e2e8f0', 
                      borderRadius: '8px', 
                      padding: '1rem', 
                      marginBottom: '1rem',
                      backgroundColor: '#fafafa'
                    }}>
                      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '0.75rem' }}>
                        <h4 style={{ margin: 0, color: '#666' }}>Task #{index + 1}</h4>
                        {taskRows.length > 1 && (
                          <button
                            onClick={() => removeTaskRow(row.id)}
                            style={{
                              padding: '0.25rem 0.5rem',
                              borderRadius: '4px',
                              border: 'none',
                              backgroundColor: '#ef4444',
                              color: 'white',
                              cursor: 'pointer',
                              fontSize: '0.85rem'
                            }}
                          >
                             Remove
                          </button>
                        )}
                      </div>

                      <div style={{ display: 'grid', gap: '0.75rem' }}>
                        <div>
                          <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.3rem', fontSize: '0.9rem' }}>
                            Task Title *
                          </label>
                          <input
                            type="text"
                            value={row.title}
                            onChange={(e) => updateTaskRow(row.id, 'title', e.target.value)}
                            placeholder="Enter task title"
                            style={{
                              width: '100%',
                              padding: '0.5rem',
                              borderRadius: '6px',
                              border: '1px solid #ddd',
                              fontSize: '0.9rem'
                            }}
                          />
                        </div>

                        <div>
                          <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.3rem', fontSize: '0.9rem' }}>
                            Description
                          </label>
                          <textarea
                            value={row.description}
                            onChange={(e) => updateTaskRow(row.id, 'description', e.target.value)}
                            placeholder="Enter task description"
                            rows={2}
                            style={{
                              width: '100%',
                              padding: '0.5rem',
                              borderRadius: '6px',
                              border: '1px solid #ddd',
                              fontSize: '0.9rem',
                              resize: 'vertical'
                            }}
                          />
                        </div>

                        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '0.75rem' }}>
                          <div>
                            <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.3rem', fontSize: '0.9rem' }}>
                              Due Date *
                            </label>
                            <input
                              type="datetime-local"
                              value={row.dueDate}
                              onChange={(e) => {
                                updateTaskRow(row.id, 'dueDate', e.target.value);
                                e.target.blur();
                              }}
                              style={{
                                width: '100%',
                                padding: '0.5rem',
                                borderRadius: '6px',
                                border: '1px solid #ddd',
                                fontSize: '0.9rem'
                              }}
                            />
                          </div>

                          <div>
                            <label style={{ fontWeight: '600', display: 'block', marginBottom: '0.3rem', fontSize: '0.9rem' }}>
                              Priority
                            </label>
                            <select
                              value={row.priority}
                              onChange={(e) => updateTaskRow(row.id, 'priority', e.target.value)}
                              style={{
                                width: '100%',
                                padding: '0.5rem',
                                borderRadius: '6px',
                                border: '1px solid #ddd',
                                fontSize: '0.9rem'
                              }}
                            >
                              <option value="Low">Low</option>
                              <option value="Medium">Medium</option>
                              <option value="High">High</option>
                            </select>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}

                  {/* Add Task Button */}
                  <button
                    onClick={addTaskRow}
                    style={{
                      padding: '0.75rem',
                      borderRadius: '8px',
                      border: '2px dashed #9C27B0',
                      backgroundColor: 'white',
                      color: '#9C27B0',
                      fontSize: '1rem',
                      fontWeight: '600',
                      cursor: 'pointer',
                      width: '100%',
                      marginBottom: '1rem'
                    }}
                  >
                     Add Another Task
                  </button>

                  {/* Submit All Button */}
                  <div style={{ display: 'flex', gap: '0.5rem', marginTop: '0.5rem' }}>
                    <button
                      onClick={handleMultiTaskSubmit}
                      style={{
                        flex: 1,
                        padding: '1rem',
                        borderRadius: '8px',
                        border: 'none',
                        backgroundColor: '#9C27B0',
                        color: 'white',
                        fontSize: '1.1rem',
                        fontWeight: '700',
                        cursor: 'pointer'
                      }}
                    >
                      Assign All {taskRows.length} Task{taskRows.length > 1 ? 's' : ''}
                    </button>
                    <button
                      onClick={() => {
                        setTaskRows([{ id: Date.now(), title: '', description: '', dueDate: '', priority: 'Medium' }]);
                        setSelectedEmployee('');
                        setSubmissionResults([]);
                      }}
                      style={{
                        flex: 1,
                        padding: '1rem',
                        borderRadius: '8px',
                        border: '2px solid #ef4444',
                        backgroundColor: 'white',
                        color: '#ef4444',
                        fontSize: '1.1rem',
                        fontWeight: '700',
                        cursor: 'pointer'
                      }}
                    >
                      Clear All
                    </button>
                  </div>

                  {/* Submission Results */}
                  {submissionResults.length > 0 && (
                    <div style={{ marginTop: '1.5rem', padding: '1rem', backgroundColor: '#f9fafb', borderRadius: '8px', border: '1px solid #e5e7eb' }}>
                      <h4 style={{ marginTop: 0, color: '#374151' }}>Submission Results:</h4>
                      {submissionResults.map((result, idx) => (
                        <div key={idx} style={{ 
                          padding: '0.5rem', 
                          marginBottom: '0.5rem', 
                          borderRadius: '6px',
                          backgroundColor: result.status === 'success' ? '#d1fae5' : '#fee2e2',
                          border: `1px solid ${result.status === 'success' ? '#10b981' : '#ef4444'}`
                        }}>
                          <strong>{result.status === 'success' ? '' : ''} Task #{result.index}:</strong> {result.title}
                          <div style={{ fontSize: '0.85rem', color: '#666', marginTop: '0.25rem' }}>
                            {result.message}
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              )}
            </div>
          )}

          {/* WORK LOGS TAB */}
          {activeTab === 'workLogs' && (
            <WorkLogManagement user={user} />
          )}

          {/* EMPLOYEE MANAGEMENT TAB */}
          {activeTab === 'employeeMgmt' && (
            <EmployeeManagement token={user.token} />
          )}

          {/* TASKS OVERVIEW TAB */}
          {activeTab === 'tasksOverview' && (
            <div>
              <h2 style={{ marginBottom: '1.5rem' }}> All Tasks Overview</h2>
              <WorkLogHistory 
                user={{ email: 'all', name: 'Admin' }} 
                showActions={true}
                onEdit={handleEditTask}
                onDelete={handleDeleteTask}
              />
            </div>
          )}

          {/* NETWORK MANAGEMENT TAB */}
          {activeTab === 'networkManagement' && (
            <NetworkManagement />
          )}

        </div>
      </div>
    </>
  );
}

export default AdminDashboard;
