import React, { useState, useEffect } from 'react';
import '../styles/Register.css';
import { showLoadingCursor, hideLoadingCursor } from '../utils/helpers';
import { validateEmail, validateName, validatePassword, getPasswordStrength } from '../utils/validation';
import { validateNetwork, getApiBaseUrl, getApiHeaders, loadAppConfig } from '../config/networkConfig';

function Register({ onSwitchToLogin }) {
  const [emailUsername, setEmailUsername] = useState('');
  const [companyDomain, setCompanyDomain] = useState('company.com');
  const email = emailUsername ? `${emailUsername}@${companyDomain}` : '';
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [msg, setMsg] = useState('');
  const [emailError, setEmailError] = useState('');
  const [nameError, setNameError] = useState('');
  const [passwordError, setPasswordError] = useState('');

  // Load company domain from config
  useEffect(() => {
    loadAppConfig().then(config => {
      const companyName = config.companyName || 'Company';
      // Extract domain dynamically: "Mark Audio" -> "markaudio", "Revit" -> "revit"
      const domain = companyName.toLowerCase().replace(/\s+/g, '');
      setCompanyDomain(`${domain}.com`);
    });
  }, []);

  const validate = () => {
    let valid = true;

    // Validate name
    const nameValidation = validateName(name);
    if (!nameValidation.valid) {
      setNameError(nameValidation.message);
      valid = false;
    }

    // Validate email
    const emailValidation = validateEmail(email);
    if (!emailValidation.valid) {
      setEmailError(emailValidation.message);
      valid = false;
    }

    // Validate password
    const passwordValidation = validatePassword(password);
    if (!passwordValidation.valid) {
      setPasswordError(passwordValidation.message);
      valid = false;
    }

    if (!valid) {
      alert('Please fix all validation errors before submitting');
    }

    return valid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMsg('');
    
    if (!validate()) return;

    // Validate network (Router MAC + Gateway IP)
    const networkCheck = await validateNetwork();
    if (!networkCheck.valid) {
      alert(` ${networkCheck.reason}`);
      return;
    }

    showLoadingCursor();

    try {
      const res = await fetch(`${getApiBaseUrl()}/api/auth/register`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({
          email,
          name,
          password,
          role: 'Employee',
          allowedGatewayIp: networkCheck.networkInfo.gatewayIp,
          allowedRouterMac: networkCheck.networkInfo.routerMac
        }),
      });
      const data = await res.json();
      if (!res.ok) {
        setMsg(data.message || "Registration failed");
      } else {
        // Show success alert
        alert("Registration successful! You can now log in.");
        // Clear the form and message
        setEmailUsername('');
        setName('');
        setPassword('');
        setMsg('');
        // Clear error messages
        setEmailError('');
        setNameError('');
        setPasswordError('');
        // Switch to login after a short delay
        setTimeout(() => onSwitchToLogin && onSwitchToLogin(), 500);
      }
    } catch (err) {
      setMsg("Server error, please try again.");
    } finally {
      hideLoadingCursor();
    }
  };

  return (
    <div className="register-container">
      <div className="register-header">
        <h2 className="register-title">Create Account</h2>
        <p className="register-subtitle">Please fill in your information to register</p>
      </div>
      <form onSubmit={handleSubmit} className="register-form" autoComplete="off">
        <div className="form-group">
          <label className="form-label">Email *</label>
          <div className="email-input-wrapper">
            <input 
              type="text" 
              className={`form-input ${emailError ? 'input-error' : ''}`}
              placeholder="Enter username"
              autoComplete="off"
              tabIndex={1}
              required 
              value={emailUsername} 
              onChange={e => {
                setEmailUsername(e.target.value);
                setEmailError('');
              }}
              onBlur={() => {
                const fullEmail = emailUsername ? `${emailUsername}@${companyDomain}` : '';
                const validation = validateEmail(fullEmail);
                setEmailError(validation.valid ? '' : validation.message);
              }}
            />
            <span className="email-domain-suffix">@{companyDomain}</span>
          </div>
          {emailError && <span className="error-text">{emailError}</span>}
        </div>
        <div className="form-group">
          <label className="form-label">Name * (Letters only)</label>
          <input 
            type="text" 
            className={`form-input ${nameError ? 'input-error' : ''}`}
            placeholder="Enter your full name"
            autoComplete="off"
            tabIndex={2}
            required 
            value={name} 
            onChange={e => {
              setName(e.target.value);
              setNameError('');
            }}
            onBlur={() => {
              const validation = validateName(name);
              setNameError(validation.valid ? '' : validation.message);
            }}
          />
          {nameError && <span className="error-text">{nameError}</span>}
        </div>
        <div className="form-group">
          <label className="form-label">Password * (Min 8 chars, 1 number, 1 special char)</label>
          <div className="password-input-wrapper">
            <input 
              type={showPassword ? "text" : "password"}
              className={`form-input ${passwordError ? 'input-error' : ''}`}
              placeholder="Enter your password"
              autoComplete="off"
              tabIndex={3}
              required 
              value={password} 
              onChange={e => {
                setPassword(e.target.value);
                setPasswordError('');
              }}
              onBlur={() => {
                const validation = validatePassword(password);
                setPasswordError(validation.valid ? '' : validation.message);
              }}
            />
            <button
              type="button"
              className="password-toggle"
              onClick={() => setShowPassword(!showPassword)}
              tabIndex={-1}
              aria-label={showPassword ? "Hide password" : "Show password"}
            >
              {showPassword ? (
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M3.98 8.223A10.477 10.477 0 0 0 1.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.451 10.451 0 0 1 12 4.5c4.756 0 8.773 3.162 10.065 7.498a10.522 10.522 0 0 1-4.293 5.774M6.228 6.228 3 3m3.228 3.228 3.65 3.65m7.894 7.894L21 21m-3.228-3.228-3.65-3.65m0 0a3 3 0 1 0-4.243-4.243m4.242 4.242L9.88 9.88" />
                </svg>
              ) : (
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M2.036 12.322a1.012 1.012 0 0 1 0-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178Z" />
                  <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
                </svg>
              )}
            </button>
          </div>
          {passwordError && <span className="error-text">{passwordError}</span>}
          {password && !passwordError && (
            <div className="password-strength">
              <div className="strength-bar">
                <div 
                  className="strength-fill" 
                  style={{ 
                    width: `${getPasswordStrength(password).strength}%`,
                    backgroundColor: getPasswordStrength(password).color
                  }}
                ></div>
              </div>
              <span style={{ color: getPasswordStrength(password).color, fontSize: '0.85rem', fontWeight: '500' }}>
                {getPasswordStrength(password).label}
              </span>
            </div>
          )}
        </div>
        <button type="submit" className="register-button" tabIndex={4}>Register</button>
        <div className="login-link">
          Already have an account?{' '}
          <button 
            type="button" 
            onClick={onSwitchToLogin}
            className="login-link-button"
          >
            Back to Login
          </button>
        </div>
        {msg && (
          <div className={msg.includes('successful') ? 'success-message' : 'error-message'}>
            {msg}
          </div>
        )}
      </form>
    </div>
  );
}

export default Register;
