import React, { useState, useEffect, useCallback } from 'react';
import '../styles/SelectionPopup.css';

function SelectionPopup() {
  const [visible, setVisible] = useState(false);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [selectedText, setSelectedText] = useState('');

  const handleRightClick = useCallback((e) => {
    const selection = window.getSelection();
    const text = selection.toString().trim();

    // Only show popup on right-click if text is selected (min 3 characters)
    if (text.length >= 3) {
      e.preventDefault(); // Prevent default context menu
      
      // Position popup at mouse cursor
      setPosition({
        x: e.pageX,
        y: e.pageY - 10 // 10px above cursor
      });

      setSelectedText(text);
      setVisible(true);
    } else {
      setVisible(false);
    }
  }, []);

  const handleClickOutside = useCallback((e) => {
    // Hide popup if clicking outside of it
    if (!e.target.closest('.selection-popup')) {
      setVisible(false);
    }
  }, []);

  useEffect(() => {
    // Listen for right-click (contextmenu) events on document
    document.addEventListener('contextmenu', handleRightClick);
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('contextmenu', handleRightClick);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [handleRightClick, handleClickOutside]);

  const handleCopy = async () => {
    try {
      if (!navigator.clipboard) {
        // Fallback for browsers that don't support clipboard API
        const textArea = document.createElement('textarea');
        textArea.value = selectedText;
        textArea.style.position = 'fixed';
        textArea.style.opacity = '0';
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand('copy');
        document.body.removeChild(textArea);
        alert(' Text copied to clipboard!');
      } else {
        await navigator.clipboard.writeText(selectedText);
        alert(' Text copied to clipboard!');
      }
      setVisible(false);
    } catch (err) {
      console.error('Copy error:', err);
      alert(' Failed to copy text. Please try again.');
    }
  };

  const handlePrint = () => {
    const printWindow = window.open('', '_blank', 'width=800,height=600');
    if (printWindow) {
      printWindow.document.write(`
        <!DOCTYPE html>
        <html>
          <head>
            <title>Print Selected Text</title>
            <style>
              body {
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                padding: 2rem;
                line-height: 1.6;
                color: #2c3e50;
              }
              h1 {
                color: #3498db;
                border-bottom: 2px solid #3498db;
                padding-bottom: 0.5rem;
                margin-bottom: 1rem;
              }
              .content {
                white-space: pre-wrap;
                word-wrap: break-word;
              }
              .footer {
                margin-top: 2rem;
                padding-top: 1rem;
                border-top: 1px solid #ccc;
                font-size: 0.875rem;
                color: #7f8c8d;
              }
              @media print {
                body {
                  padding: 1rem;
                }
              }
            </style>
          </head>
          <body>
            <h1>Selected Text</h1>
            <div class="content">${selectedText.replace(/\n/g, '<br>')}</div>
            <div class="footer">
              Printed on: ${new Date().toLocaleString()}
            </div>
            <script>
              window.onload = function() {
                window.print();
              };
            </script>
          </body>
        </html>
      `);
      printWindow.document.close();
    }
    setVisible(false);
  };

  if (!visible) return null;

  return (
    <div
      className="selection-popup"
      style={{
        left: `${position.x}px`,
        top: `${position.y}px`,
      }}
    >
      <button
        className="selection-popup-button copy-button"
        onClick={handleCopy}
        title="Copy to clipboard"
      >
        <span></span> Copy
      </button>
      <button
        className="selection-popup-button print-button"
        onClick={handlePrint}
        title="Print selected text"
      >
        Print
      </button>
    </div>
  );
}

export default SelectionPopup;
