import React, { useState, useEffect } from 'react';
import Login from './components/Login';
import Register from './components/Register';
import PunchInOut from './components/PunchInOut';
import AdminDashboard from './components/AdminDashboard';
import ProfileForm from './components/ProfileForm';
import SelectionPopup from './components/SelectionPopup';
import AlertModal from './components/AlertModal';
import { loadAppConfig, getApiBaseUrl, getApiHeaders } from './config/networkConfig';
import './styles/App.css';

function App() {
  const [user, setUser] = useState(null);
  const [showRegister, setShowRegister] = useState(false);
  const [showProfileForm, setShowProfileForm] = useState(false);
  const [loadingProfile, setLoadingProfile] = useState(false);
  const [existingProfile, setExistingProfile] = useState(null); // Store existing profile for face registration
  const [alertMessage, setAlertMessage] = useState(null); // Custom alert state
  const [configLoaded, setConfigLoaded] = useState(false);

  // Load app configuration (CompanyId) on startup
  useEffect(() => {
    loadAppConfig().then(() => {
      setConfigLoaded(true);
      console.log(' App configuration loaded');
    });
  }, []);

  // GLOBAL: Override alert() to use custom modal instead of native alert
  useEffect(() => {
    if (!window.__customAlertInstalled) {
      window.customAlert = (message) => {
        setAlertMessage(message);
      };
      // Override native alert to use custom modal
      window.alert = window.customAlert;
      window.__customAlertInstalled = true;
      console.log(' Custom alert modal installed - keyboard input will NOT break');
    }
  }, []);

  // Check if employee needs to create profile on first login
  useEffect(() => {
    if (user && user.role !== 'Admin') {
      checkProfileComplete();
    }
    // eslint-disable-next-line
  }, [user]);

  const checkProfileComplete = async () => {
    setLoadingProfile(true);
    try {
      // Check if profile exists
      const existsRes = await fetch(`${getApiBaseUrl()}/api/Profile/exists/${user.email}`, {
        headers: getApiHeaders()
      });
      const existsData = await existsRes.json();
      
      if (!existsData.exists) {
        // No profile - show profile form
        setExistingProfile(null);
        setShowProfileForm(true);
        setLoadingProfile(false);
        return;
      }

      // Profile exists - check if face is registered
      const profileRes = await fetch(`${getApiBaseUrl()}/api/Profile/${user.email}`, {
        headers: getApiHeaders()
      });
      if (profileRes.ok) {
        const profile = await profileRes.json();
        
        // Check if face is registered
        if (!profile.faceRegistered || !profile.faceDescriptor) {
          // Profile exists but no face - show profile form to register face
          setExistingProfile(profile); // Pass existing profile data
          setShowProfileForm(true);
          alert(' Face registration required! Please register your face to continue using the attendance system.');
        } else {
          // Profile complete with face - clear existing profile
          setExistingProfile(null);
        }
      }
    } catch (err) {
      console.error('Error checking profile:', err);
    } finally {
      setLoadingProfile(false);
    }
  };

  const handleLoginSuccess = (userData) => {
    console.log('Login successful, received user data:', userData);
    setUser(userData);
  };

  const handleLogout = () => {
    setUser(null);
    setShowRegister(false);
    setShowProfileForm(false);
    setExistingProfile(null);
    console.log('User logged out');
  };

  const handleProfileComplete = async () => {
    setShowProfileForm(false);
    setExistingProfile(null);
    // Force a state update to trigger PunchInOut re-render and profile refetch
    // This ensures ProfileCard shows immediately without requiring re-login
    setUser(prevUser => ({ ...prevUser, profileUpdated: Date.now() }));
  };

  if (loadingProfile) {
    return (
      <div className="app-container">
        <div className="app-header-container">
          <h1 className="app-header">PS Sheet</h1>
        </div>
        <div style={{ textAlign: 'center', padding: '2rem' }}>Checking profile...</div>
      </div>
    );
  }

  return (
    <div className="app-container">
      <div className="app-header-container">
        <h1 className="app-header">PS Sheet</h1>
      </div>

      {/* Profile Form Modal - Required for new employees on first login */}
      {user && user.role !== 'Admin' && showProfileForm && (
        <ProfileForm 
          userId={user.email}
          initialProfile={existingProfile} // Pass existing profile if user needs to add face
          onSave={handleProfileComplete}
          onCancel={() => { setUser(null); setShowProfileForm(false); setExistingProfile(null); }}
          onProfileComplete={handleProfileComplete}
        />
      )}

      {!user ? (
        showRegister ? (
          <Register onSwitchToLogin={() => setShowRegister(false)} />
        ) : (
          <Login
            onLoginSuccess={handleLoginSuccess}
            onSwitchToRegister={() => setShowRegister(true)}
          />
        )
      ) : user.role === 'Admin' ? (
        <div className="dashboard">
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '2rem' }}>
            <h2>Welcome, {user.name}!</h2>
            <button 
              onClick={handleLogout}
              className="logout-button"
              style={{
                padding: '0.6rem 1.5rem',
                backgroundColor: '#dc3545',
                color: 'white',
                border: 'none',
                borderRadius: '8px',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '600',
                boxShadow: '0 2px 8px rgba(220, 53, 69, 0.3)',
                transition: 'all 0.3s ease'
              }}
              onMouseOver={(e) => e.target.style.backgroundColor = '#c82333'}
              onMouseOut={(e) => e.target.style.backgroundColor = '#dc3545'}
            >
              Logout
            </button>
          </div>
          {/* Admin: No Profile Card */}
          <AdminDashboard token={user.token} user={user} />
        </div>
      ) : (
        <div className="dashboard">
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '2rem' }}>
            <h2>Welcome, {user.name}!</h2>
            <button 
              onClick={handleLogout}
              className="logout-button"
              style={{
                padding: '0.6rem 1.5rem',
                backgroundColor: '#dc3545',
                color: 'white',
                border: 'none',
                borderRadius: '8px',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '600',
                boxShadow: '0 2px 8px rgba(220, 53, 69, 0.3)',
                transition: 'all 0.3s ease'
              }}
              onMouseOver={(e) => e.target.style.backgroundColor = '#c82333'}
              onMouseOut={(e) => e.target.style.backgroundColor = '#dc3545'}
            >
              Logout
            </button>
          </div>
          {/* Employee: PunchInOut now has its own sidebar with profile card */}
          <PunchInOut user={user} />
        </div>
      )}
      
      {/* Global Selection Popup for all tables */}
      <SelectionPopup />
      
      {/* Global Custom Alert Modal - prevents keyboard input blocking */}
      <AlertModal message={alertMessage} onClose={() => setAlertMessage(null)} />
    </div>
  );
}

export default App;
