/**
 * Network Validation Configuration
 * MAC Address + IP validation for maximum security
 * 
 * IMPORTANT: Your office network details are configured below
 */

 
   //ALLOWED_ROUTER_MAC: '3C-64-CF-30-FC-2D', 
 //ALLOWED_GATEWAY_IP: '192.168.0.1',
export const NETWORK_CONFIG = {
  // ===== YOUR OFFICE ROUTER DETAILS =====
 ALLOWED_ROUTER_MAC: 'fc-9f-29-00-1f-f1',  
  ALLOWED_GATEWAY_IP: '192.168.1.1',
 

  // ===== VALIDATION MODE =====
  // 'mac-ip' - Both MAC and IP must match (MOST SECURE - RECOMMENDED)
  // 'mac' - Only MAC must match
  // 'ip' - Only IP must match  
  // 'disabled' - Disable network validation (NOT RECOMMENDED)
  VALIDATION_MODE: 'mac-ip',

  // ===== ERROR MESSAGES =====
  ERROR_MESSAGES: {
    NO_NETWORK: 'No network connection detected. Please connect to the office WiFi.',
    WRONG_MAC: 'Invalid network. You must be connected to the office router.',
    WRONG_IP: 'Invalid network. You must be connected to the office network.',
    WRONG_MAC_IP: 'Access denied. You must be connected to the office network.',
    API_NOT_AVAILABLE: 'Network validation not available. Please use the desktop application.',
  }
};

/**
 * Multi-Tenant Configuration
 * CompanyId is loaded from Electron app config at runtime or uses default for browser
 */
let APP_CONFIG = null;

/**
 * Load app configuration from Electron or use browser defaults
 * This includes CompanyId and API base URL
 */
export async function loadAppConfig() {
  if (APP_CONFIG) return APP_CONFIG;
  
  // Check if running in Electron
  if (window.electronAPI && window.electronAPI.getAppConfig) {
    try {
      APP_CONFIG = await window.electronAPI.getAppConfig();
      console.log('App config loaded (Electron):', APP_CONFIG);
      return APP_CONFIG;
    } catch (error) {
      console.error('Failed to load app config:', error);
    }
  }
  
  // Browser mode - use environment variables or defaults
  APP_CONFIG = {
    companyId: process.env.REACT_APP_COMPANY_ID || 'default',
    apiBaseUrl: process.env.REACT_APP_API_URL || 'http://localhost:5001',
    autoUpdate: { enabled: false }
  };
  console.log('App config loaded (Browser):', APP_CONFIG);
  return APP_CONFIG;
}

/**
 * Get CompanyId for multi-tenant support
 */
export function getCompanyId() {
  return APP_CONFIG?.companyId || 'default';
}

/**
 * Get API Base URL
 */
export function getApiBaseUrl() {
  return APP_CONFIG?.apiBaseUrl || 'http://localhost:5001';
}

/**
 * Create headers for API requests with CompanyId
 */
export function getApiHeaders(additionalHeaders = {}) {
  return {
    'Content-Type': 'application/json',
    'X-Company-Id': getCompanyId(),
    ...additionalHeaders
  };
}

/**
 * Validate network connection
 * Checks Router MAC Address and Gateway IP against allowed values
 */
export async function validateNetwork() {
  console.log('=== Network Validation Starting ===');
  console.log('Validation Mode:', NETWORK_CONFIG.VALIDATION_MODE);

  // Check if running in Electron (has network API)
  if (!window.electronAPI || !window.electronAPI.getNetworkInfo) {
    console.warn(' Network API not available - running in browser mode (network validation skipped)');
    return { 
      valid: true,  // Allow access in browser mode
      browserMode: true,
      networkInfo: { gatewayIp: '', routerMac: '' }
    };
  }

  // Skip validation if disabled
  if (NETWORK_CONFIG.VALIDATION_MODE === 'disabled') {
    console.log(' Network validation DISABLED - allowing access');
    return { valid: true };
  }

  try {
    // Get current network information
    const networkInfo = await window.electronAPI.getNetworkInfo();
    console.log('Current Network Info:', networkInfo);

    // Check for connection errors
    if (networkInfo.error) {
      console.error(' Network error:', networkInfo.error);
      return {
        valid: false,
        reason: NETWORK_CONFIG.ERROR_MESSAGES.NO_NETWORK,
        networkInfo
      };
    }

    // Validate based on selected mode
    switch (NETWORK_CONFIG.VALIDATION_MODE) {
      case 'mac-ip':
        // Validate both MAC and IP (Most Secure)
        const macValid = networkInfo.routerMac === NETWORK_CONFIG.ALLOWED_ROUTER_MAC;
        const ipValid = networkInfo.gatewayIp === NETWORK_CONFIG.ALLOWED_GATEWAY_IP;

        console.log('MAC Match:', macValid, `(Expected: ${NETWORK_CONFIG.ALLOWED_ROUTER_MAC}, Got: ${networkInfo.routerMac})`);
        console.log('IP Match:', ipValid, `(Expected: ${NETWORK_CONFIG.ALLOWED_GATEWAY_IP}, Got: ${networkInfo.gatewayIp})`);

        if (macValid && ipValid) {
          console.log(' Network validation PASSED (MAC + IP match)');
          return { valid: true, networkInfo };
        } else {
          console.error(' Network validation FAILED');
          return {
            valid: false,
            reason: NETWORK_CONFIG.ERROR_MESSAGES.WRONG_MAC_IP + 
              `\n\nExpected Router:\n` +
              ` MAC: ${NETWORK_CONFIG.ALLOWED_ROUTER_MAC}\n` +
              ` IP: ${NETWORK_CONFIG.ALLOWED_GATEWAY_IP}\n\n` +
              `Your Network:\n` +
              ` MAC: ${networkInfo.routerMac || 'Not detected'}\n` +
              ` IP: ${networkInfo.gatewayIp || 'Not detected'}`,
            networkInfo
          };
        }

      case 'mac':
        // Validate MAC only
        if (networkInfo.routerMac === NETWORK_CONFIG.ALLOWED_ROUTER_MAC) {
          console.log(' Network validation PASSED (MAC match)');
          return { valid: true, networkInfo };
        } else {
          console.error(' MAC address mismatch');
          return {
            valid: false,
            reason: NETWORK_CONFIG.ERROR_MESSAGES.WRONG_MAC +
              `\n\nExpected: ${NETWORK_CONFIG.ALLOWED_ROUTER_MAC}\n` +
              `Found: ${networkInfo.routerMac || 'Not detected'}`,
            networkInfo
          };
        }

      case 'ip':
        // Validate IP only
        if (networkInfo.gatewayIp === NETWORK_CONFIG.ALLOWED_GATEWAY_IP) {
          console.log(' Network validation PASSED (IP match)');
          return { valid: true, networkInfo };
        } else {
          console.error(' IP address mismatch');
          return {
            valid: false,
            reason: NETWORK_CONFIG.ERROR_MESSAGES.WRONG_IP +
              `\n\nExpected: ${NETWORK_CONFIG.ALLOWED_GATEWAY_IP}\n` +
              `Found: ${networkInfo.gatewayIp || 'Not detected'}`,
            networkInfo
          };
        }

      default:
        console.error(' Unknown validation mode:', NETWORK_CONFIG.VALIDATION_MODE);
        return { 
          valid: false, 
          reason: 'Invalid validation configuration' 
        };
    }
  } catch (error) {
    console.error(' Network validation error:', error);
    return {
      valid: false,
      reason: `Network check failed: ${error.message}`
    };
  }
}

/**
 * Helper function to get current network info (for debugging/setup)
 */
export async function getCurrentNetworkInfo() {
  if (!window.electronAPI || !window.electronAPI.getNetworkInfo) {
    console.log('Browser mode - network info not available');
    return { gatewayIp: 'Browser Mode', routerMac: 'Browser Mode', browserMode: true };
  }
  try {
    return await window.electronAPI.getNetworkInfo();
  } catch (error) {
    console.error('Error getting network info:', error);
    return null;
  }
}
