import React, { useState, useEffect } from 'react';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import '../styles/NetworkManagement.css';

function NetworkManagement() {
  const [isVerified, setIsVerified] = useState(false);
  const [passcode, setPasscode] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [showPasscode, setShowPasscode] = useState(false);
  
  // Network settings
  const [routerMac, setRouterMac] = useState('');
  const [gatewayIp, setGatewayIp] = useState('');
  const [validationMode, setValidationMode] = useState('mac-ip');
  const [currentNetwork, setCurrentNetwork] = useState(null);
  const [saveSuccess, setSaveSuccess] = useState('');

  // Load current settings after verification
  useEffect(() => {
    if (isVerified) {
      loadCurrentSettings();
      detectCurrentNetwork();
    }
  }, [isVerified]);

  const loadCurrentSettings = async () => {
    try {
      const response = await fetch(`${getApiBaseUrl()}/api/settings/network`, {
        headers: getApiHeaders()
      });
      const data = await response.json();
      setRouterMac(data.routerMac);
      setGatewayIp(data.gatewayIp);
      setValidationMode(data.validationMode);
    } catch (err) {
      console.error('Error loading settings:', err);
    }
  };

  const detectCurrentNetwork = async () => {
    if (window.electronAPI && window.electronAPI.getNetworkInfo) {
      try {
        const networkInfo = await window.electronAPI.getNetworkInfo();
        setCurrentNetwork(networkInfo);
      } catch (err) {
        console.error('Error detecting network:', err);
      }
    }
  };

  const handleVerifyPasscode = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/settings/network/verify`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({ passcode }),
      });

      const data = await response.json();

      if (response.ok && data.verified) {
        setIsVerified(true);
        setError('');
      } else {
        setError(data.message || 'Invalid passcode');
        setPasscode('');
      }
    } catch (err) {
      setError('Error verifying passcode: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleSaveSettings = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setSaveSuccess('');

    // Validate inputs
    if (!routerMac || !gatewayIp) {
      setError('Both Router MAC and Gateway IP are required');
      setLoading(false);
      return;
    }

    // Validate MAC format (XX-XX-XX-XX-XX-XX)
    const macRegex = /^([0-9A-Fa-f]{2}[:-]){5}([0-9A-Fa-f]{2})$/;
    if (!macRegex.test(routerMac)) {
      setError('Invalid MAC address format. Use format: XX-XX-XX-XX-XX-XX');
      setLoading(false);
      return;
    }

    // Validate IP format
    const ipRegex = /^(\d{1,3}\.){3}\d{1,3}$/;
    if (!ipRegex.test(gatewayIp)) {
      setError('Invalid IP address format. Use format: XXX.XXX.XXX.XXX');
      setLoading(false);
      return;
    }

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/settings/network`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({
          passcode,
          routerMac: routerMac.toUpperCase(),
          gatewayIp,
          validationMode,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        setSaveSuccess(' Network settings updated successfully! Please rebuild the app for changes to take effect.');
        setError('');
      } else {
        setError(data.message || 'Error saving settings');
      }
    } catch (err) {
      setError('Error saving settings: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const useCurrentNetwork = () => {
    if (currentNetwork && !currentNetwork.error) {
      setRouterMac(currentNetwork.routerMac || '');
      setGatewayIp(currentNetwork.gatewayIp || '');
    }
  };

  if (!isVerified) {
    return (
      <div className="network-management">
        <div className="verification-container">
          <div className="verification-card">
            <h2> Network Management</h2>
            <p className="warning-text">
               This section is password protected. Enter the security passcode to continue.
            </p>

            <form onSubmit={handleVerifyPasscode}>
              <div className="form-group">
                <label>Security Passcode</label>
                <div className="password-input-wrapper">
                  <input
                    type={showPasscode ? "text" : "password"}
                    value={passcode}
                    onChange={(e) => setPasscode(e.target.value)}
                    placeholder="Enter passcode"
                    className={error ? 'input-error' : ''}
                    required
                  />
                  <button
                    type="button"
                    className="password-toggle"
                    onClick={() => setShowPasscode(!showPasscode)}
                    title={showPasscode ? "Hide passcode" : "Show passcode"}
                  >
                    {showPasscode ? (
                      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M3.98 8.223A10.477 10.477 0 0 0 1.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.451 10.451 0 0 1 12 4.5c4.756 0 8.773 3.162 10.065 7.498a10.522 10.522 0 0 1-4.293 5.774M6.228 6.228 3 3m3.228 3.228 3.65 3.65m7.894 7.894L21 21m-3.228-3.228-3.65-3.65m0 0a3 3 0 1 0-4.243-4.243m4.242 4.242L9.88 9.88" />
                      </svg>
                    ) : (
                      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M2.036 12.322a1.012 1.012 0 0 1 0-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178Z" />
                        <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
                      </svg>
                    )}
                  </button>
                </div>
                {error && <p className="error-message">{error}</p>}
              </div>

              <button type="submit" className="btn-verify" disabled={loading}>
                {loading ? 'Verifying...' : 'Verify Passcode'}
              </button>
            </form>

            <div className="hint-box">
              <p>Contact system administrator for the passcode</p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="network-management">
      <div className="settings-container">
        <h2> Network Management</h2>
        <p className="subtitle">Configure allowed network for attendance validation</p>

        {saveSuccess && (
          <div className="success-message">
            {saveSuccess}
          </div>
        )}

        {error && (
          <div className="error-message">
            {error}
          </div>
        )}

        {/* Current Network Detection */}
        {currentNetwork && !currentNetwork.error && (
          <div className="current-network-box">
            <h3> Current Network Detected</h3>
            <div className="network-info">
              <div className="info-row">
                <span className="label">Router MAC:</span>
                <span className="value">{currentNetwork.routerMac || 'N/A'}</span>
              </div>
              <div className="info-row">
                <span className="label">Gateway IP:</span>
                <span className="value">{currentNetwork.gatewayIp || 'N/A'}</span>
              </div>
              <div className="info-row">
                <span className="label">Your IP:</span>
                <span className="value">{currentNetwork.localIp || 'N/A'}</span>
              </div>
            </div>
            <button className="btn-use-current" onClick={useCurrentNetwork}>
              Use Current Network
            </button>
          </div>
        )}

        {/* Settings Form */}
        <form onSubmit={handleSaveSettings} className="settings-form">
          <div className="form-group">
            <label>Router MAC Address *</label>
            <input
              type="text"
              value={routerMac}
              onChange={(e) => setRouterMac(e.target.value.toUpperCase())}
              placeholder="XX-XX-XX-XX-XX-XX"
              required
            />
            <small>Format: XX-XX-XX-XX-XX-XX (hardware address of your office router)</small>
          </div>

          <div className="form-group">
            <label>Gateway IP Address *</label>
            <input
              type="text"
              value={gatewayIp}
              onChange={(e) => setGatewayIp(e.target.value)}
              placeholder="192.168.0.1"
              required
            />
            <small>Format: XXX.XXX.XXX.XXX (IP address of your office router)</small>
          </div>

          <div className="form-group">
            <label>Validation Mode</label>
            <select
              value={validationMode}
              onChange={(e) => setValidationMode(e.target.value)}
            >
              <option value="mac-ip">MAC + IP (Most Secure - Recommended)</option>
              <option value="mac">MAC Only</option>
              <option value="ip">IP Only</option>
              <option value="disabled">Disabled (Not Recommended)</option>
            </select>
            <small>Select how to validate network connection</small>
          </div>

          <div className="button-group">
            <button type="submit" className="btn-save" disabled={loading}>
              {loading ? 'Saving...' : 'Save Settings'}
            </button>
            <button
              type="button"
              className="btn-cancel"
              onClick={() => loadCurrentSettings()}
            >
              Reset
            </button>
          </div>
        </form>

        {/* Instructions */}
        <div className="instructions-box">
          <h3> How to find your network details:</h3>
          <ol>
            <li>Open Command Prompt (cmd) on a computer connected to your office WiFi</li>
            <li>Type: <code>ipconfig /all</code></li>
            <li>Find "Default Gateway" - this is your Gateway IP</li>
            <li>Type: <code>arp -a [your-gateway-ip]</code></li>
            <li>The first entry shows your Router MAC address</li>
          </ol>
        </div>

        <div className="warning-box">
          <p> <strong>Note:</strong> Changes take effect immediately. Employees just need to logout and login again to use the new network settings.</p>
        </div>
      </div>
    </div>
  );
}

export default NetworkManagement;
