// Industrial-standard validation utilities

// Name validation - only letters, spaces, hyphens, apostrophes
export const validateName = (name) => {
  if (!name || name.trim().length === 0) {
    return { valid: false, message: 'Name is required' };
  }
  
  if (name.trim().length < 2) {
    return { valid: false, message: 'Name must be at least 2 characters' };
  }
  
  if (name.trim().length > 50) {
    return { valid: false, message: 'Name must not exceed 50 characters' };
  }
  
  // Only letters, spaces, hyphens, apostrophes allowed
  const nameRegex = /^[a-zA-Z\s'-]+$/;
  if (!nameRegex.test(name)) {
    return { valid: false, message: 'Name can only contain letters, spaces, hyphens, and apostrophes' };
  }
  
  return { valid: true, message: '' };
};

// Email validation - RFC 5322 compliant
export const validateEmail = (email) => {
  if (!email || email.trim().length === 0) {
    return { valid: false, message: 'Email is required' };
  }
  
  // Email must contain @ and .
  if (!email.includes('@') || !email.includes('.')) {
    return { valid: false, message: 'Email must contain @ and .' };
  }
  
  // Standard email regex
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (!emailRegex.test(email)) {
    return { valid: false, message: 'Please enter a valid email address' };
  }
  
  // Check for valid domain
  const parts = email.split('@');
  if (parts.length !== 2 || parts[1].indexOf('.') === -1) {
    return { valid: false, message: 'Email must have a valid domain' };
  }
  
  return { valid: true, message: '' };
};

// Password validation - minimum 8 chars, 1 number, 1 special char
export const validatePassword = (password) => {
  if (!password || password.length === 0) {
    return { valid: false, message: 'Password is required' };
  }
  
  if (password.length < 8) {
    return { valid: false, message: 'Password must be at least 8 characters long' };
  }
  
  if (password.length > 128) {
    return { valid: false, message: 'Password must not exceed 128 characters' };
  }
  
  // Must contain at least one number
  if (!/\d/.test(password)) {
    return { valid: false, message: 'Password must contain at least one number' };
  }
  
  // Must contain at least one special character
  const specialCharRegex = /[!@#$%^&*(),.?":{}|<>_\-+=\[\]\\\/;'`~]/;
  if (!specialCharRegex.test(password)) {
    return { valid: false, message: 'Password must contain at least one special character (!@#$%^&*...)' };
  }
  
  // Must contain at least one letter
  if (!/[a-zA-Z]/.test(password)) {
    return { valid: false, message: 'Password must contain at least one letter' };
  }
  
  return { valid: true, message: '' };
};

// Phone number validation (flexible international format)
export const validatePhone = (phone) => {
  if (!phone || phone.trim().length === 0) {
    return { valid: false, message: 'Phone number is required' };
  }
  
  // Remove common formatting characters
  const cleaned = phone.replace(/[\s\-().+]/g, '');
  
  // Must be between 10-15 digits
  if (cleaned.length < 10 || cleaned.length > 15) {
    return { valid: false, message: 'Phone number must be between 10-15 digits' };
  }
  
  // Must contain only digits after cleanup
  if (!/^\d+$/.test(cleaned)) {
    return { valid: false, message: 'Phone number can only contain digits and formatting characters' };
  }
  
  return { valid: true, message: '' };
};

// Date validation (must not be in future for DOB)
export const validateDateOfBirth = (date) => {
  if (!date) {
    return { valid: false, message: 'Date of birth is required' };
  }
  
  const selectedDate = new Date(date);
  const today = new Date();
  today.setHours(0, 0, 0, 0);
  
  if (selectedDate > today) {
    return { valid: false, message: 'Date of birth cannot be in the future' };
  }
  
  // Check minimum age (e.g., 16 years)
  const minAge = 16;
  const minDate = new Date();
  minDate.setFullYear(minDate.getFullYear() - minAge);
  
  if (selectedDate > minDate) {
    return { valid: false, message: `Minimum age is ${minAge} years` };
  }
  
  // Check maximum age (e.g., 100 years)
  const maxAge = 100;
  const maxDate = new Date();
  maxDate.setFullYear(maxDate.getFullYear() - maxAge);
  
  if (selectedDate < maxDate) {
    return { valid: false, message: 'Please enter a valid date of birth' };
  }
  
  return { valid: true, message: '' };
};

// Address validation
export const validateAddress = (address) => {
  if (!address || address.trim().length === 0) {
    return { valid: false, message: 'Address is required' };
  }
  
  if (address.trim().length < 10) {
    return { valid: false, message: 'Address must be at least 10 characters' };
  }
  
  if (address.trim().length > 200) {
    return { valid: false, message: 'Address must not exceed 200 characters' };
  }
  
  return { valid: true, message: '' };
};

// Generic text validation (for work logs, notes, etc.)
export const validateText = (text, minLength = 1, maxLength = 1000, fieldName = 'Text') => {
  if (!text || text.trim().length === 0) {
    return { valid: false, message: `${fieldName} is required` };
  }
  
  if (text.trim().length < minLength) {
    return { valid: false, message: `${fieldName} must be at least ${minLength} characters` };
  }
  
  if (text.trim().length > maxLength) {
    return { valid: false, message: `${fieldName} must not exceed ${maxLength} characters` };
  }
  
  return { valid: true, message: '' };
};

// Password strength indicator
export const getPasswordStrength = (password) => {
  if (!password) return { strength: 0, label: 'No password', color: '#ccc' };
  
  let strength = 0;
  
  // Length
  if (password.length >= 8) strength += 20;
  if (password.length >= 12) strength += 10;
  if (password.length >= 16) strength += 10;
  
  // Contains lowercase
  if (/[a-z]/.test(password)) strength += 15;
  
  // Contains uppercase
  if (/[A-Z]/.test(password)) strength += 15;
  
  // Contains numbers
  if (/\d/.test(password)) strength += 15;
  
  // Contains special characters
  if (/[!@#$%^&*(),.?":{}|<>_\-+=\[\]\\\/;'`~]/.test(password)) strength += 15;
  
  // Determine label and color
  if (strength < 40) {
    return { strength, label: 'Weak', color: '#e74c3c' };
  } else if (strength < 70) {
    return { strength, label: 'Medium', color: '#f39c12' };
  } else {
    return { strength, label: 'Strong', color: '#2ecc71' };
  }
};
