/**
 * Time Utilities for IST Display
 * 
 * DATABASE STORAGE: All timestamps are stored in UTC format
 * UI DISPLAY: All timestamps are displayed in IST (Indian Standard Time)
 * 
 * IST = UTC + 5 hours 30 minutes
 */

// Force module load
console.log(' IST TIME UTILS LOADED - GLOBAL +5:30 CONVERSION ACTIVE');

/**
 * Convert timestamp from database to IST for display
 * GLOBAL IST CONVERSION: Adds +5:30 hours to EVERY timestamp for display
 * @param {string} timestamp - ISO 8601 timestamp from database
 * @param {string} source - Where this is called from (e.g., "AdminDashboard-AttendanceTable")
 * @returns {string} Formatted IST time (e.g., "01-12-2024 16:00:00")
 */
export function formatUTCtoIST(timestamp, source = 'Unknown') {
  console.log('\n');
  console.log(' [formatUTCtoIST] TIMEZONE CONVERSION STARTED');
  console.log('');
  console.log(' SOURCE:', source);
  
  if (!timestamp) {
    console.log(' INPUT: NULL/EMPTY');
    console.log(' OUTPUT: "-"');
    console.log('\n');
    return '-';
  }

  try {
    const str = timestamp.toString();
    console.log(' INPUT FROM DATABASE (UTC):', str);
    
    // Parse timestamp as-is (treat as UTC)
    let utcString = str;
    if (!str.endsWith('Z') && !str.includes('+')) {
      utcString = str + 'Z';
      console.log(' NORMALIZED:', utcString);
    }
    
    const date = new Date(utcString);
    console.log(' JAVASCRIPT DATE OBJECT:', date.toISOString());
    
    if (isNaN(date.getTime())) {
      console.error(' INVALID DATE:', timestamp);
      console.log('\n');
      return '-';
    }

    // FORCE ADD +5:30 hours for IST display
    const IST_OFFSET = 5.5 * 60 * 60 * 1000;
    const istMillis = date.getTime() + IST_OFFSET;
    const istDate = new Date(istMillis);
    
    console.log(' CONVERSION PROCESS:');
    console.log('    UTC Milliseconds:', date.getTime());
    console.log('    IST Offset: +5:30 hours (' + IST_OFFSET + 'ms)');
    console.log('    IST Milliseconds:', istMillis);
    console.log('    IST Date Object:', istDate.toISOString());

    // Format as DD-MM-YYYY HH:MM:SS
    const year = istDate.getUTCFullYear();
    const month = String(istDate.getUTCMonth() + 1).padStart(2, '0');
    const day = String(istDate.getUTCDate()).padStart(2, '0');
    const hour = String(istDate.getUTCHours()).padStart(2, '0');
    const minute = String(istDate.getUTCMinutes()).padStart(2, '0');
    const second = String(istDate.getUTCSeconds()).padStart(2, '0');

    const result = `${day}-${month}-${year} ${hour}:${minute}:${second}`;
    
    console.log(' FORMATTED FOR UI DISPLAY:', result);
    console.log(' THIS WILL SHOW ON SCREEN');
    console.log('\n');
    
    return result;
  } catch (error) {
    console.error(' ERROR IN CONVERSION:', error, timestamp);
    console.log('\n');
    return '-';
  }
}

/**
 * Convert IST time from user input to UTC for database storage
 * User enters IST time, we subtract 5:30 to store as UTC
 * @param {string} istDateTime - datetime-local input (e.g., "2024-12-01T16:00")
 * @param {string} source - Where this is called from (e.g., "AdminDashboard-AddAttendance")
 * @returns {string} UTC ISO string for database (e.g., "2024-12-01T10:30:00.000Z")
 */
export function convertISTtoUTC(istDateTime, source = 'Unknown') {
  console.log('\n');
  console.log(' [convertISTtoUTC] IST  UTC CONVERSION FOR DATABASE');
  console.log('');
  console.log(' SOURCE:', source);
  
  if (!istDateTime) {
    console.log(' USER INPUT: NULL/EMPTY');
    console.log('\n');
    return null;
  }

  try {
    console.log(' USER INPUT (IST):', istDateTime);
    
    // Parse user input (they enter IST time)
    const [datePart, timePart] = istDateTime.split('T');
    const [year, month, day] = datePart.split('-').map(Number);
    const [hour, minute] = timePart.split(':').map(Number);
    
    console.log(' PARSED COMPONENTS:');
    console.log('    Year:', year);
    console.log('    Month:', month);
    console.log('    Day:', day);
    console.log('    Hour:', hour);
    console.log('    Minute:', minute);
    
    // Create date from parts
    const istMillis = Date.UTC(year, month - 1, day, hour, minute, 0);
    console.log(' IST Milliseconds:', istMillis);
    
    // SUBTRACT 5:30 to convert IST to UTC for storage
    const IST_OFFSET = 5.5 * 60 * 60 * 1000;
    const utcMillis = istMillis - IST_OFFSET;
    
    console.log(' CONVERSION PROCESS:');
    console.log('    IST Offset: -5:30 hours (' + IST_OFFSET + 'ms)');
    console.log('    UTC Milliseconds:', utcMillis);
    console.log('    Offset Subtracted');
    
    const result = new Date(utcMillis).toISOString();
    
    console.log(' OUTPUT FOR DATABASE (UTC):', result);
    console.log(' READY TO SAVE TO POSTGRESQL');
    console.log('\n');
    
    return result;
  } catch (error) {
    console.error(' ERROR IN CONVERSION:', error, istDateTime);
    console.log('\n');
    return null;
  }
}

/**
 * Get current IST timestamp for display
 * @returns {string} Current IST time formatted as "01-12-2024 16:00:00"
 */
export function getCurrentISTTime() {
  const now = new Date();
  
  const istFormatter = new Intl.DateTimeFormat('en-IN', {
    timeZone: 'Asia/Kolkata',
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: false
  });

  const parts = istFormatter.formatToParts(now);
  const dateObj = {};
  parts.forEach(({ type, value }) => {
    dateObj[type] = value;
  });

  return `${dateObj.day}-${dateObj.month}-${dateObj.year} ${dateObj.hour}:${dateObj.minute}:${dateObj.second}`;
}

/**
 * Format date only (no time)
 * @param {string} utcTimestamp - UTC timestamp from database
 * @returns {string} Date in DD-MM-YYYY format
 */
export function formatDateOnly(utcTimestamp) {
  if (!utcTimestamp) return '-';

  try {
    const date = new Date(utcTimestamp);
    
    if (isNaN(date.getTime())) return '-';

    const istFormatter = new Intl.DateTimeFormat('en-IN', {
      timeZone: 'Asia/Kolkata',
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    });

    const parts = istFormatter.formatToParts(date);
    const dateObj = {};
    parts.forEach(({ type, value }) => {
      dateObj[type] = value;
    });

    return `${dateObj.day}-${dateObj.month}-${dateObj.year}`;
  } catch (error) {
    console.error('Error formatting date:', error);
    return '-';
  }
}

/**
 * Format time only (no date) - ADDS +5:30 hours for IST
 * @param {string} utcTimestamp - UTC timestamp from database
 * @param {string} source - Where this is called from
 * @returns {string} Time in HH:MM:SS format
 */
export function formatTimeOnly(utcTimestamp, source = 'Unknown') {
  console.log('\n');
  console.log(' [formatTimeOnly] TIME-ONLY CONVERSION');
  console.log('');
  console.log(' SOURCE:', source);
  
  if (!utcTimestamp) {
    console.log(' INPUT: NULL');
    console.log('\n');
    return '-';
  }

  try {
    let utcString = utcTimestamp.toString();
    console.log(' INPUT (UTC):', utcString);
    
    if (!utcString.endsWith('Z') && !utcString.includes('+')) {
      utcString += 'Z';
      console.log(' NORMALIZED:', utcString);
    }
    
    const date = new Date(utcString);
    if (isNaN(date.getTime())) {
      console.log(' INVALID DATE');
      console.log('\n');
      return '-';
    }

    // ADD +5:30 hours for IST
    const IST_OFFSET = 5.5 * 60 * 60 * 1000;
    const istDate = new Date(date.getTime() + IST_OFFSET);
    console.log(' ADDED IST OFFSET: +5:30 hours');

    const hour = String(istDate.getUTCHours()).padStart(2, '0');
    const minute = String(istDate.getUTCMinutes()).padStart(2, '0');
    const second = String(istDate.getUTCSeconds()).padStart(2, '0');

    const result = `${hour}:${minute}:${second}`;
    console.log(' OUTPUT FOR UI:', result);
    console.log('\n');
    
    return result;
  } catch (error) {
    console.error(' ERROR:', error);
    console.log('\n');
    return '-';
  }
}

/**
 * Get datetime-local input value for IST
 * @param {string|null} utcTimestamp - Optional UTC timestamp, defaults to current time
 * @param {string} source - Where this is called from
 * @returns {string} Format: "2024-12-01T16:00" for datetime-local input
 */
export function getDateTimeLocalValue(utcTimestamp = null, source = 'Unknown') {
  console.log('\n');
  console.log(' [getDateTimeLocalValue] PREPARE INPUT FIELD VALUE');
  console.log('');
  console.log(' SOURCE:', source);
  
  try {
    const date = utcTimestamp ? new Date(utcTimestamp) : new Date();
    console.log(' CURRENT/INPUT TIME:', date.toISOString());
    
    if (isNaN(date.getTime())) {
      console.error(' INVALID DATE');
      console.log('\n');
      return '';
    }

    // Get IST timestamp components
    const istFormatter = new Intl.DateTimeFormat('en-IN', {
      timeZone: 'Asia/Kolkata',
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      hour12: false
    });

    const parts = istFormatter.formatToParts(date);
    const dateObj = {};
    parts.forEach(({ type, value }) => {
      dateObj[type] = value;
    });
    
    console.log(' IST COMPONENTS:');
    console.log('    Year:', dateObj.year);
    console.log('    Month:', dateObj.month);
    console.log('    Day:', dateObj.day);
    console.log('    Hour:', dateObj.hour);
    console.log('    Minute:', dateObj.minute);

    // Return in YYYY-MM-DDTHH:MM format for datetime-local input
    const result = `${dateObj.year}-${dateObj.month}-${dateObj.day}T${dateObj.hour}:${dateObj.minute}`;
    console.log(' INPUT FIELD VALUE:', result);
    console.log(' PURPOSE: Pre-fill datetime-local input');
    console.log('\n');
    
    return result;
  } catch (error) {
    console.error(' ERROR:', error);
    console.log('\n');
    return '';
  }
}

/**
 * Debug function to log timezone conversion
 */
export function debugTimeConversion(utcTimestamp) {
  console.log('=== TIME CONVERSION DEBUG ===');
  console.log('Input (UTC):', utcTimestamp);
  console.log('Parsed Date:', new Date(utcTimestamp));
  console.log('IST Display:', formatUTCtoIST(utcTimestamp));
  console.log('Date Only:', formatDateOnly(utcTimestamp));
  console.log('Time Only:', formatTimeOnly(utcTimestamp));
  console.log('============================');
}
