import React, { useState, useEffect } from 'react';
import '../styles/EmployeeManagement.css';
import '../styles/TablePagination.css';
import { exportToCSV, printTable, paginateData, getTotalPages, showLoadingCursor, hideLoadingCursor } from '../utils/helpers';
import { validateEmail, validateName, validatePassword } from '../utils/validation';
import { getApiBaseUrl, getApiHeaders, loadAppConfig } from '../config/networkConfig';
import EmployeeDetailsView from './EmployeeDetailsView';
import useSortableData, { SortableHeader } from '../hooks/useSortableData';

function EmployeeManagement({ token }) {
  const [employees, setEmployees] = useState([]);
  const [unapprovedEmployees, setUnapprovedEmployees] = useState([]);
  const [form, setForm] = useState({ name: '', emailUsername: '', email: '', role: 'Employee', password: '', isApproved: false });
  const [companyDomain, setCompanyDomain] = useState('company.com');
  const [editForm, setEditForm] = useState({ name: '', email: '', role: 'Employee', password: '', isApproved: false });
  const [editingId, setEditingId] = useState(null);
  const [error, setError] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedEmployeeEmail, setSelectedEmployeeEmail] = useState(null);
  const [showPassword, setShowPassword] = useState(false);

  // Inline validation errors
  const [formErrors, setFormErrors] = useState({ name: '', email: '', password: '' });
  const [editFormErrors, setEditFormErrors] = useState({ name: '', email: '', password: '' });

  useEffect(() => {
    loadAppConfig().then(config => {
      console.log('EmployeeManagement loaded config:', config);
      const companyName = config.companyName || 'Company';
      // Extract domain dynamically: "Mark Audio" -> "markaudio", "Revit" -> "revit"
      const domain = companyName.toLowerCase().replace(/\s+/g, '');
      setCompanyDomain(`${domain}.com`);
    });
  }, []);

  useEffect(() => {
    fetchEmployees();
    fetchUnapprovedEmployees();
    // eslint-disable-next-line
  }, []);

  const fetchEmployees = () => {
    fetch(`${getApiBaseUrl()}/api/employees`, {
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` }
    })
      .then(res => res.json())
      .then(setEmployees)
      .catch(err => setError(err.message));
  };

  const fetchUnapprovedEmployees = () => {
    fetch(`${getApiBaseUrl()}/api/employees/unapproved`, {
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` }
    })
      .then(res => res.json())
      .then(setUnapprovedEmployees)
      .catch(err => setError(err.message));
  };

  const isFormValid = (f) => {
    // Validate name
    const nameValidation = validateName(f.name);
    if (!nameValidation.valid) {
      alert(`Name Error: ${nameValidation.message}`);
      return false;
    }

    // Validate email
    const emailValidation = validateEmail(f.email);
    if (!emailValidation.valid) {
      alert(`Email Error: ${emailValidation.message}`);
      return false;
    }

    // Validate password (only for new employee, not for edit)
    if (editingId === null && f.password.trim() !== '') {
      const passwordValidation = validatePassword(f.password);
      if (!passwordValidation.valid) {
        alert(`Password Error: ${passwordValidation.message}`);
        return false;
      }
    }

    // Password required for new employee
    if (editingId === null && f.password.trim() === '') {
      alert('Password is required for new employee');
      return false;
    }

    return true;
  };

  // Silent validation for button disabled state (no alerts)
  const isFormValidSilent = (f) => {
    if (!f || !f.name || !f.emailUsername) return false;
    
    // Construct full email from username
    const fullEmail = f.emailUsername ? `${f.emailUsername}@${companyDomain}` : '';
    
    const nameValidation = validateName(f.name);
    const emailValidation = validateEmail(fullEmail);
    
    if (!nameValidation.valid || !emailValidation.valid) return false;
    
    // For new employee, password is required
    if (editingId === null && !f.password.trim()) return false;
    
    // If password exists, validate it
    if (editingId === null && f.password.trim()) {
      const passwordValidation = validatePassword(f.password);
      if (!passwordValidation.valid) return false;
    }
    
    return true;
  };

  // Validate individual fields on blur
  const validateField = (fieldName, value, isEditForm = false) => {
    let errorMessage = '';
    
    switch (fieldName) {
      case 'name':
        const nameValidation = validateName(value);
        if (!nameValidation.valid) {
          errorMessage = nameValidation.message;
        }
        break;
      case 'email':
        const emailValidation = validateEmail(value);
        if (!emailValidation.valid) {
          errorMessage = emailValidation.message;
        }
        break;
      case 'password':
        // Only validate password if it's provided
        if (value.trim()) {
          const passwordValidation = validatePassword(value);
          if (!passwordValidation.valid) {
            errorMessage = passwordValidation.message;
          }
        } else if (!isEditForm) {
          // Password required for new employee only
          errorMessage = 'Password is required';
        }
        break;
      default:
        break;
    }
    
    // Update error state
    if (isEditForm) {
      setEditFormErrors(prev => ({ ...prev, [fieldName]: errorMessage }));
    } else {
      setFormErrors(prev => ({ ...prev, [fieldName]: errorMessage }));
    }
  };

  const handleAdd = () => {
    // Construct full email from username
    const fullEmail = form.emailUsername ? `${form.emailUsername}@${companyDomain}` : '';
    const formWithEmail = { ...form, email: fullEmail };
    
    if (!isFormValid(formWithEmail)) {
      return;
    }

    showLoadingCursor();
    fetch(`${getApiBaseUrl()}/api/employees`, {
      method: 'POST',
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
      body: JSON.stringify(formWithEmail),
    })
      .then(res => {
        if (!res.ok) throw new Error('Existing employee or invalid data');
        return res.json();
      })
      .then((newEmployee) => {
        alert(`${newEmployee.email} added successfully!`);
        fetchEmployees();
        fetchUnapprovedEmployees();
        setForm({ name: '', emailUsername: '', email: '', role: 'Employee', password: '', isApproved: false });
        setFormErrors({ name: '', email: '', password: '' }); // Clear errors
        setError('');
      })
      .catch(err => setError(err.message))
      .finally(() => hideLoadingCursor());
  };

  const startEdit = (emp) => {
    setEditingId(emp.id);
    setEditForm({
      name: emp.name,
      email: emp.email,
      role: emp.role,
      password: '', // never preload password
      isApproved: emp.isApproved || false,
    });
    setError('');
  };

  const cancelEdit = () => {
    setEditingId(null);
    setEditForm({ name: '', email: '', role: 'Employee', password: '', isApproved: false });
    setError('');
  };

  const handleUpdate = (id) => {
    if (!isFormValid(editForm)) {
      alert('Please fill all fields with valid values.');
      return;
    }

    if (!window.confirm("Are you sure you want to save changes?")) {
      return; // Cancelled
    }

    showLoadingCursor();
    
    // Only include password if it's filled, otherwise omit it
    const payload = { ...editForm };
    if (!payload.password || payload.password.trim() === '') {
      // Don't send password field at all if empty
      const { password, ...payloadWithoutPassword } = payload;
      
      fetch(`${getApiBaseUrl()}/api/employees/${id}`, {
        method: 'PUT',
        headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
        body: JSON.stringify(payloadWithoutPassword),
      })
        .then(res => {
          if (!res.ok) throw new Error('Update failed');
          fetchEmployees();
          fetchUnapprovedEmployees();
          cancelEdit();
        })
        .catch(err => {
          console.error(err);
          alert('Failed to update employee: ' + err.message);
        })
        .finally(() => hideLoadingCursor());
    } else {
      // Send with password if provided
      fetch(`${getApiBaseUrl()}/api/employees/${id}`, {
        method: 'PUT',
        headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
        body: JSON.stringify(payload),
      })
        .then(res => {
          if (!res.ok) throw new Error('Update failed');
          fetchEmployees();
          fetchUnapprovedEmployees();
          cancelEdit();
        })
        .catch(err => {
          console.error(err);
          alert('Failed to update employee: ' + err.message);
        })
        .finally(() => hideLoadingCursor());
    }
  };

  const handleDelete = (id) => {
    // Check both approved and unapproved employees
    let employee = employees.find(emp => emp.id === id);
    if (!employee) {
      employee = unapprovedEmployees.find(emp => emp.id === id);
    }
    const employeeName = employee ? employee.name : 'this employee';
    
    if (!window.confirm(`Are you sure you want to delete ${employeeName}?\n\nThis action cannot be undone.`)) {
      return; // Cancelled
    }

    showLoadingCursor();
    fetch(`${getApiBaseUrl()}/api/employees/${id}`, {
      method: 'DELETE',
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then(res => {
        if (!res.ok) throw new Error('Delete failed');
        alert(` Employee "${employeeName}" has been deleted successfully!`);
        fetchEmployees();
        fetchUnapprovedEmployees();
      })
      .catch(err => alert(' Failed to delete employee: ' + err.message))
      .finally(() => hideLoadingCursor());
  };

  const handleApprove = (id) => {
    const employee = unapprovedEmployees.find(emp => emp.id === id);
    const employeeName = employee ? employee.name : 'this employee';
    
    if (!window.confirm(`Approve ${employeeName}'s account?\n\nThey will be able to log in after approval.`)) {
      return;
    }

    showLoadingCursor();
    fetch(`${getApiBaseUrl()}/api/employees/approve/${id}`, {
      method: 'POST',
      headers: { ...getApiHeaders(), Authorization: `Bearer ${token}` },
    })
      .then(res => {
        if (!res.ok) throw new Error('Approval failed');
        alert(` Employee "${employeeName}" has been approved successfully!`);
        fetchEmployees();
        fetchUnapprovedEmployees();
      })
      .catch(err => alert(' Approval failed: ' + err.message))
      .finally(() => hideLoadingCursor());
  };

  // Filter and pagination
  const filteredEmployees = employees.filter(emp =>
    emp.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    emp.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    emp.role.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Sorting
  const { items: sortedEmployees, requestSort, sortConfig } = useSortableData(filteredEmployees);
  const { items: sortedUnapprovedEmployees, requestSort: requestSortUnapproved, sortConfig: sortConfigUnapproved } = useSortableData(unapprovedEmployees);

  const paginatedEmployees = paginateData(sortedEmployees, currentPage, itemsPerPage);
  const totalPages = getTotalPages(sortedEmployees.length, itemsPerPage);

  const handlePageChange = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  const handleExport = () => {
    const exportData = filteredEmployees.map(emp => ({
      'Name': emp.name,
      'Email': emp.email,
      'Role': emp.role,
      'Approved': emp.isApproved ? 'Yes' : 'No'
    }));
    
    // Build filter info object
    const filterInfo = {
      searchText: searchTerm.trim() || null,
      totalRecords: employees.length,
      filteredRecords: filteredEmployees.length
    };
    
    exportToCSV(exportData, `employees_${new Date().toISOString().split('T')[0]}.csv`, null, null, filterInfo);
  };

  const handlePrint = () => {
    const columns = [
      { label: '<th>Name</th>', key: 'name' },
      { label: '<th>Email</th>', key: 'email' },
      { label: '<th>Role</th>', key: 'role' },
      { label: '<th>Approved</th>', key: 'isApproved', getValue: (emp) => emp.isApproved ? 'Yes' : 'No' }
    ];
    
    // Build filter info object
    const filterInfo = {
      searchText: searchTerm.trim() || null,
      totalRecords: employees.length,
      filteredRecords: filteredEmployees.length
    };
    
    printTable(filteredEmployees, columns, 'Employee List', null, null, filterInfo);
  };

  // Show employee details view
  if (selectedEmployeeEmail) {
    return (
      <EmployeeDetailsView
        employeeEmail={selectedEmployeeEmail}
        token={token}
        onClose={() => setSelectedEmployeeEmail(null)}
      />
    );
  }

  return (
    <div className="employee-management">
      <div className="employee-header">
        <h2 className="employee-title">Employee Management ({companyDomain})</h2>
      </div>
      {error && <p className="employee-error-message">{error}</p>}

      {/* Add Employee Form */}
      <div className="employee-form">
        <div className="form-group">
          <div className="employee-form-field">
            <input
              className={`form-input ${formErrors.name ? 'employee-input-error' : ''}`}
              placeholder="Name *"
              value={form.name}
              onChange={e => setForm({ ...form, name: e.target.value })}
              onBlur={e => validateField('name', e.target.value, false)}
              required
            />
            {formErrors.name && <span className="employee-field-error">{formErrors.name}</span>}
          </div>
          <div className="employee-form-field">
            <div className="email-input-wrapper">
              <input
                className={`form-input ${formErrors.email ? 'employee-input-error' : ''}`}
                placeholder="Username *"
                value={form.emailUsername}
                onChange={e => setForm({ ...form, emailUsername: e.target.value })}
                onBlur={e => {
                  const fullEmail = e.target.value ? `${e.target.value}@${companyDomain}` : '';
                  validateField('email', fullEmail, false);
                }}
                type="text"
                required
              />
              <span className="email-domain-suffix">@{companyDomain}</span>
            </div>
            {formErrors.email && <span className="employee-field-error">{formErrors.email}</span>}
          </div>
          <div className="employee-form-field">
            <div className="password-input-wrapper">
              <input
                className={`form-input ${formErrors.password ? 'employee-input-error' : ''}`}
                placeholder="Password *"
                type={showPassword ? "text" : "password"}
                value={form.password}
                onChange={e => setForm({ ...form, password: e.target.value })}
                onBlur={e => validateField('password', e.target.value, false)}
                required
              />
              <button
                type="button"
                className="password-toggle-btn"
                onClick={() => setShowPassword(!showPassword)}
                title={showPassword ? "Hide password" : "Show password"}
              >
                {showPassword ? (
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M3.98 8.223A10.477 10.477 0 0 0 1.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.451 10.451 0 0 1 12 4.5c4.756 0 8.773 3.162 10.065 7.498a10.522 10.522 0 0 1-4.293 5.774M6.228 6.228 3 3m3.228 3.228 3.65 3.65m7.894 7.894L21 21m-3.228-3.228-3.65-3.65m0 0a3 3 0 1 0-4.243-4.243m4.242 4.242L9.88 9.88" />
                  </svg>
                ) : (
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M2.036 12.322a1.012 1.012 0 0 1 0-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178Z" />
                    <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
                  </svg>
                )}
              </button>
            </div>
            {formErrors.password && <span className="employee-field-error">{formErrors.password}</span>}
          </div>
          <div className="employee-form-field">
            <select
              className="form-select"
              value={form.role}
              onChange={e => setForm({ ...form, role: e.target.value })}
            >
              <option value="Employee">Employee</option>
              <option value="Admin">Admin</option>
            </select>
          </div>
          <div style={{ display: 'flex', gap: '0.5rem' }}>
            <button className="add-employee-button" onClick={handleAdd} disabled={!isFormValidSilent(form)}>Add Employee</button>
            <button 
              className="employee-reset-button" 
              onClick={() => {
                setForm({ name: '', emailUsername: '', email: '', role: 'Employee', password: '', isApproved: false });
                setFormErrors({ name: '', email: '', password: '' });
              }}
            >
              Clear
            </button>
          </div>
        </div>
      </div>

      {/* Existing Employees Table */}
      <div className="employee-list">
        <h3>Approved Employees ({filteredEmployees.length})</h3>
        
        {/* Search Filter */}
        <div className="employee-search-wrapper">
          <input
            type="text"
            placeholder="Search by name, email, or role..."
            value={searchTerm}
            onChange={(e) => {
              setSearchTerm(e.target.value);
              setCurrentPage(1);
            }}
            className="employee-search-input"
          />
          <button
            onClick={() => {
              setSearchTerm('');
              setCurrentPage(1);
            }}
            className="employee-reset-button"
            title="Clear search"
          >
            Clear
          </button>
        </div>

        <div id="employees-table-container">
          <table className="employee-table" id="employees-table">
            <thead>
              <tr>
                <SortableHeader columnKey="name" label="Name" requestSort={requestSort} sortConfig={sortConfig} />
                <SortableHeader columnKey="email" label="Email" requestSort={requestSort} sortConfig={sortConfig} />
                <SortableHeader columnKey="role" label="Role" requestSort={requestSort} sortConfig={sortConfig} />
                <SortableHeader columnKey="isApproved" label="Approved" requestSort={requestSort} sortConfig={sortConfig} />
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {paginatedEmployees.map(emp =>
                editingId === emp.id ? (
                  <tr key={emp.id}>
                    <td>
                      <input
                        value={editForm.name}
                        onChange={e => setEditForm({ ...editForm, name: e.target.value })}
                        required
                      />
                    </td>
                    <td>
                      <input
                        value={editForm.email}
                        onChange={e => setEditForm({ ...editForm, email: e.target.value })}
                        type="email"
                        required
                      />
                    </td>
                    <td>
                      <select
                        value={editForm.role}
                        onChange={e => setEditForm({ ...editForm, role: e.target.value })}
                      >
                        <option value="Employee">Employee</option>
                        <option value="Admin">Admin</option>
                      </select>
                    </td>
                    <td>
                      <input
                        type="checkbox"
                        checked={editForm.isApproved}
                        onChange={e => setEditForm({ ...editForm, isApproved: e.target.checked })}
                        aria-label="Approved"
                      />
                    </td>
                    <td className="employee-actions">
                      <button className="edit-button" onClick={() => handleUpdate(emp.id)} disabled={!isFormValidSilent(editForm)}>Save</button>
                      <button className="delete-button" onClick={cancelEdit}>Cancel</button>
                    </td>
                  </tr>
                ) : (
                  <tr key={emp.id}>
                    <td>
                      <span
                        onClick={() => setSelectedEmployeeEmail(emp.email)}
                        className="employee-name-link"
                      >
                        {emp.name}
                      </span>
                    </td>
                    <td>{emp.email}</td>
                    <td>{emp.role}</td>
                    <td>{emp.isApproved ? 'Yes' : 'No'}</td>
                    <td className="employee-actions">
                      <button className="edit-button" onClick={() => startEdit(emp)}>Edit</button>
                      <button className="delete-button" onClick={() => handleDelete(emp.id)}>Delete</button>
                    </td>
                  </tr>
                )
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination and Export Controls */}
        {filteredEmployees.length > 0 && (
          <div className="table-pagination-container">
            <div className="table-pagination-controls">
              <button
                onClick={() => handlePageChange(1)}
                disabled={currentPage === 1}
                className="table-pagination-button"
              >
                First
              </button>
              <button
                onClick={() => handlePageChange(currentPage - 1)}
                disabled={currentPage === 1}
                className="table-pagination-button"
              >
                Previous
              </button>
              <span className="table-pagination-info">
                Page {currentPage} of {totalPages} ({filteredEmployees.length} total records)
              </span>
              <button
                onClick={() => handlePageChange(currentPage + 1)}
                disabled={currentPage === totalPages}
                className="table-pagination-button"
              >
                Next
              </button>
              <button
                onClick={() => handlePageChange(totalPages)}
                disabled={currentPage === totalPages}
                className="table-pagination-button"
              >
                Last
              </button>
              <select
                value={itemsPerPage}
                onChange={(e) => {
                  setItemsPerPage(Number(e.target.value));
                  setCurrentPage(1);
                }}
                className="table-pagination-select"
              >
                <option value={10}>10 rows</option>
                <option value={50}>50 rows</option>
                <option value={100}>100 rows</option>
              </select>
            </div>

            <div className="table-pagination-actions">
              <button
                onClick={handleExport}
                className="table-export-button"
              >
                Export CSV
              </button>
              <button
                onClick={handlePrint}
                className="table-print-button"
              >
                Print
              </button>
            </div>
          </div>
        )}

        {/* Unapproved Employees Section */}
        <h3 style={{ marginTop: '2rem' }}>Unapproved Employees</h3>
        {unapprovedEmployees.length === 0 ? (
          <p>No unapproved employees.</p>
        ) : (
          <div className="employee-list" style={{ marginTop: '2rem' }}>
            <table className="employee-table">
              <thead>
                <tr>
                  <SortableHeader columnKey="name" label="Name" requestSort={requestSortUnapproved} sortConfig={sortConfigUnapproved} />
                  <SortableHeader columnKey="email" label="Email" requestSort={requestSortUnapproved} sortConfig={sortConfigUnapproved} />
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {sortedUnapprovedEmployees.map(emp => (
                  <tr key={emp.id}>
                    <td>{emp.name}</td>
                    <td>{emp.email}</td>
                    <td className="employee-actions">
                      <button className="edit-button" onClick={() => handleApprove(emp.id)}>Approve</button>
                      <button className="delete-button" onClick={() => handleDelete(emp.id)}>Delete</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}

export default EmployeeManagement;
