import React, { useState, useEffect } from 'react';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import '../styles/EmployeeDetailsView.css';
import { formatDate, exportToCSV, printTable, paginateData, getTotalPages } from '../utils/helpers';
import { formatTimeOnly, formatUTCtoIST } from '../utils/istTimeUtils';
import useSortableData from '../hooks/useSortableData';
import '../styles/TablePagination.css';

function EmployeeDetailsView({ employeeEmail, token, onClose }) {
  const [profile, setProfile] = useState(null);
  const [attendanceRecords, setAttendanceRecords] = useState([]);
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  // Attendance pagination & filters
  const [attendanceSearch, setAttendanceSearch] = useState('');
  const [attendancePage, setAttendancePage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [attendanceDateFilter, setAttendanceDateFilter] = useState('SHOW');

  // Task pagination & filters
  const [taskSearch, setTaskSearch] = useState('');
  const [taskPage, setTaskPage] = useState(1);
  const [taskRowsPerPage, setTaskRowsPerPage] = useState(10);
  const [taskDateFilter, setTaskDateFilter] = useState('SHOW');
  const [taskColumnFilters, setTaskColumnFilters] = useState({
    status: '',
    priority: '',
    approvedBy: '',
    overdueStatus: '',
    rejectionCount: ''
  });

  useEffect(() => {
    fetchEmployeeData();
    // eslint-disable-next-line
  }, [employeeEmail]);

  const fetchEmployeeData = async () => {
    setLoading(true);
    try {
      // Fetch profile (optional - may not exist)
      try {
        const profileRes = await fetch(`${getApiBaseUrl()}/api/Profile/${employeeEmail}`, {
          headers: getApiHeaders()
        });
        if (profileRes.ok) {
          const profileData = await profileRes.json();
          setProfile(profileData);
        }
      } catch (err) {
        console.log('Profile not found for user:', employeeEmail);
      }

      // Fetch attendance records - use admin endpoint
      try {
        const attendanceRes = await fetch(`${getApiBaseUrl()}/api/admin/AttendanceRecords?employeeId=${encodeURIComponent(employeeEmail)}`, {
          headers: {
            ...getApiHeaders(),
            'Authorization': `Bearer ${token}`
          }
        });
        if (attendanceRes.ok) {
          const allAttendance = await attendanceRes.json();
          console.log('Fetched attendance:', allAttendance);
          setAttendanceRecords(allAttendance);
        } else {
          console.error('Attendance fetch failed:', attendanceRes.status);
        }
      } catch (err) {
        console.error('Failed to fetch attendance:', err);
      }

      // Fetch tasks assigned to this employee
      try {
        const tasksRes = await fetch(`${getApiBaseUrl()}/api/Task/employee/${encodeURIComponent(employeeEmail)}`, {
          headers: getApiHeaders()
        });
        if (tasksRes.ok) {
          const allTasks = await tasksRes.json();
          console.log('Fetched tasks:', allTasks);
          setTasks(allTasks);
        } else {
          console.error('Tasks fetch failed:', tasksRes.status);
        }
      } catch (err) {
        console.error('Failed to fetch tasks:', err);
      }
    } catch (err) {
      setError('Failed to load employee data: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  // Filter and paginate attendance
  const filteredAttendance = attendanceRecords.filter(record => {
    const searchLower = attendanceSearch.toLowerCase();
    const recordDate = record.timestamp ? formatDate(record.timestamp) : '';
    return (
      recordDate.includes(searchLower) ||
      (record.wifiSsid && record.wifiSsid.toLowerCase().includes(searchLower)) ||
      (record.name && record.name.toLowerCase().includes(searchLower))
    );
  });

  // Sorting for attendance
  const { items: sortedAttendance, requestSort: requestSortAttendance, sortConfig: sortConfigAttendance } = useSortableData(filteredAttendance);

  // Date filtering for attendance
  const getFilteredAttendanceByDate = () => {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    const dayBefore = new Date(yesterday);
    dayBefore.setDate(dayBefore.getDate() - 1);

    switch (attendanceDateFilter) {
      case 'TODAY':
        return sortedAttendance.filter(record => new Date(record.timestamp) >= today);
      case 'YESTERDAY':
        return sortedAttendance.filter(record => {
          const recordDate = new Date(record.timestamp);
          return recordDate >= yesterday && recordDate < today;
        });
      case 'LATEST':
        return sortedAttendance.filter(record => new Date(record.timestamp) >= dayBefore);
      default:
        return sortedAttendance;
    }
  };

  const filteredAttendanceRecords = getFilteredAttendanceByDate();

  const paginatedAttendance = filteredAttendanceRecords.slice(
    (attendancePage - 1) * rowsPerPage,
    attendancePage * rowsPerPage
  );

  // Filter and paginate tasks - Same as WorkLogHistory
  const getFilteredTasksByDate = () => {
    if (taskDateFilter === 'SHOW') return tasks;

    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    const dayBeforeYesterday = new Date(today);
    dayBeforeYesterday.setDate(dayBeforeYesterday.getDate() - 2);

    return tasks.filter(task => {
      const taskDate = new Date(task.createdAt);
      if (taskDateFilter === 'TODAY') return taskDate >= today;
      if (taskDateFilter === 'YESTERDAY') return taskDate >= yesterday && taskDate < today;
      if (taskDateFilter === 'LATEST') return taskDate >= dayBeforeYesterday;
      return true;
    });
  };

  const getFilteredTasks = () => {
    let filtered = getFilteredTasksByDate();

    // Text search
    if (taskSearch.trim()) {
      const search = taskSearch.toLowerCase();
      filtered = filtered.filter(task =>
        (task.title || '').toLowerCase().includes(search) ||
        (task.description || '').toLowerCase().includes(search) ||
        (task.status || '').toLowerCase().includes(search) ||
        (task.priority || '').toLowerCase().includes(search) ||
        (task.employeeRemarks || '').toLowerCase().includes(search) ||
        (task.adminRemarks || '').toLowerCase().includes(search) ||
        (task.approvedBy || '').toLowerCase().includes(search) ||
        (task.rejectedBy || '').toLowerCase().includes(search)
      );
    }

    // Column filters
    if (taskColumnFilters.status) {
      filtered = filtered.filter(task => 
        (task.status || '').toLowerCase() === taskColumnFilters.status.toLowerCase()
      );
    }
    if (taskColumnFilters.priority) {
      filtered = filtered.filter(task => 
        (task.priority || '').toLowerCase() === taskColumnFilters.priority.toLowerCase()
      );
    }
    if (taskColumnFilters.approvedBy) {
      filtered = filtered.filter(task =>
        ((task.approvedBy || task.rejectedBy) || '').toLowerCase().includes(taskColumnFilters.approvedBy.toLowerCase())
      );
    }
    if (taskColumnFilters.overdueStatus) {
      filtered = filtered.filter(task => {
        const overdueStatus = calculateTaskOverdueStatus(task);
        return overdueStatus.toLowerCase() === taskColumnFilters.overdueStatus.toLowerCase();
      });
    }
    if (taskColumnFilters.rejectionCount) {
      const filterValue = taskColumnFilters.rejectionCount;
      filtered = filtered.filter(task => {
        const count = task.rejectionCount || 0;
        if (filterValue === 'rejected') return count > 0;
        if (filterValue === 'never') return count === 0;
        if (filterValue === '1') return count === 1;
        if (filterValue === '2+') return count >= 2;
        return true;
      });
    }

    return filtered;
  };

  const calculateTaskOverdueStatus = (task) => {
    if (!task.dueDate) return 'N/A';
    const dueDate = new Date(task.dueDate);
    const completedDate = task.approvedDate || task.rejectedDate ? new Date(task.approvedDate || task.rejectedDate) : new Date();
    
    if (task.status === 'Approved' && completedDate <= dueDate) {
      return 'On Time';
    } else if (task.status === 'Approved' && completedDate > dueDate) {
      return 'Overdue';
    } else if (task.status !== 'Approved' && new Date() > dueDate) {
      return 'Overdue';
    } else {
      return 'Pending';
    }
  };

  const handleTaskColumnFilterChange = (column, value) => {
    setTaskColumnFilters(prev => ({
      ...prev,
      [column]: value
    }));
    setTaskPage(1);
  };

  const filteredTasks = getFilteredTasks();
  const { items: sortedTasks, requestSort: requestSortTasks, sortConfig: sortConfigTasks } = useSortableData(filteredTasks);
  const paginatedTasks = paginateData(sortedTasks, taskPage, taskRowsPerPage);
  const totalTaskPages = getTotalPages(sortedTasks.length, taskRowsPerPage);

  const getTaskSortIndicator = (columnName) => {
    if (!sortConfigTasks || sortConfigTasks.key !== columnName) return ' ';
    return sortConfigTasks.direction === 'ascending' ? ' ' : ' ';
  };

  const getAttendanceSortIndicator = (columnName) => {
    if (!sortConfigAttendance || sortConfigAttendance.key !== columnName) return ' ';
    return sortConfigAttendance.direction === 'ascending' ? ' ' : ' ';
  };

  const exportAttendance = () => {
    const csvData = filteredAttendanceRecords.map(record => ({
      Date: formatDate(record.timestamp),
      Time: formatTimeOnly(record.timestamp),
      Type: record.isClockIn ? 'In' : 'Out',
      'WiFi SSID': record.wifiSsid || 'N/A',
    }));
    
    // Build filter info object
    const filterInfo = {
      searchText: attendanceSearch.trim() || null,
      totalRecords: attendanceRecords.length,
      filteredRecords: filteredAttendanceRecords.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    exportToCSV(csvData, 'Attendance', fullName, employeeEmail, filterInfo);
  };

  const exportTasks = () => {
    const data = filteredTasks.map(task => ({
      'Date': formatUTCtoIST(task.createdAt),
      'Task Summary': task.title,
      'Due Date': task.dueDate ? formatUTCtoIST(task.dueDate) : 'N/A',
      'Priority': task.priority || 'N/A',
      'Status': task.status,
      'Employee Remarks': task.employeeRemarks || 'N/A',
      'Approved/Rejected By': task.approvedBy || task.rejectedBy || 'N/A',
      'Approval Time': task.approvedDate || task.rejectedDate ? formatUTCtoIST(task.approvedDate || task.rejectedDate) : 'N/A',
      'Admin Remarks': task.adminRemarks || 'N/A',
      'Times Rejected': task.rejectionCount || 0,
      'Overdue Status': calculateTaskOverdueStatus(task)
    }));
    
    // Build filter info object
    const filterInfo = {
      dateFilter: taskDateFilter !== 'SHOW' ? taskDateFilter : null,
      statusFilter: taskColumnFilters.status || null,
      priorityFilter: taskColumnFilters.priority || null,
      approvedByFilter: taskColumnFilters.approvedBy || null,
      overdueStatusFilter: taskColumnFilters.overdueStatus || null,
      rejectionCountFilter: taskColumnFilters.rejectionCount || null,
      searchText: taskSearch.trim() || null,
      totalRecords: tasks.length,
      filteredRecords: filteredTasks.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    exportToCSV(data, 'work_log_history', fullName, employeeEmail, filterInfo);
  };

  const printAttendanceTable = () => {
    const columns = [
      { key: 'date', label: '<th>Date</th>', getValue: (rec) => formatDate(rec.timestamp) },
      { key: 'time', label: '<th>Time</th>', getValue: (rec) => formatTimeOnly(rec.timestamp) },
      { key: 'type', label: '<th>Type</th>', getValue: (rec) => rec.isClockIn ? 'In' : 'Out' },
      { key: 'wifi', label: '<th>WiFi SSID</th>', getValue: (rec) => rec.wifiSsid || 'N/A' }
    ];
    
    // Build filter info object
    const filterInfo = {
      searchText: attendanceSearch.trim() || null,
      totalRecords: attendanceRecords.length,
      filteredRecords: filteredAttendanceRecords.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    printTable(filteredAttendanceRecords, columns, 'Attendance Records', fullName, employeeEmail, filterInfo);
  };

  const printTasksTable = () => {
    const columns = [
      { key: 'createdAt', label: '<th>Date</th>', getValue: (task) => formatUTCtoIST(task.createdAt) },
      { key: 'title', label: '<th>Task Summary</th>', getValue: (task) => task.title },
      { key: 'dueDate', label: '<th>Due Date</th>', getValue: (task) => task.dueDate ? formatUTCtoIST(task.dueDate) : 'N/A' },
      { key: 'priority', label: '<th>Priority</th>', getValue: (task) => task.priority },
      { key: 'status', label: '<th>Status</th>', getValue: (task) => task.status },
      { key: 'employeeRemarks', label: '<th>Employee Remarks</th>', getValue: (task) => task.employeeRemarks || 'N/A' },
      { key: 'approvedBy', label: '<th>Approved/Rejected By</th>', getValue: (task) => task.approvedBy || task.rejectedBy || 'N/A' },
      { key: 'approvalTime', label: '<th>Approval Time</th>', getValue: (task) => task.approvedDate || task.rejectedDate ? formatUTCtoIST(task.approvedDate || task.rejectedDate) : 'N/A' },
      { key: 'adminRemarks', label: '<th>Admin Remarks</th>', getValue: (task) => task.adminRemarks || 'N/A' },
      { key: 'rejectionCount', label: '<th>Times Rejected</th>', getValue: (task) => task.rejectionCount || 0 },
      { key: 'overdueStatus', label: '<th>Overdue Status</th>', getValue: (task) => calculateTaskOverdueStatus(task) }
    ];
    
    // Build filter info object
    const filterInfo = {
      dateFilter: taskDateFilter !== 'SHOW' ? taskDateFilter : null,
      statusFilter: taskColumnFilters.status || null,
      priorityFilter: taskColumnFilters.priority || null,
      approvedByFilter: taskColumnFilters.approvedBy || null,
      overdueStatusFilter: taskColumnFilters.overdueStatus || null,
      rejectionCountFilter: taskColumnFilters.rejectionCount || null,
      searchText: taskSearch.trim() || null,
      totalRecords: tasks.length,
      filteredRecords: filteredTasks.length
    };
    
    const fullName = profile ? `${profile.firstName || ''} ${profile.middleName || ''} ${profile.lastName || ''}`.trim() : null;
    printTable(filteredTasks, columns, 'Work Log History', fullName, employeeEmail, filterInfo);
  };

  if (loading) {
    return (
      <div className="employee-details-view">
        <div className="employee-details-loading">Loading employee data...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="employee-details-view">
        <div className="employee-details-error">{error}</div>
        <button onClick={onClose} className="btn-close">Close</button>
      </div>
    );
  }

  const fullName = profile
    ? [profile.firstName, profile.middleName, profile.lastName].filter(Boolean).join(' ')
    : employeeEmail;

  return (
    <div className="employee-details-view">
      <div className="employee-details-header">
        <h1>Employee Details: {fullName}</h1>
        <button onClick={onClose} className="btn-close-icon"></button>
      </div>

      <div className="employee-details-content">
        {/* Profile Card */}
        {profile && (
          <div className="details-card profile-details-card">
            <h2>Profile Information</h2>
            <div className="profile-details-grid">
              <div className="profile-details-avatar">
                {profile.profilePicture ? (
                  <img src={profile.profilePicture} alt={fullName} />
                ) : (
                  <div className="profile-details-avatar-placeholder">
                    {profile.firstName?.charAt(0)}{profile.lastName?.charAt(0)}
                  </div>
                )}
              </div>
              <div className="profile-details-info">
                <div className="profile-detail-row">
                  <strong>Full Name:</strong> {fullName}
                </div>
                <div className="profile-detail-row">
                  <strong>Email:</strong> {profile.email}
                </div>
                <div className="profile-detail-row">
                  <strong>Phone:</strong> {profile.phoneNumber}
                </div>
                <div className="profile-detail-row">
                  <strong>Date of Birth:</strong> {formatDate(profile.dateOfBirth)}
                </div>
                <div className="profile-detail-row">
                  <strong>Address:</strong> {profile.address}
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Attendance Records Card */}
        <div className="details-card">
          <div className="details-card-header">
            <h2>Attendance Records ({filteredAttendanceRecords.length})</h2>
            <div className="details-card-actions">
              <input
                type="text"
                placeholder="Search attendance..."
                value={attendanceSearch}
                onChange={(e) => {
                  setAttendanceSearch(e.target.value);
                  setAttendancePage(1);
                }}
                className="search-input"
              />
              <select
                value={attendanceDateFilter}
                onChange={(e) => {
                  setAttendanceDateFilter(e.target.value);
                  setAttendancePage(1);
                }}
                style={{
                  padding: '0.6rem 1rem',
                  border: '1px solid #cbd5e1',
                  borderRadius: '8px',
                  backgroundColor: 'white',
                  cursor: 'pointer',
                  fontWeight: '500',
                  fontSize: '0.95rem'
                }}
              >
                <option value="SHOW">SHOW</option>
                <option value="TODAY">TODAY</option>
                <option value="YESTERDAY">YESTERDAY</option>
                <option value="LATEST">LATEST</option>
              </select>
              <button onClick={exportAttendance} className="btn-action">Export</button>
              <button onClick={printAttendanceTable} className="btn-action">Print</button>
            </div>
          </div>

          <div className="table-container">
            <table id="attendanceTable" className="details-table">
              <thead>
                <tr>
                  <th onClick={() => requestSortAttendance('timestamp')} style={{ cursor: 'pointer' }}>
                    Date{getAttendanceSortIndicator('timestamp')}
                  </th>
                  <th>Time</th>
                  <th onClick={() => requestSortAttendance('isClockIn')} style={{ cursor: 'pointer' }}>
                    Type{getAttendanceSortIndicator('isClockIn')}
                  </th>
                  <th onClick={() => requestSortAttendance('wifiSsid')} style={{ cursor: 'pointer' }}>
                    WiFi SSID{getAttendanceSortIndicator('wifiSsid')}
                  </th>
                </tr>
              </thead>
              <tbody>
                {paginatedAttendance.length > 0 ? (
                  attendanceDateFilter !== 'LATEST' ? (
                    paginatedAttendance.map((record) => (
                      <tr key={record.id}>
                        <td>{formatDate(record.timestamp)}</td>
                        <td>{formatTimeOnly(record.timestamp)}</td>
                        <td>{record.isClockIn ? 'In' : 'Out'}</td>
                        <td>{record.wifiSsid || 'N/A'}</td>
                      </tr>
                    ))
                  ) : (
                    (() => {
                      const now = new Date();
                      const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
                      const yesterday = new Date(today);
                      yesterday.setDate(yesterday.getDate() - 1);
                      const dayBefore = new Date(yesterday);
                      dayBefore.setDate(dayBefore.getDate() - 1);

                      const todayRecords = paginatedAttendance.filter(r => new Date(r.timestamp) >= today);
                      const yesterdayRecords = paginatedAttendance.filter(r => {
                        const d = new Date(r.timestamp);
                        return d >= yesterday && d < today;
                      });
                      const dayBeforeRecords = paginatedAttendance.filter(r => {
                        const d = new Date(r.timestamp);
                        return d >= dayBefore && d < yesterday;
                      });

                      const renderRecords = (records) => records.map((record) => (
                        <tr key={record.id}>
                          <td>{formatDate(record.timestamp)}</td>
                          <td>{formatTimeOnly(record.timestamp)}</td>
                          <td>{record.isClockIn ? 'In' : 'Out'}</td>
                          <td>{record.wifiSsid || 'N/A'}</td>
                        </tr>
                      ));

                      return (
                        <>
                          {todayRecords.length > 0 && (
                            <>
                              <tr style={{ backgroundColor: '#e3f2fd' }}>
                                <td colSpan="4" style={{ fontWeight: 'bold', padding: '0.75rem', fontSize: '1rem' }}>
                                   Today ({formatDate(today)})
                                </td>
                              </tr>
                              {renderRecords(todayRecords)}
                            </>
                          )}
                          {yesterdayRecords.length > 0 && (
                            <>
                              <tr style={{ backgroundColor: '#fff3e0' }}>
                                <td colSpan="4" style={{ fontWeight: 'bold', padding: '0.75rem', fontSize: '1rem' }}>
                                   Yesterday ({formatDate(yesterday)})
                                </td>
                              </tr>
                              {renderRecords(yesterdayRecords)}
                            </>
                          )}
                          {dayBeforeRecords.length > 0 && (
                            <>
                              <tr style={{ backgroundColor: '#f3e5f5' }}>
                                <td colSpan="4" style={{ fontWeight: 'bold', padding: '0.75rem', fontSize: '1rem' }}>
                                   {formatDate(dayBefore)}
                                </td>
                              </tr>
                              {renderRecords(dayBeforeRecords)}
                            </>
                          )}
                        </>
                      );
                    })()
                  )
                ) : (
                  <tr>
                    <td colSpan="4" style={{ textAlign: 'center' }}>
                      No attendance records found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {filteredAttendanceRecords.length > rowsPerPage && (
            <div className="pagination" style={{ display: 'flex', gap: '0.5rem', alignItems: 'center', justifyContent: 'center', flexWrap: 'wrap' }}>
              <button
                onClick={() => setAttendancePage(1)}
                disabled={attendancePage === 1}
              >
                First
              </button>
              <button
                onClick={() => setAttendancePage(p => Math.max(1, p - 1))}
                disabled={attendancePage === 1}
              >
                Previous
              </button>
              <span>
                Page {attendancePage} of {Math.ceil(filteredAttendanceRecords.length / rowsPerPage)} ({filteredAttendanceRecords.length} total records)
              </span>
              <button
                onClick={() => setAttendancePage(p => Math.min(Math.ceil(filteredAttendanceRecords.length / rowsPerPage), p + 1))}
                disabled={attendancePage >= Math.ceil(filteredAttendanceRecords.length / rowsPerPage)}
              >
                Next
              </button>
              <button
                onClick={() => setAttendancePage(Math.ceil(filteredAttendanceRecords.length / rowsPerPage))}
                disabled={attendancePage >= Math.ceil(filteredAttendanceRecords.length / rowsPerPage)}
              >
                Last
              </button>
              <select
                value={rowsPerPage}
                onChange={(e) => {
                  setRowsPerPage(Number(e.target.value));
                  setAttendancePage(1);
                }}
                style={{
                  padding: '0.5rem',
                  border: '1px solid #cbd5e1',
                  borderRadius: '6px',
                  backgroundColor: 'white',
                  cursor: 'pointer'
                }}
              >
                <option value={10}>10 rows</option>
                <option value={50}>50 rows</option>
                <option value={100}>100 rows</option>
              </select>
            </div>
          )}
        </div>

        {/* Tasks/WorkLog Records Card - Same structure as WorkLogHistory */}
        <div className="details-card">
          <div className="worklog-history-header" style={{ padding: '1rem', borderBottom: '1px solid #e2e8f0' }}>
            <h3 className="worklog-history-title" style={{ fontSize: '1.25rem', fontWeight: '600', marginBottom: '0.5rem' }}>
              Work Log History ({filteredTasks.length} records)
            </h3>
            <input
              type="text"
              placeholder="Search work logs..."
              value={taskSearch}
              onChange={(e) => {
                setTaskSearch(e.target.value);
                setTaskPage(1);
              }}
              className="worklog-history-search"
              style={{ width: '100%', padding: '0.5rem', border: '1px solid #cbd5e1', borderRadius: '6px' }}
            />
          </div>

          <div className="worklog-history-table-wrapper" style={{ overflowX: 'auto' }}>
            <table className="worklog-history-table" style={{ width: '100%', borderCollapse: 'collapse' }}>
              <thead>
                <tr className="worklog-history-table-header-row" style={{ backgroundColor: '#f8fafc' }}>
                  <th onClick={() => requestSortTasks('createdAt')} className="worklog-history-th-sortable worklog-history-th-date" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Date{getTaskSortIndicator('createdAt')}
                  </th>
                  <th onClick={() => requestSortTasks('title')} className="worklog-history-th-sortable worklog-history-th-task" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Task Summary{getTaskSortIndicator('title')}
                  </th>
                  <th onClick={() => requestSortTasks('dueDate')} className="worklog-history-th-sortable worklog-history-th-due" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Due Date{getTaskSortIndicator('dueDate')}
                  </th>
                  <th onClick={() => requestSortTasks('priority')} className="worklog-history-th-sortable worklog-history-th-priority" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Priority{getTaskSortIndicator('priority')}
                  </th>
                  <th onClick={() => requestSortTasks('status')} className="worklog-history-th-sortable worklog-history-th-status" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Status{getTaskSortIndicator('status')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-emp-remarks" style={{ padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Employee Remarks
                  </th>
                  <th onClick={() => requestSortTasks('approvedBy')} className="worklog-history-th-sortable worklog-history-th-approver" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Approved/Rejected By{getTaskSortIndicator('approvedBy')}
                  </th>
                  <th onClick={() => requestSortTasks('approvedDate')} className="worklog-history-th-sortable worklog-history-th-approval-time" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Approval Time{getTaskSortIndicator('approvedDate')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-admin-remarks" style={{ padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Admin Remarks
                  </th>
                  <th onClick={() => requestSortTasks('rejectionCount')} className="worklog-history-th-sortable worklog-history-th-rejection-count" style={{ cursor: 'pointer', padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Times Rejected{getTaskSortIndicator('rejectionCount')}
                  </th>
                  <th className="worklog-history-th worklog-history-th-overdue" style={{ padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #e2e8f0' }}>
                    Overdue Status
                  </th>
                </tr>
                <tr className="worklog-history-filter-row" style={{ backgroundColor: '#f1f5f9' }}>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}>
                    <select
                      value={taskColumnFilters.priority}
                      onChange={(e) => handleTaskColumnFilterChange('priority', e.target.value)}
                      className="worklog-history-filter-select"
                      style={{ width: '100%', padding: '0.25rem', border: '1px solid #cbd5e1', borderRadius: '4px', fontSize: '0.875rem' }}
                    >
                      <option value="">All</option>
                      <option value="High">High</option>
                      <option value="Medium">Medium</option>
                      <option value="Low">Low</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}>
                    <select
                      value={taskColumnFilters.status}
                      onChange={(e) => handleTaskColumnFilterChange('status', e.target.value)}
                      className="worklog-history-filter-select"
                      style={{ width: '100%', padding: '0.25rem', border: '1px solid #cbd5e1', borderRadius: '4px', fontSize: '0.875rem' }}
                    >
                      <option value="">All</option>
                      <option value="Pending">Pending</option>
                      <option value="Completed">Completed</option>
                      <option value="Approved">Approved</option>
                      <option value="Rejected">Rejected</option>
                      <option value="Overdue">Overdue</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}>
                    <input
                      type="text"
                      placeholder="Filter..."
                      value={taskColumnFilters.approvedBy}
                      onChange={(e) => handleTaskColumnFilterChange('approvedBy', e.target.value)}
                      className="worklog-history-filter-input"
                      style={{ width: '100%', padding: '0.25rem', border: '1px solid #cbd5e1', borderRadius: '4px', fontSize: '0.875rem' }}
                    />
                  </th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}></th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}>
                    <select
                      value={taskColumnFilters.rejectionCount}
                      onChange={(e) => handleTaskColumnFilterChange('rejectionCount', e.target.value)}
                      className="worklog-history-filter-select"
                      style={{ width: '100%', padding: '0.25rem', border: '1px solid #cbd5e1', borderRadius: '4px', fontSize: '0.875rem' }}
                    >
                      <option value="">All</option>
                      <option value="never">Never Rejected</option>
                      <option value="rejected">Has Rejections</option>
                      <option value="1">Rejected Once</option>
                      <option value="2+">Rejected 2+</option>
                    </select>
                  </th>
                  <th className="worklog-history-filter-cell" style={{ padding: '0.5rem' }}>
                    <select
                      value={taskColumnFilters.overdueStatus}
                      onChange={(e) => handleTaskColumnFilterChange('overdueStatus', e.target.value)}
                      className="worklog-history-filter-select"
                      style={{ width: '100%', padding: '0.25rem', border: '1px solid #cbd5e1', borderRadius: '4px', fontSize: '0.875rem' }}
                    >
                      <option value="">All</option>
                      <option value="On Time">On Time</option>
                      <option value="Overdue">Overdue</option>
                      <option value="Pending">Pending</option>
                    </select>
                  </th>
                </tr>
              </thead>
              <tbody>
                {paginatedTasks.length > 0 ? (
                  paginatedTasks.map((task) => (
                    <tr key={task.id} style={{ borderBottom: '1px solid #e2e8f0' }}>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem' }}>{formatUTCtoIST(task.createdAt)}</td>
                      <td style={{ padding: '0.75rem' }}>
                        <div style={{ fontWeight: '600', marginBottom: '0.25rem' }}>{task.title}</div>
                        {task.description && (
                          <div style={{ fontSize: '0.875rem', color: '#64748b' }}>
                            {task.description.length > 100 ? task.description.substring(0, 100) + '...' : task.description}
                          </div>
                        )}
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem' }}>
                        {task.dueDate ? formatUTCtoIST(task.dueDate) : 'N/A'}
                      </td>
                      <td style={{ padding: '0.75rem' }}>
                        <span style={{
                          backgroundColor: task.priority === 'High' ? '#ef4444' : task.priority === 'Medium' ? '#f59e0b' : '#10b981',
                          color: 'white',
                          padding: '0.25rem 0.5rem',
                          borderRadius: '4px',
                          fontSize: '0.75rem',
                          fontWeight: '600'
                        }}>
                          {task.priority}
                        </span>
                      </td>
                      <td style={{ padding: '0.75rem' }}>
                        <span style={{
                          backgroundColor: 
                            task.status === 'Approved' ? '#10b981' :
                            task.status === 'Rejected' ? '#ef4444' :
                            task.status === 'Completed' ? '#3b82f6' :
                            task.status === 'Overdue' ? '#dc2626' :
                            '#fbbf24',
                          color: 'white',
                          padding: '0.25rem 0.5rem',
                          borderRadius: '4px',
                          fontSize: '0.75rem',
                          fontWeight: '600'
                        }}>
                          {task.status}
                        </span>
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem', maxWidth: '200px' }}>
                        {task.employeeRemarks || 'N/A'}
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem' }}>
                        {task.approvedBy || task.rejectedBy || 'N/A'}
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem' }}>
                        {task.approvedDate || task.rejectedDate ? formatUTCtoIST(task.approvedDate || task.rejectedDate) : 'N/A'}
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem', maxWidth: '200px' }}>
                        {task.adminRemarks || 'N/A'}
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem', textAlign: 'center' }}>
                        <span style={{
                          backgroundColor: (task.rejectionCount || 0) > 0 ? '#fca5a5' : '#d1fae5',
                          color: (task.rejectionCount || 0) > 0 ? '#7f1d1d' : '#065f46',
                          padding: '0.25rem 0.5rem',
                          borderRadius: '4px',
                          fontSize: '0.75rem',
                          fontWeight: '600'
                        }}>
                          {task.rejectionCount || 0}
                        </span>
                      </td>
                      <td style={{ padding: '0.75rem', fontSize: '0.875rem' }}>
                        <span style={{
                          backgroundColor: 
                            calculateTaskOverdueStatus(task) === 'On Time' ? '#10b981' :
                            calculateTaskOverdueStatus(task) === 'Overdue' ? '#ef4444' :
                            '#fbbf24',
                          color: 'white',
                          padding: '0.25rem 0.5rem',
                          borderRadius: '4px',
                          fontSize: '0.75rem',
                          fontWeight: '600'
                        }}>
                          {calculateTaskOverdueStatus(task)}
                        </span>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="11" className="worklog-history-no-data" style={{ padding: '2rem', textAlign: 'center', color: '#64748b' }}>
                      No work log records found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* BOTTOM: Controls - Same as WorkLogHistory */}
          <div className="table-pagination-container" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '1rem', borderTop: '1px solid #e2e8f0', flexWrap: 'wrap', gap: '1rem' }}>
            {/* LEFT: Pagination */}
            <div className="table-pagination-controls" style={{ display: 'flex', gap: '0.5rem', alignItems: 'center', flexWrap: 'wrap' }}>
              <button
                onClick={() => setTaskPage(1)}
                disabled={taskPage === 1}
                className="table-pagination-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: taskPage === 1 ? '#f1f5f9' : 'white', cursor: taskPage === 1 ? 'not-allowed' : 'pointer' }}
              >
                First
              </button>
              <button
                onClick={() => setTaskPage(prev => Math.max(1, prev - 1))}
                disabled={taskPage === 1}
                className="table-pagination-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: taskPage === 1 ? '#f1f5f9' : 'white', cursor: taskPage === 1 ? 'not-allowed' : 'pointer' }}
              >
                Previous
              </button>
              <span className="table-pagination-info" style={{ padding: '0 0.5rem', fontSize: '0.875rem' }}>
                Page {taskPage} of {totalTaskPages} ({filteredTasks.length} total records)
              </span>
              <button
                onClick={() => setTaskPage(prev => Math.min(totalTaskPages, prev + 1))}
                disabled={taskPage === totalTaskPages}
                className="table-pagination-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: taskPage === totalTaskPages ? '#f1f5f9' : 'white', cursor: taskPage === totalTaskPages ? 'not-allowed' : 'pointer' }}
              >
                Next
              </button>
              <button
                onClick={() => setTaskPage(totalTaskPages)}
                disabled={taskPage === totalTaskPages}
                className="table-pagination-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: taskPage === totalTaskPages ? '#f1f5f9' : 'white', cursor: taskPage === totalTaskPages ? 'not-allowed' : 'pointer' }}
              >
                Last
              </button>
              <select
                value={taskRowsPerPage}
                onChange={(e) => {
                  setTaskRowsPerPage(Number(e.target.value));
                  setTaskPage(1);
                }}
                className="table-pagination-select"
                style={{ padding: '0.5rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: 'white', cursor: 'pointer' }}
              >
                <option value={10}>10 rows</option>
                <option value={50}>50 rows</option>
                <option value={100}>100 rows</option>
              </select>
              <select
                value={taskDateFilter}
                onChange={(e) => {
                  setTaskDateFilter(e.target.value);
                  setTaskPage(1);
                }}
                className="table-pagination-select"
                style={{ padding: '0.5rem', border: '1px solid #cbd5e1', borderRadius: '6px', backgroundColor: 'white', cursor: 'pointer' }}
              >
                <option value="SHOW">SHOW ALL</option>
                <option value="TODAY">TODAY</option>
                <option value="YESTERDAY">YESTERDAY</option>
                <option value="LATEST">LATEST (3 days)</option>
              </select>
            </div>

            {/* RIGHT: Actions */}
            <div className="table-pagination-actions" style={{ display: 'flex', gap: '0.5rem' }}>
              <button
                onClick={exportTasks}
                className="table-export-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #10b981', backgroundColor: '#10b981', color: 'white', borderRadius: '6px', cursor: 'pointer', fontWeight: '600' }}
              >
                Export CSV
              </button>
              <button
                onClick={printTasksTable}
                className="table-print-button"
                style={{ padding: '0.5rem 1rem', border: '1px solid #3b82f6', backgroundColor: '#3b82f6', color: 'white', borderRadius: '6px', cursor: 'pointer', fontWeight: '600' }}
              >
                Print
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EmployeeDetailsView;
