import React, { useState, useEffect } from 'react';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import { formatUTCtoIST } from '../utils/istTimeUtils';
import '../styles/WorkLogManagement.css';

const WorkLogManagement = () => {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [expandedEmployee, setExpandedEmployee] = useState(null);
  const [selectedStatus, setSelectedStatus] = useState('Completed');
  const [adminRemarks, setAdminRemarks] = useState('');
  const [showRemarksDialog, setShowRemarksDialog] = useState(false);
  const [selectedTaskForAction, setSelectedTaskForAction] = useState(null);
  const [actionType, setActionType] = useState('');
  const [remarksError, setRemarksError] = useState('');
  const [showEditDialog, setShowEditDialog] = useState(false);
  const [editingTask, setEditingTask] = useState(null);
  const [editForm, setEditForm] = useState({ title: '', description: '', priority: 'Medium', dueDate: '' });

  useEffect(() => {
    fetchAllTasks();
    
    // Set up polling every 10 seconds for real-time updates
    const pollingInterval = setInterval(() => {
      // Silent fetch without loading state to prevent flickering
      fetchAllTasksSilent();
    }, 10000); // 10 seconds
    
    // Cleanup interval on unmount
    return () => clearInterval(pollingInterval);
  }, []);

  // Silent fetch for polling - only updates if data actually changed
  const fetchAllTasksSilent = async () => {
    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/all`, {
        headers: getApiHeaders()
      });
      if (response.ok) {
        const data = await response.json();
        // Only update if data actually changed (prevents flickering)
        if (JSON.stringify(data) !== JSON.stringify(tasks)) {
          setTasks(data);
        }
      }
    } catch (error) {
      // Silently fail during polling (don't show errors)
      console.debug('Polling error (ignored):', error.message);
    }
  };

  const fetchAllTasks = async () => {
    try {
      setLoading(true);
      const response = await fetch(`${getApiBaseUrl()}/api/Task/all`, {
        headers: getApiHeaders()
      });
      if (response.ok) {
        const data = await response.json();
        setTasks(data);
      }
    } catch (error) {
      console.error('Error fetching tasks:', error);
    } finally {
      setLoading(false);
    }
  };

  const groupTasksByEmployee = () => {
    const grouped = {};
    
    // Get today's date (midnight to midnight)
    const now = new Date();
    const todayStart = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const todayEnd = new Date(todayStart);
    todayEnd.setDate(todayEnd.getDate() + 1); // Next day midnight
    
    tasks.forEach(task => {
      const email = task.assignedToEmail;
      if (!grouped[email]) {
        grouped[email] = {
          pending: [],
          completed: [],
          approved: []
        };
      }
      
      if (task.status === 'Pending' || task.status === 'Rejected') {
        grouped[email].pending.push(task);
      } else if (task.status === 'Completed') {
        grouped[email].completed.push(task);
      } else if (task.status === 'Approved') {
        // Only include approved tasks from TODAY
        if (task.approvedDate) {
          const approvedDate = new Date(task.approvedDate);
          const approvedDateStart = new Date(approvedDate.getFullYear(), approvedDate.getMonth(), approvedDate.getDate());
          
          // Only add if approved today (same date)
          if (approvedDateStart.getTime() === todayStart.getTime()) {
            grouped[email].approved.push(task);
          }
        }
      }
    });
    
    return grouped;
  };  const handleDelete = async (taskId) => {
    if (!window.confirm('Are you sure you want to delete this task?')) {
      return;
    }

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/delete/${taskId}`, {
        method: 'DELETE',
        headers: getApiHeaders()
      });

      if (response.ok) {
        alert('Task deleted successfully');
        fetchAllTasks();
      } else {
        alert('Failed to delete task');
      }
    } catch (error) {
      console.error('Error deleting task:', error);
      alert('Error deleting task');
    }
  };

  const openApproveDialog = (task) => {
    setSelectedTaskForAction(task);
    setActionType('approve');
    setAdminRemarks('');
    setRemarksError('');
    setShowRemarksDialog(true);
  };

  const openRejectDialog = (task) => {
    setSelectedTaskForAction(task);
    setActionType('reject');
    setAdminRemarks('');
    setRemarksError('');
    setShowRemarksDialog(true);
  };

  const handleApprove = async () => {
    // Get admin email from localStorage
    const adminEmail = localStorage.getItem('userEmail') || 'admin@example.com';
    
    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/approve/${selectedTaskForAction.id}`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({ 
          AdminRemarks: adminRemarks || '',
          AdminEmail: adminEmail 
        }),
      });

      if (response.ok) {
        alert('Task approved successfully');
        setShowRemarksDialog(false);
        setAdminRemarks('');
        setSelectedTaskForAction(null);
        fetchAllTasks();
      } else {
        alert('Failed to approve task');
      }
    } catch (error) {
      console.error('Error approving task:', error);
      alert('Error approving task');
    }
  };

  const handleReject = async () => {
    if (!adminRemarks || adminRemarks.trim() === '') {
      setRemarksError('Admin remarks are required for rejection');
      return;
    }

    // Get admin email from localStorage
    const adminEmail = localStorage.getItem('userEmail') || 'admin@example.com';

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/reject/${selectedTaskForAction.id}`, {
        method: 'POST',
        headers: getApiHeaders(),
        body: JSON.stringify({ 
          AdminRemarks: adminRemarks,
          AdminEmail: adminEmail 
        }),
      });

      if (response.ok) {
        alert('Task rejected successfully');
        setShowRemarksDialog(false);
        setAdminRemarks('');
        setRemarksError('');
        setSelectedTaskForAction(null);
        fetchAllTasks();
      } else {
        alert('Failed to reject task');
      }
    } catch (error) {
      console.error('Error rejecting task:', error);
      alert('Error rejecting task');
    }
  };

  const handleRevokeApproval = async (taskId) => {
    if (!window.confirm('Are you sure you want to revoke this approval? The task will go back to Completed status.')) {
      return;
    }

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/reverse/${taskId}`, {
        method: 'POST',
        headers: getApiHeaders()
      });

      if (response.ok) {
        alert('Approval revoked successfully');
        fetchAllTasks();
      } else {
        alert('Failed to revoke approval');
      }
    } catch (error) {
      console.error('Error revoking approval:', error);
      alert('Error revoking approval');
    }
  };

  const handleEditClick = (task) => {
    setEditingTask(task);
    setEditForm({
      title: task.title,
      description: task.description || '',
      priority: task.priority || 'Medium',
      dueDate: task.dueDate ? new Date(task.dueDate).toISOString().split('T')[0] : ''
    });
    setShowEditDialog(true);
  };

  const handleEditSave = async () => {
    if (!editForm.title.trim()) {
      alert('Title is required');
      return;
    }

    try {
      const response = await fetch(`${getApiBaseUrl()}/api/Task/edit/${editingTask.id}`, {
        method: 'PUT',
        headers: getApiHeaders(),
        body: JSON.stringify({
          id: editingTask.id,
          title: editForm.title,
          description: editForm.description,
          priority: editForm.priority,
          dueDate: editForm.dueDate || null,
          assignedToEmail: editingTask.assignedToEmail
        }),
      });

      if (response.ok) {
        alert('Task updated successfully');
        setShowEditDialog(false);
        setEditingTask(null);
        fetchAllTasks();
      } else {
        alert('Failed to update task');
      }
    } catch (error) {
      console.error('Error updating task:', error);
      alert('Error updating task');
    }
  };

  const handleRowClick = (employeeEmail) => {
    if (expandedEmployee === employeeEmail) {
      setExpandedEmployee(null);
      setSelectedStatus(null);
    } else {
      setExpandedEmployee(employeeEmail);
      setSelectedStatus('Completed');
    }
  };

  const handleStatusButtonClick = (employeeEmail, status, e) => {
    if (e) e.stopPropagation();
    
    if (expandedEmployee === employeeEmail && selectedStatus === status) {
      setExpandedEmployee(null);
      setSelectedStatus(null);
    } else {
      setExpandedEmployee(employeeEmail);
      setSelectedStatus(status);
    }
  };

  const renderTaskCard = (task) => {
    const getPriorityClass = (priority) => {
      switch (priority?.toLowerCase()) {
        case 'high': return 'priority-badge priority-high';
        case 'medium': return 'priority-badge priority-medium';
        case 'low': return 'priority-badge priority-low';
        default: return 'priority-badge';
      }
    };

    return (
      <div key={task.id} className="task-detail-card">
        <div className="task-detail-header">
          <strong>{task.title}</strong>
          {task.priority && <span className={getPriorityClass(task.priority)}>{task.priority}</span>}
        </div>
        
        {task.description && <div className="task-description">{task.description}</div>}
        
        <div className="task-metadata">
          {task.dueDate && <span>Due: {formatUTCtoIST(task.dueDate)}</span>}
          {task.completedDate && <span>Submitted: {formatUTCtoIST(task.completedDate)}</span>}
          {task.rejectionCount > 0 && (
            <span className="rejection-badge">Rejected {task.rejectionCount}x</span>
          )}
          {task.approvedBy && <span>Approved By: {task.approvedBy}</span>}
          {task.rejectedBy && <span>Rejected By: {task.rejectedBy}</span>}
        </div>

        {task.employeeRemarks && (
          <div className="task-employee-remarks">
            <div className="task-employee-remarks-label">Employee Remarks:</div>
            <div className="task-employee-remarks-text">{task.employeeRemarks}</div>
          </div>
        )}

        {task.adminRemarks && (
          <div className="task-admin-remarks">
            <div className="task-admin-remarks-label">Admin Remarks:</div>
            <div className="task-admin-remarks-text">{task.adminRemarks}</div>
          </div>
        )}

        <div className="task-actions">
          {task.status === 'Completed' && (
            <>
              <button 
                className="btn-approve-task"
                onClick={() => openApproveDialog(task)}
              >
                Approve
              </button>
              <button 
                className="btn-reject-task"
                onClick={() => openRejectDialog(task)}
              >
                Reject
              </button>
            </>
          )}

          {task.status === 'Approved' && (
            <button 
              className="btn-revoke"
              onClick={() => handleRevokeApproval(task.id)}
            >
              Revoke Approval
            </button>
          )}
        </div>
      </div>
    );
  };

  const groupedTasks = groupTasksByEmployee();

  if (loading) {
    return <div className="loading">Loading tasks...</div>;
  }

  return (
    <div className="worklog-management-container">
      <h2>Work Log Management</h2>
      
      {Object.keys(groupedTasks).length === 0 ? (
        <div className="no-tasks">No tasks found</div>
      ) : (
        Object.entries(groupedTasks).map(([email, tasksByStatus]) => {
          const isExpanded = expandedEmployee === email;
          const totalTasks = tasksByStatus.pending.length + tasksByStatus.completed.length + tasksByStatus.approved.length;

          return (
            <div key={email} className="employee-worklog-section">
              <div className="employee-header" onClick={() => handleRowClick(email)}>
                <div className="employee-info">
                  <div className="employee-name">{email}</div>
                  <div className="employee-id">Total Tasks: {totalTasks}</div>
                </div>
                
                <div className="status-buttons">
                  <button
                    className={`status-btn status-btn-pending ${isExpanded && selectedStatus === 'Pending' ? 'active' : ''}`}
                    onClick={(e) => handleStatusButtonClick(email, 'Pending', e)}
                  >
                    Pending
                    <span className="badge">{tasksByStatus.pending.length}</span>
                  </button>
                  <button
                    className={`status-btn status-btn-completed ${isExpanded && selectedStatus === 'Completed' ? 'active' : ''}`}
                    onClick={(e) => handleStatusButtonClick(email, 'Completed', e)}
                  >
                    Completed
                    <span className="badge">{tasksByStatus.completed.length}</span>
                  </button>
                  <button
                    className={`status-btn status-btn-approved ${isExpanded && selectedStatus === 'Approved' ? 'active' : ''}`}
                    onClick={(e) => handleStatusButtonClick(email, 'Approved', e)}
                  >
                    Approved
                    <span className="badge">{tasksByStatus.approved.length}</span>
                  </button>
                </div>
              </div>

              {isExpanded && selectedStatus && (
                <div className="worklog-grid">
                  {selectedStatus === 'Pending' && tasksByStatus.pending.length > 0 ? (
                    tasksByStatus.pending.map(task => renderTaskCard(task))
                  ) : selectedStatus === 'Completed' && tasksByStatus.completed.length > 0 ? (
                    tasksByStatus.completed.map(task => renderTaskCard(task))
                  ) : selectedStatus === 'Approved' && tasksByStatus.approved.length > 0 ? (
                    tasksByStatus.approved.map(task => renderTaskCard(task))
                  ) : (
                    <div className="no-worklogs-message">No {selectedStatus.toLowerCase()} tasks</div>
                  )}
                </div>
              )}
            </div>
          );
        })
      )}

      {showRemarksDialog && (
        <div className="dialog-overlay" onClick={() => setShowRemarksDialog(false)}>
          <div className="dialog-content" onClick={(e) => e.stopPropagation()}>
            <h3>{actionType === 'approve' ? 'Approve Task' : 'Reject Task'}</h3>
            <p><strong>Task:</strong> {selectedTaskForAction?.title}</p>
            
            <label>
              Admin Remarks {actionType === 'reject' && <span className="required">*</span>}
            </label>
            <textarea
              value={adminRemarks}
              onChange={(e) => {
                setAdminRemarks(e.target.value);
                setRemarksError('');
              }}
              placeholder={actionType === 'approve' ? 'Optional remarks' : 'Required: Explain reason for rejection'}
              rows={4}
            />
            {remarksError && <div className="error-message">{remarksError}</div>}
            
            <div className="dialog-actions">
              <button 
                className="btn btn-cancel"
                onClick={() => {
                  setShowRemarksDialog(false);
                  setAdminRemarks('');
                  setRemarksError('');
                }}
              >
                Cancel
              </button>
              <button 
                className={`btn ${actionType === 'approve' ? 'btn-approve' : 'btn-reject'}`}
                onClick={actionType === 'approve' ? handleApprove : handleReject}
              >
                {actionType === 'approve' ? 'Approve' : 'Reject'}
              </button>
            </div>
          </div>
        </div>
      )}

      {showEditDialog && (
        <div className="dialog-overlay" onClick={() => setShowEditDialog(false)}>
          <div className="dialog-content" onClick={(e) => e.stopPropagation()}>
            <h3>Edit Task</h3>
            
            <label>
              Title <span className="required">*</span>
            </label>
            <input
              type="text"
              value={editForm.title}
              onChange={(e) => setEditForm({ ...editForm, title: e.target.value })}
              placeholder="Task title"
            />
            
            <label>Description</label>
            <textarea
              value={editForm.description}
              onChange={(e) => setEditForm({ ...editForm, description: e.target.value })}
              placeholder="Task description"
              rows={4}
            />
            
            <label>Priority</label>
            <select
              value={editForm.priority}
              onChange={(e) => setEditForm({ ...editForm, priority: e.target.value })}
            >
              <option value="Low">Low</option>
              <option value="Medium">Medium</option>
              <option value="High">High</option>
            </select>
            
            <label>Due Date</label>
            <input
              type="date"
              value={editForm.dueDate}
              onChange={(e) => setEditForm({ ...editForm, dueDate: e.target.value })}
            />
            
            <div className="dialog-actions">
              <button 
                className="btn btn-cancel"
                onClick={() => {
                  setShowEditDialog(false);
                  setEditingTask(null);
                }}
              >
                Cancel
              </button>
              <button 
                className="btn btn-approve"
                onClick={handleEditSave}
              >
                Save Changes
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default WorkLogManagement;