import { useState, useMemo } from 'react';

/**
 * Custom hook for sortable table data
 * @param {Array} items - The data array to sort
 * @param {Object} config - Initial sort configuration {key: string, direction: 'asc'|'desc'|null}
 * @returns {Object} - {items: sortedItems, requestSort: function, sortConfig: {key, direction}}
 */
function useSortableData(items, config = null) {
  const [sortConfig, setSortConfig] = useState(config);

  const sortedItems = useMemo(() => {
    let sortableItems = [...items];
    if (sortConfig !== null && sortConfig.direction !== null) {
      sortableItems.sort((a, b) => {
        const aValue = getNestedValue(a, sortConfig.key);
        const bValue = getNestedValue(b, sortConfig.key);

        // Handle null/undefined values
        if (aValue == null && bValue == null) return 0;
        if (aValue == null) return 1;
        if (bValue == null) return -1;

        // Handle dates
        if (aValue instanceof Date && bValue instanceof Date) {
          return sortConfig.direction === 'asc' 
            ? aValue.getTime() - bValue.getTime()
            : bValue.getTime() - aValue.getTime();
        }

        // Try parsing as dates from strings
        const aDate = new Date(aValue);
        const bDate = new Date(bValue);
        if (!isNaN(aDate) && !isNaN(bDate)) {
          return sortConfig.direction === 'asc'
            ? aDate.getTime() - bDate.getTime()
            : bDate.getTime() - aDate.getTime();
        }

        // Handle numbers
        const aNum = parseFloat(aValue);
        const bNum = parseFloat(bValue);
        if (!isNaN(aNum) && !isNaN(bNum)) {
          return sortConfig.direction === 'asc' ? aNum - bNum : bNum - aNum;
        }

        // Handle strings (case-insensitive)
        const aStr = String(aValue).toLowerCase();
        const bStr = String(bValue).toLowerCase();
        
        if (aStr < bStr) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (aStr > bStr) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }
    return sortableItems;
  }, [items, sortConfig]);

  const requestSort = (key) => {
    let direction = 'asc';
    if (
      sortConfig &&
      sortConfig.key === key &&
      sortConfig.direction === 'asc'
    ) {
      direction = 'desc';
    } else if (
      sortConfig &&
      sortConfig.key === key &&
      sortConfig.direction === 'desc'
    ) {
      direction = null; // Reset to unsorted
    }
    setSortConfig(direction === null ? null : { key, direction });
  };

  return { items: sortedItems, requestSort, sortConfig };
}

/**
 * Helper function to get nested object values using dot notation
 * @param {Object} obj - The object to extract value from
 * @param {String} path - Dot notation path (e.g., 'user.name')
 */
function getNestedValue(obj, path) {
  return path.split('.').reduce((acc, part) => acc && acc[part], obj);
}

/**
 * React component for sortable table header with arrow indicators
 * @param {String} columnKey - The key of the column for sorting
 * @param {String} label - Display label for the header
 * @param {Function} requestSort - Function to request sorting
 * @param {Object} sortConfig - Current sort configuration
 */
export function SortableHeader({ columnKey, label, requestSort, sortConfig }) {
  const getSortIndicator = () => {
    if (!sortConfig || sortConfig.key !== columnKey) {
      return <span className="sort-indicator"></span>; // Both arrows (unsorted)
    }
    if (sortConfig.direction === 'asc') {
      return <span className="sort-indicator active"></span>; // Up arrow (ascending)
    }
    if (sortConfig.direction === 'desc') {
      return <span className="sort-indicator active"></span>; // Down arrow (descending)
    }
    return <span className="sort-indicator"></span>; // Both arrows (unsorted)
  };

  return (
    <th 
      onClick={() => requestSort(columnKey)}
      style={{ cursor: 'pointer', userSelect: 'none' }}
      className="sortable-header"
    >
      {label} {getSortIndicator()}
    </th>
  );
}

export default useSortableData;
