import React from 'react';
import '../styles/AlertModal.css';

/**
 * Custom Alert Modal that doesn't block Electron keyboard input
 * Replaces native alert() which breaks keyboard input after closing
 */
function AlertModal({ message, onClose }) {
  if (!message) return null;

  const handleClose = () => {
    onClose();
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' || e.key === 'Escape') {
      handleClose();
    }
  };

  return (
    <div className="alert-modal-overlay" onClick={handleClose}>
      <div 
        className="alert-modal-content" 
        onClick={(e) => e.stopPropagation()}
        onKeyDown={handleKeyDown}
        tabIndex={0}
        autoFocus
      >
        <div className="alert-modal-header">
          <h3 className="alert-modal-title">
            {message.includes('') ? ' Success' :
             message.includes('') ? ' Error' :
             message.includes('') ? ' Warning' :
             message.includes('') ? ' Access Denied' :
             message.includes('') ? ' Notice' :
             'Notice'}
          </h3>
        </div>
        <div className="alert-modal-body">
          <p className="alert-modal-message">{message}</p>
        </div>
        <div className="alert-modal-footer">
          <button 
            className="alert-modal-button" 
            onClick={handleClose}
            autoFocus
          >
            OK
          </button>
        </div>
      </div>
    </div>
  );
}

export default AlertModal;
