import React, { useState } from 'react';
import '../styles/Login.css';
import { showLoadingCursor, hideLoadingCursor } from '../utils/helpers';
import { getApiBaseUrl, getApiHeaders } from '../config/networkConfig';
import { validateEmail } from '../utils/validation';

function Login({ onLoginSuccess, onSwitchToRegister }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [emailError, setEmailError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate email format
    const emailValidation = validateEmail(email);
    if (!emailValidation.valid) {
      setEmailError(emailValidation.message);
      return;
    }
    setEmailError('');

    showLoadingCursor();

    try {
      // Login does NOT require network validation
      // Network validation is only for punch in/out and worklog submission
      const headers = getApiHeaders();
      const baseUrl = getApiBaseUrl();
      console.log(' Login attempt:', { email, baseUrl, headers });
      
      const response = await fetch(`${baseUrl}/api/auth/login`, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify({ 
          email, 
          password
        }),
      });

      if (!response.ok) {
        const text = await response.text();
        let errorMessage;
        try {
          const data = JSON.parse(text);
          errorMessage = data.message;
        } catch {
          errorMessage = text || `HTTP ${response.status}`;
        }
        
        if (errorMessage === "Your account is awaiting admin approval.") {
          alert('Your account is awaiting admin approval. Please wait for admin approval before logging in.');
          hideLoadingCursor();
          return;
        }
        throw new Error(errorMessage);
      }

      const text = await response.text();
      const user = text ? JSON.parse(text) : null;
      
      // Successfully logged in
      onLoginSuccess(user);
    } catch (err) {
      alert(`Login failed: ${err.message}`);
    } finally {
      hideLoadingCursor();
    }
  };

  return (
    <div className="login-container">
      <div className="login-header">
        <h2 className="login-title">Login</h2>
        <p className="login-subtitle">Welcome back! Please login to your account</p>
      </div>
      <form onSubmit={handleSubmit} className="login-form" noValidate autoComplete="off">
        <div className="form-group">
          <label className="form-label">Email *</label>
          <input
            type="email"
            value={email}
            onChange={e => {
              setEmail(e.target.value);
              if (emailError) setEmailError('');
            }}
            placeholder="Enter your email"
            className={`form-input ${emailError ? 'input-error' : ''}`}
            autoComplete="off"
            tabIndex={1}
            required
            title="Enter your email address"
          />
          {emailError && <div className="error-message">{emailError}</div>}
        </div>
        <div className="form-group">
          <label className="form-label">Password *</label>
          <div className="password-input-wrapper">
            <input
              type={showPassword ? "text" : "password"}
              value={password}
              onChange={e => setPassword(e.target.value)}
              placeholder="Enter your password"
              className="form-input"
              autoComplete="off"
              tabIndex={2}
              required
              title="Enter your password"
            />
            <button
              type="button"
              className="password-toggle"
              onClick={() => setShowPassword(!showPassword)}
              tabIndex={-1}
              aria-label={showPassword ? "Hide password" : "Show password"}
              title={showPassword ? "Hide password" : "Show password"}
            >
              {showPassword ? (
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M3.98 8.223A10.477 10.477 0 0 0 1.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.451 10.451 0 0 1 12 4.5c4.756 0 8.773 3.162 10.065 7.498a10.522 10.522 0 0 1-4.293 5.774M6.228 6.228 3 3m3.228 3.228 3.65 3.65m7.894 7.894L21 21m-3.228-3.228-3.65-3.65m0 0a3 3 0 1 0-4.243-4.243m4.242 4.242L9.88 9.88" />
                </svg>
              ) : (
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="eye-icon">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M2.036 12.322a1.012 1.012 0 0 1 0-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178Z" />
                  <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0Z" />
                </svg>
              )}
            </button>
          </div>
        </div>
        <button type="submit" className="login-button" tabIndex={3} title="Login to your account">Login</button>
        <div className="register-link">
          Don't have an account?{' '}
          <button
            type="button"
            onClick={onSwitchToRegister}
            className="register-link-button"
            title="Go to registration page"
          >
            Register Here
          </button>
        </div>
      </form>
    </div>
  );
}

export default Login;
